import { Currency } from "../currencies";
import { HttpClient } from "../http";
import { TopUpOrders } from "./topUpOrders";
import { TopUpProviders } from "./topUpProviders";

/** Provides access to top up orders and available top up providers */
export class TopUps {
  /** Represents a user's cryptocurrency purchases (top ups) */
  public readonly orders: TopUpOrders;

  /** Represents top up providers available for use on the platform */
  public readonly providers: TopUpProviders;

  /** @hidden */
  constructor(
    /** @hidden */ _http: HttpClient,
    /** @hidden */ _getCurrency: (id: string) => Promise<Currency>,
    /** @hidden */ _isUserSignedIn: () => Promise<boolean>
  ) {
    this.orders = new TopUpOrders(_http, _getCurrency);
    this.providers = new TopUpProviders(_http, _isUserSignedIn);
  }
}
