import * as Oidc from "oidc-client";
import { hmac } from "./crypto";
import { Currency } from "./currencies";
import {
  ChangeUserPreferencesRequestDto,
  RecoveryCodesDto,
  Request2faTokenDto,
  RequestEmailChangeResponseDto,
  ResendEmailChangeResponseDto,
  TwoFactorMethodsDto,
  UserFeaturesDto,
  UserPreferencesDto,
  UserProfileDto,
  VerifyEmailChangeResponseDto
} from "./dtos";
import { HttpClient } from "./http";

/**
 * @hidden
 */
export interface UserManagerUser {
  access_token: string;
  expires_in: number | undefined;
  expired: boolean | undefined;
  profile: {
    sub: string;
    given_name?: string | undefined;
    family_name?: string;
    email?: string;
  };
  expires_at: number;
}

/**
 * @hidden
 */
export interface UserManager {
  getUser(): Promise<UserManagerUser | undefined>;
  storeUser(user: Oidc.User): Promise<void>;
  signinRedirect(args?: any): Promise<void>;
  signoutRedirect(): Promise<void>;
  events: Oidc.UserManagerEvents;
}

/**
 * Profile information for a user account
 */
export interface UserProfile {
  /**
   * the unique id of the user
   */
  userId: string;
  /**
   * the e-mail address of the user
   */
  email: string;

  /**
   * the given name(s) or first name(s) of the user
   */
  firstName: string;

  /**
   * the surname(s) or last name(s) of the user
   */
  lastName: string;

  /**
   * the country of residence of the user
   */
  country: string;
}

/**
 * User Session info that indicates if user is currently signed in and when their session will expire
 */
export interface UserSession {
  /** whether the user is currently signed in */
  isSignedIn: boolean;
  /** the number of seconds until the user session expires */
  expiresIn?: number;
  /** the timestamp in seconds, of when the user session will expire */
  expiresAt?: number;
}

/**
 * Users system preferences, returned by [[User.getPreferences]]
 */
export interface UserPreferences {
  /** the users native currency */
  nativeCurrency: Currency;
  /** value indicating whether to send the user a notification when funds are received into one of their accounts */
  notifyOnFundsReceived: boolean;
  /** value indicating whether to send the user a notification when funds are sent from one of their accounts */
  notifyOnFundsSent: boolean;
}

/**
 * Updated user preferences, to be saved, accepted by [[User.updatePreferences]]
 */
export interface UpdatedUserPreferences {
  /** optional id of the users native currency, api calls will return monetary amounts in this currency along with the original currencies.  If not specified then the current preference will not be updated. */
  nativeCurrencyId?: number;
  /** optional value indicating whether to send the user a notification when funds are received into one of, their accounts.  If not specified then the current preference will not be updated. */
  notifyOnFundsReceived?: boolean;
  /** optional value indicating whether to send the user a notification when funds are sent from one of their accounts.  If not specified then the current preference will not be updated. */
  notifyOnFundsSent?: boolean;
}

/** @hidden */
export interface UserInitOptions {
  getCurrencyById: (currencyId: string) => Promise<Currency>;
}

/**
 * Represents the response to a 2fa code request
 */
interface Request2faToken {
  /**
   * The generated request ID
   */
  requestId: string;
  /**
   * The authenticator key for adding google authenticator only
   */
  authenticatorKey?: string;
  /**
   * The qr code uri for adding google authenticator
   */
  qrCodeUri?: string;
}

interface TwoFactorMethods {
  /**
   * The 2FA methods that the user has not yet activated and that are available to them for activation
   */
  available: TwoFactorMethodType[];
  /**
   * The 2FA methods that the user has activated
   */
  activated: TwoFactorMethodType[];

  /**
   * The 2FA method that the user currently has set for login
   */
  current: TwoFactorMethodType;
}

interface RecoveryCodes {
  /**
   * The recovery codes generated when setting up google authenticator device.
   */
  recoveryCodes?: string[];
}

/**
 * Possible two factor methods a user could have
 */
export enum TwoFactorMethodType {
  googleAuthenticator = "google authenticator",
  email = "email",
  none = "none"
}

/**
 * Provides methods to sign-in and out, as well as methods to manage the currently authenticated user account
 */
export class User {
  /**
   * @hidden
   */
  constructor(
    /** @hidden */
    private readonly identityClient: HttpClient,
    /** @hidden */
    private readonly orionClient: HttpClient,
    /** @hidden */
    private readonly userManager: UserManager | Oidc.UserManager,
    /** @hidden */
    private readonly identityBaseUrl: string,
    /** @hidden */
    private readonly options: UserInitOptions
  ) {}

  /**
   * Returns a promise which fulfills with a UserSession object indicating whether a user is currently signed in
   * and when the session will expire.
   * @returns `Promise<UserSession>` which fulfills with an object indicating whether a user is signed in and when session will expire
   */
  public async getUserSessionInfo(): Promise<UserSession> {
    const user = await this.userManager.getUser();

    if (!user || !user.access_token || user.expired) {
      return {
        isSignedIn: false
      };
    }

    return {
      isSignedIn: true,
      expiresAt: user.expires_at,
      expiresIn: user.expires_in
    };
  }

  /**
   * performs the signin redirect for authentication/authorization, the browser will be redirected to the CoinPayments
   * Identity server where the user will authenticate and authorize the client and then the browser will be redirected
   * back to the URL specified by the [[CoinPaymentsClientConfig.identitySignInRedirectUri]].
   *
   * @returns `Promise` which fulfills when redirect is completed
   */
  public async signinRedirect(): Promise<void> {
    const referral = this.getReferrerCodeOrDefault();
    await this.userManager.signinRedirect({ extraQueryParams: { ref: referral } });
  }

  /**
   * performs the [[signinRedirect]] for authentication/authorization but redirecting the user to the signup page.
   *
   * @returns `Promise` which fulfills when redirect is completed
   */
  public async signupRedirect(): Promise<void> {
    const referral = this.getReferrerCodeOrDefault();
    await this.userManager.signinRedirect({ extraQueryParams: { signup: "1", ref: referral } });
  }

  /**
   * performs the signout redirect to sign out of the current user account
   */
  public async signout(): Promise<void> {
    await this.userManager.signoutRedirect();
  }

  /**
   * performs the signout redirect to sign out of the current user account, and redirect them back to the login page
   */
  public async signoutRedirectToLogin(): Promise<void> {
    await this.userManager.signoutRedirect({
      post_logout_redirect_uri: `${this.identityBaseUrl}/Identity/Account/Login`
    });
  }

  /**
   * Returns the [[UserProfile]] of the current user asynchronously, or `null` if a user isn't signed in.
   * @returns `Promise<UserProfile>` which fulfills with the user profile information of the current user or null if the user isn't signed in
   */
  public async getProfile(): Promise<UserProfile | null> {
    const user = await this.userManager.getUser();
    if (!user) {
      return null;
    }

    // some info is in claims, but could've changed with user updating their info
    const profile = await this.identityClient.get<UserProfileDto>("/api/v1/user/profile");

    return {
      email: user.profile.email ? user.profile.email : "",
      firstName: profile.data.firstName,
      lastName: profile.data.lastName,
      userId: user.profile.sub,
      country: profile.data.country
    };
  }

  /**
   * Returns whether the user has passed KYC
   * @returns `Promise<boolean>` which fulfills with either true if they passed KYC, false otherwise
   */
  public async hasPassedKyc(): Promise<boolean> {
    const user = (await this.userManager.getUser()) as Oidc.User;
    if (!user) {
      return false;
    }

    if (user.profile.kyc === "true") {
      return true;
    }
    return false;
  }

  /**
   * Redirects to the KYC verification page in Identity
   * @param returnUrl Optional url to return users to from the KYC verification page
   */
  public async redirectToKycVerification(returnUrl?: string) {
    let url = `${this.identityBaseUrl}/identity/kyc/verification`;
    if (returnUrl) {
      url = `${url}?returnUrl=${returnUrl}`;
    }

    window.location.href = url;
  }

  /**
   * Returns the [[UserPreferences]] of the current user asynchronously
   * @returns `Promise<UserPreferences>` which fulfills with the current users system preferences or null if the user isn't signed in
   */
  public async getPreferences(): Promise<UserPreferences | null> {
    const user = await this.userManager.getUser();
    if (!user) {
      return null;
    }

    const response = await this.orionClient.get<UserPreferencesDto>("/api/v1/preferences");
    const prefs = response.data;
    const nativeCurrency = await this.options.getCurrencyById(`${prefs.nativeCurrencyId}`);

    return {
      nativeCurrency,
      notifyOnFundsReceived: prefs.notifyOnFundsReceived,
      notifyOnFundsSent: prefs.notifyOnFundsSent
    };
  }

  /**
   * Updates the users personal information.  The users `email` address can't be changed.
   * @param profile the updated personal information
   * @returns `Promise<void>` that fulfills when the users profile has been updated
   */
  public async updateProfile(profile: UserProfile): Promise<void> {
    await this.identityClient.put("/api/v1/user/profile", {
      firstName: profile.firstName,
      lastName: profile.lastName
    });
  }

  /**
   * Updates the users system preferences with the new values provided
   * @param updatedPreferences the updated preferences to save
   * @returns `Promise<void>` that fulfills when the new preferences have been sent to the server and server accepted them, they may not yet have been updated
   */
  public async updatePreferences(updatedPreferences: UpdatedUserPreferences): Promise<void> {
    const isNullOrUndefined = (val: any) => val === null || typeof val === "undefined";

    if (
      isNullOrUndefined(updatedPreferences.nativeCurrencyId) &&
      isNullOrUndefined(updatedPreferences.notifyOnFundsReceived) &&
      isNullOrUndefined(updatedPreferences.notifyOnFundsSent)
    ) {
      // nothing to update
      return;
    }

    const dataToUpdate: ChangeUserPreferencesRequestDto = updatedPreferences;

    await this.orionClient.put("/api/v1/preferences", dataToUpdate);
  }

  /**
   * Verifies that the users current password matches `oldPassword` and if correct updates the users password to `newPassword`
   * @param oldPassword the users current password
   * @param newPassword the new password to set for the user
   * @returns `Promise<boolean>` which fulfills with a boolean value indicating whether the operation succeeded, false in case of password mismatch
   */
  public async changePassword(oldPassword: string, newPassword: string): Promise<boolean> {
    const profile = await this.getProfile();
    if (!profile) {
      throw new Error("No user signed in");
    }

    // make email case insensitive
    const upperCaseEmail = profile.email.toUpperCase();

    const oldPasswordHmac = await hmac(upperCaseEmail, oldPassword);
    const newPasswordHmac = await hmac(upperCaseEmail, newPassword);

    const response = await this.identityClient.post(
      "/api/v1/user/changepassword",
      {
        oldPassword: oldPasswordHmac,
        newPassword: newPasswordHmac
      },
      { validateStatus: _ => true /* server response status validated below */ }
    );

    return response.status >= 200 && response.status < 300;
  }

  /**
   * Verifies that the specified password matches the current password of the user
   * @param password the password to verify
   * @returns `Promise<boolean>` which fulfills with a boolean value indicating whether the specified password was correct
   */
  public async verifyPassword(password: string): Promise<boolean> {
    const profile = await this.getProfile();
    if (!profile) {
      return false; // todo: throw here?
    }

    // make email case insensitive by converting it to uppercase
    const upperCaseEmail = profile.email.toUpperCase();

    const passwordHmac = await hmac(upperCaseEmail, password);
    const response = await this.identityClient.post<{ isValid: boolean }>("/api/v1/user/verifypassword", {
      password: passwordHmac
    });

    return response.data.isValid;
  }

  /**
   * Gets info about a user's available and activated, methods, and also shows which one is set for login
   * @returns `Promise<TwoFactorMethods>` which is info about a user's 2fa methods
   */
  public async get2faInfo(): Promise<TwoFactorMethods> {
    const response = await this.identityClient.get<TwoFactorMethodsDto>("/api/v1/user/2fa");

    const data = response.data;
    const availableArray: TwoFactorMethodType[] = [];
    data.available.map(method => {
      availableArray.push(this.convertToMethodType(method));
    });

    const activatedArray: TwoFactorMethodType[] = [];
    data.activated.map(method => {
      activatedArray.push(this.convertToMethodType(method));
    });

    const current: TwoFactorMethodType = this.convertToMethodType(data.current);

    return {
      available: availableArray,
      activated: activatedArray,
      current
    };
  }

  /**
   * Initiate a 2FA challenge to verify a generated 2FA code or to activate (add) a 2FA method for user.
   * Use [[verify2fa]] to verify a 2FA challenge.
   * @param method The 2FA method to challenge
   * @param reason Used for email 2FA only. Contains a short message such as what would be used as the email subject.
   * @param details Used for email 2FA only. Can contain information that will be in the body of the email.
   * @returns `Promise<Request2faToken>` which provides the requestId needed for verifying a 2fa challenge, and also the authenticator key if adding Google Authenticator
   */
  public async challenge2fa(
    method: TwoFactorMethodType,
    reason: string | null = null,
    details: string | null = null
  ): Promise<Request2faToken> {
    const response = await this.identityClient.post<Request2faTokenDto>("/api/v1/user/2fa/challenge", {
      method,
      reason,
      details
    });

    const data = response.data;
    if (data.authenticatorKey && data.qrCodeUri) {
      return {
        requestId: data.requestId,
        authenticatorKey: data.authenticatorKey,
        qrCodeUri: data.qrCodeUri
      };
    }

    return {
      requestId: data.requestId
    };
  }

  /**
   * Verifies a 2FA challenge. Must call the 2fa/challenge api method to get the RequestId, before being able to verify the challenge.
   * @param method The 2FA method that was requested
   * @param requestId The requestId generated by the challenge
   * @param otp The 2FA code to verify
   * @returns `Promise<RecoveryCodes | void>` that fulfills when the 2fa challenge has been verified, or if adding google authenticator, will return the recovery codes generated.
   */
  public async verify2fa(
    method: TwoFactorMethodType,
    requestId: string,
    otp: string
  ): Promise<RecoveryCodes | void> {
    const response = await this.identityClient.post<RecoveryCodesDto | void>("/api/v1/user/2fa/verify", {
      method,
      requestId,
      otp
    });

    if (response.data) {
      return {
        recoveryCodes: response.data.recoveryCodes
      };
    }
  }

  /**
   * Initiate a challenge to set 2FA method to use for login.
   * Use [[set2fa]] to verify the challenge.
   * @param method The method to set for login
   * @returns `Promise<Request2faToken>` which provides the requestId needed for verifying a 2fa challenge.
   */
  public async setCurrent2faChallenge(method: TwoFactorMethodType): Promise<Request2faToken> {
    const response = await this.identityClient.put<Request2faTokenDto>("/api/v1/user/2fa/current", {
      method
    });

    return {
      requestId: response.data.requestId
    };
  }

  /**
   * Verifies a set 2FA challenge, and if successful sets 2FA method to use for login
   * @param method The method to set for login
   * @param requestId The requestId generated by the challenge
   * @param otp The 2FA code to verify
   * @returns `Promise<void>` that fulfills when the 2fa method has been set
   */
  public async setCurrent2fa(method: TwoFactorMethodType, requestId: string, otp: string): Promise<void> {
    await this.identityClient.put("/api/v1/user/2fa/current", {
      method,
      requestId,
      otp
    });
  }

  /**
   * Create an email change request
   * @param newEmail The email to request to change to
   */
  public async requestEmailChange(
    newEmail: string,
    password: string
  ): Promise<RequestEmailChangeResponseDto> {
    const profile = await this.getProfile();
    if (!profile) {
      throw new Error("No user signed in");
    }

    const isValid = await this.verifyPassword(password);
    if (!isValid) {
      throw new Error("Invalid password entered");
    }

    // make email case insensitive
    const upperCaseNewEmail = newEmail.toUpperCase();
    const updatedPassword = await hmac(upperCaseNewEmail, password);
    const response = await this.identityClient.post<RequestEmailChangeResponseDto>(
      "/api/v1/user/email/change/request",
      {
        email: newEmail,
        passwordHash: updatedPassword
      }
    );

    return response.data;
  }

  /**
   * Verify an email change request
   * @param currentEmailCode The code sent to the user's current email address
   * @param newEmailCode The code sent to the email user would like to change to
   * @returns `Promise<VerifyChangeEmailResponseDto>` that fulfills with a result status when the change email request is verified
   */
  public async verifyEmailChange(
    requestId: string,
    currentEmailCode: string,
    newEmailCode: string
  ): Promise<VerifyEmailChangeResponseDto> {
    const response = await this.identityClient.post<VerifyEmailChangeResponseDto>(
      "/api/v1/user/email/change/verify",
      {
        requestId,
        currentEmailCode,
        newEmailCode
      }
    );

    return response.data;
  }

  /**
   * Resend an email change code to the email specified
   * Note: this method cannot be used if the [[User.requestEmailChange]] method was not called first.
   * @param emailToResendCode The email address to which to resend the code
   */
  public async resendEmailChangeCode(
    requestId: string,
    emailToResendCode: string
  ): Promise<ResendEmailChangeResponseDto> {
    const response = await this.identityClient.post<ResendEmailChangeResponseDto>(
      "/api/v1/user/email/change/resendcode",
      {
        requestId,
        email: emailToResendCode
      }
    );

    return response.data;
  }

  /**
   * Indicates which features, that are only available to certain users, are available for this particular user
   */
  public async getAvailableFeatures(): Promise<UserFeaturesDto> {
    const response = await this.orionClient.get<UserFeaturesDto>("/api/v1/user/features");
    return response.data;
  }

  /**
   * Converts the the method string into a value in the TwoFactorMethodType enum
   * @param method The method string to convert to TwoFactorMethodType
   * @hidden
   */
  private convertToMethodType(method: string): TwoFactorMethodType {
    switch (method) {
      case "email":
        return TwoFactorMethodType.email;
      case "google authenticator":
        return TwoFactorMethodType.googleAuthenticator;
      default:
        return TwoFactorMethodType.none;
    }
  }

  /**
   * gets the referral code of the channel that the user was referred through
   */
  private getReferrerCodeOrDefault(): string | undefined {
    const win = (window as any) as { CoinPayments?: { refId?: string } };
    return win && win.CoinPayments && win.CoinPayments.refId;
  }
}
