import { EntityFactory, UrlBuilder } from "../common";
import {
  AcceptedCurrencyDto,
  MerchantPaymentCurrencyPayoutFrequency,
  PagedItemsDto,
  PayoutConfigDto
} from "../dtos";
import { HttpClient } from "../http";
import { PagedItems } from "../paging";
import { AcceptedCurrency } from "./acceptedCurrency";

/**
 * Options accepted by the [[list]] method
 */
export interface ListAcceptedCurrenciesOptions {
  /**
   * the maximum number of objects that may be returned, the query may return fewer results than
   * the requested maximum.
   */
  limit?: number;
}

/**
 * Options accepted by the [[add]] method and [[AcceptedCurrency.update]] method
 */
export interface AcceptedCurrencyConfigOptions {
  /** the frequency that payouts will occur for an accepted currency */
  frequency?: MerchantPaymentCurrencyPayoutFrequency;
  /** the discount to give buyers when paying in this currency, maybe as a promotional tool */
  discountPercent?: number;
  /** the markup to charge buyers when paying in this currency, maybe to cover local crypto/fiat conversion costs */
  markupPercent?: number;
  /** the payout configurations for an accepted currency */
  payouts: PayoutConfigDto[];
}

/** Represents the currencies that a merchant accepts for payments */
export class AcceptedCurrencies {
  /** @hidden */
  constructor(
    /** @hidden */
    private readonly _http: HttpClient
  ) {
    this._factory = new EntityFactory(
      dto => dto.currency.id.toString(),
      async entityDto => {
        return new AcceptedCurrency(entityDto, _http);
      }
    );
  }

  /**
   * Gets the merchant's currently accepted currencies for payment.
   * Currencies that are ranked (ordered) will be returned at the top of the list.
   */
  public async list(
    options?: ListAcceptedCurrenciesOptions
  ): Promise<PagedItems<AcceptedCurrencyDto, AcceptedCurrency>> {
    const baseUrl = new UrlBuilder("/api/v1/merchant/currencies");

    if (options) {
      baseUrl.addQueryIfValue("limit", options.limit ? options.limit.toString() : "100");
    }

    const url = baseUrl.toString();
    const response = await this._http.get<PagedItemsDto<AcceptedCurrencyDto>>(url);
    const data = response.data;
    return PagedItems.createAsync(data, dto => this._factory.getOrCreateEntity(dto), this._http);
  }

  /**
   * Adds a currency that a merchant wishes to receive as payments from Buyers
   * @param currencyId the ID of the payment currency
   * @param currencyConfig the frequency and payout configurations for the payment currency
   */
  public async add(currencyId: string, currencyConfig: AcceptedCurrencyConfigOptions): Promise<void> {
    if (!currencyConfig.payouts || currencyConfig.payouts.length === 0) {
      throw new Error("Missing payout configurations");
    }

    await this._http.post(`/api/v1/merchant/currencies/${currencyId}`, currencyConfig);
  }

  /**
   * Deletes a currency that a merchant no longer wishes to accept
   * @param currencyId the ID of the payment currency to delete
   */
  public async delete(currencyId: string): Promise<void> {
    await this._http.delete(`/api/v1/merchant/currencies/${currencyId}`);
  }

  /**
   * @hidden
   */
  private readonly _factory: EntityFactory<AcceptedCurrencyDto, AcceptedCurrency>;
}
