import { EntityFactory, UrlBuilder } from "../common";
import { MerchantClientDto, MerchantClientPublicState, PagedItemsDto } from "../dtos";
import { HttpClient } from "../http";
import { PagedItems } from "../paging";
import { MerchantClient, NewMerchantClient } from "./merchantClient";

/**
 * Options accepted by the [[list]] method
 */
export interface ListMerchantClientsOptions {
  /** The string to search for in the client name */
  search?: string;
  /** The state of the client (active or disabled) */
  state?: MerchantClientPublicState;
  /**
   * the maximum number of objects that may be returned, the query may return fewer results than
   * the requested maximum.
   */
  limit?: number;
}

/**
 * Options accepted by the [[create]] method
 */
export interface CreateMerchantClientOptions {
  /** the name of the integration for which this client is being created (e.g. 'Magento', or 'Shopify') */
  integration?: string;
  /** The name of the client that the merchant should provide */
  name: string;
  /** The store url of the client */
  storeUrl: string;
}

/** Represents a merchant's clients (to communicate with the API) and their webhooks' creation, updates, and deletion. */
export class MerchantClients {
  /** @hidden */
  constructor(
    /** @hidden */
    private readonly _http: HttpClient
  ) {
    this._factory = new EntityFactory(
      dto => dto.clientId,
      async entityDto => {
        if (entityDto.dto.clientSecret) {
          return new NewMerchantClient(entityDto, _http);
        }
        return new MerchantClient(entityDto, _http);
      }
    );
  }

  /**
   * Get all clients belonging to the merchant
   * @param options Optional fields to filter the list by
   */
  public async list(
    options?: ListMerchantClientsOptions
  ): Promise<PagedItems<MerchantClientDto, MerchantClient>> {
    const baseUrl = new UrlBuilder("/api/v1/merchant/clients");

    if (options) {
      baseUrl
        .addQueryIfValue("search", options.search)
        .addQueryIfValue("state", options.state)
        .addQueryIfValue("limit", options.limit ? options.limit.toString() : "100");
    }

    const url = baseUrl.toString();
    const response = await this._http.get<PagedItemsDto<MerchantClientDto>>(url);
    const data = response.data;
    return PagedItems.createAsync(data, dto => this._factory.getOrCreateEntity(dto), this._http);
  }

  /**
   * Find a client by its clientId
   * @param clientId The public id of the client
   */
  public async find(clientId: string): Promise<MerchantClient> {
    const url = `/api/v1/merchant/clients/${clientId}`;
    const response = await this._http.get<MerchantClientDto>(url);
    return this._factory.getOrCreateEntity(response.data);
  }

  /**
   * Create a new client belonging to the merchant
   * @param options Info used to create the client
   */
  public async create(options: CreateMerchantClientOptions): Promise<NewMerchantClient> {
    const response = await this._http.post<MerchantClientDto>(`/api/v1/merchant/clients`, options);
    const entity = await this._factory.getOrCreateEntity(response.data);
    return entity as NewMerchantClient;
  }

  /**
   * Delete an existing client
   * @param clientId The publicly exposed ID of the client app
   */
  public async delete(clientId: string) {
    await this._http.delete(`/api/v1/merchant/clients/${clientId}`);
  }

  /**
   * @hidden
   */
  private readonly _factory: EntityFactory<MerchantClientDto, MerchantClient>;
}
