import bn from "bn.js";
import { DtoRef } from "../common";
import {
  convertDisplayAmountToSmallestUnits,
  convertSmallestUnitsToDisplayAmount
} from "../currencies/utils";
import { SmartContractDto } from "../dtos";

/**
 * Represents tokens for crypto currencies
 */
export class SmartContract {
  /** the common currency id on the CoinPayments platform */
  get currencyId() {
    return this._model.dto.currencyId;
  }

  /** the address of the smart contract */
  get contractAddress() {
    return this._model.dto.contractAddress;
  }

  /** the name of the smart contract */
  get contractName() {
    return this._model.dto.contractName;
  }

  /** the ticker symbol for the smart contract */
  get contractSymbol() {
    return this._model.dto.contractSymbol;
  }

  /** is smart contract verified by CoinPayments */
  get isVerified() {
    return this._model.dto.isVerified;
  }

  /** the number of digits after the decimal separator */
  get decimals() {
    return this._model.dto.decimals;
  }

  /**
   * Converts an amount to the currencies smallest unit
   * @hidden
   * @param amount the amount to convert
   */
  public convertAmountToSmallestUnit(amount: string): bn {
    return convertDisplayAmountToSmallestUnits(amount, this.decimals);
  }

  /**
   * Converts an amount in the currencies smallest unit to a display amount
   * @hidden
   * @param amount the amount in the currencies smallest unit to convert to a display amount
   */
  public convertSmallestUnitToDisplay(amount: string): string {
    return convertSmallestUnitsToDisplayAmount(amount, this.decimals);
  }

  /** @hidden */
  constructor(/** @hidden */ private readonly _model: DtoRef<SmartContractDto>) {}
}
