import Oidc from "oidc-client";
import { Accounts } from "./accounts";
import { Affiliate } from "./affiliates";
import { validateArgs } from "./common";
import { Currencies } from "./currencies";
import { FetchHttpClient } from "./http";
import { Merchant } from "./merchant";
import { PosInvoices } from "./pos/posInvoices";
import { AccessTokenService } from "./services/accessTokenService";
import { Settings } from "./settings/settings";
import { SignalRHub } from "./signalrhub";
import { SmartContracts } from "./smartcontracts";
import { TopUps } from "./topups/topUps";
import { Transactions } from "./transactions";
import { User, UserManager } from "./user";
import { UserNotifications } from "./userNotifications/userNotifications";

/**
 * specifies the configuration options for the [[CoinPaymentsClient]]
 */
export interface CoinPaymentsClientConfig {
  /** the identitifer of your client application, as registered with the identity server */
  identityClientId: string;

  /**
   * the identity server (OIDC/OAuth2 provider) base url
   * @example https://coinpayments-identity.azurewebsites.net
   */
  identityBaseUrl: string;

  /**
   * the redirect URI of your client application to receive a response from the identity server.  It is expected that
   * the client makes a call to [[CoinPaymentsClient.userSigninRedirectCallback]] here to complete the authentication
   * process.
   *
   * @example http://coinpayments-aphrodite.azurewebsites.net/callback.html
   */
  identitySignInRedirectUri: string;

  /**
   * the base url of the server api
   * @example https://coinpayments-orion.azurewebsites.net
   */
  apiBaseUrl: string;

  /** @hidden */
  post_logout_redirect_uri?: string;
  /** @hidden */
  userManager?: UserManager;
}

/**
 * Provides functionality to communicate with the CoinPayments server APIs
 */
export class CoinPaymentsClient {
  /**
   * callback to complete the authentication process, a call to this method is expected from the url configured in
   * [[CoinPaymentsClientConfig.identitySignInRedirectUri]].  When the promise is fulfilled the browser can be
   * redirected back to the main page.
   * @returns `Promise<boolean>` fulfills with a `boolean` to indicate whether the authentication process completed successfully
   */
  public static async userSigninRedirectCallback(): Promise<boolean> {
    const user = await new Oidc.UserManager({}).signinRedirectCallback();
    return !!user;
  }

  /**
   * provides access to the user's financial accounts hosted on the CoinPayments platform
   */
  public readonly accounts: Accounts;

  /**
   * provides access to the currencies hosted on the CoinPayments platform
   */
  public readonly currencies: Currencies;

  /**
   * provides access to the currently authenticated user as well as the ability to signin and logout.
   */
  public readonly user: User;

  /**
   * provides access to the user's transactions
   */
  public readonly transactions: Transactions;

  /**
   * provides access to user's merchant features
   */
  public readonly merchant: Merchant;

  /**
   * provides access to user's settings
   */
  public readonly settings: Settings;

  /**
   * provides access to user dashboard notifications
   */
  public readonly userNotifications: UserNotifications;

  /**
   * provides access to user smart contracts
   */
  public readonly smartContracts: SmartContracts;

  /**
   * provides access to the user's affiliate features
   */
  public readonly affiliate: Affiliate;

  /**
   * provides access to the top up features
   */
  public readonly topUps: TopUps;

  /**
   * provides access to pos invoices
   */
  public readonly pos: PosInvoices;

  /**
   * Creates a new [[CoinPaymentsClient]] with the provided [[CoinPaymentsClientConfig]]
   * @param config configuration with which to create the client
   */
  constructor(config: CoinPaymentsClientConfig) {
    validateArgs(config, ["identityClientId", "identityBaseUrl", "identitySignInRedirectUri", "apiBaseUrl"]);

    const userManagerConfig: Oidc.UserManagerSettings = {
      authority: config.identityBaseUrl,
      client_id: config.identityClientId,
      redirect_uri: config.identitySignInRedirectUri,
      response_type: "id_token token",
      scope: "openid profile orion",
      post_logout_redirect_uri: config.post_logout_redirect_uri || ""
    };

    const userManager = config.userManager || new Oidc.UserManager(userManagerConfig);
    // this event will watch if user has signed out in other tabs/windows
    userManager.events.addUserSignedOut(() => this.user.signout());

    const identityClient = new FetchHttpClient(config.identityBaseUrl);
    const orionClient = new FetchHttpClient(config.apiBaseUrl);

    const accessTokenService = new AccessTokenService(identityClient, userManager, config.identityClientId);
    const eventsHub = new SignalRHub(
      config.apiBaseUrl + "/api/v1/hubs/events",
      accessTokenService.getAccessTokenOrThrow
    );

    identityClient.addDefaultHeader("Authorization", accessTokenService.makeBearerHeader);
    orionClient.addDefaultHeader("Authorization", accessTokenService.makeBearerHeader);

    this.currencies = new Currencies(orionClient);
    this.user = new User(identityClient, orionClient, userManager, config.identityBaseUrl, {
      getCurrencyById: async currencyId => {
        const currency = await this.currencies.find(`${currencyId}`);
        if (!currency) throw new Error(`Currency not found`);
        return currency;
      }
    });

    const getSmartContract = async (currencyId: string, contract: string) => {
      return this.smartContracts.find(currencyId, contract);
    };

    const getCurrency = async (id: string) => {
      const currency = await this.currencies.find(`${id}`);
      if (!currency) {
        throw new Error(`Currency not found or inactive`);
      }
      return currency;
    };

    const getAccount = async (id: string) => {
      const account = await this.accounts.find(id);
      if (!account) {
        throw new Error(`Account not found or inactive`);
      }
      return account;
    };

    const isUserSignedIn = async (): Promise<boolean> => {
      const info = await this.user.getUserSessionInfo();
      return info.isSignedIn;
    };

    this.accounts = new Accounts(orionClient, eventsHub, getCurrency, getSmartContract);
    this.transactions = new Transactions(orionClient, eventsHub, getCurrency, getSmartContract, getAccount);
    this.merchant = new Merchant(orionClient, eventsHub, getCurrency, getAccount);
    this.settings = new Settings(orionClient);
    this.userNotifications = new UserNotifications(orionClient);
    this.smartContracts = new SmartContracts(orionClient);
    this.affiliate = new Affiliate(orionClient, getCurrency);
    this.topUps = new TopUps(orionClient, getCurrency, isUserSignedIn);
    this.pos = new PosInvoices(orionClient);

    try {
      this.user.getUserSessionInfo().then(info => {
        if (info.isSignedIn) {
          // don't record for users already signed in?
          return;
        }
        if (!CoinPaymentsClient._impressionRecorded) {
          this._recordAffiliateImpression(orionClient);
          CoinPaymentsClient._impressionRecorded = true;
        }
      });
    } catch (e) {
      // todo: log error?
    }
  }

  private _recordAffiliateImpression(orionHttpClient: FetchHttpClient) {
    const cookieName = "cps-refid";
    const win = (window as any) as { CoinPayments?: { refId?: string } };

    const tryGetReferrerIdFromHash = () => {
      const hash = window.location.hash;
      if (!hash || !hash.startsWith("#ref=")) return undefined;
      return hash.substr("#ref=".length);
    };

    const tryGetReferrerIdFromWindow = () => {
      return win && win.CoinPayments && win.CoinPayments.refId;
    };

    const getPreviousReferrerIdFromCookie = () => {
      const nameEq = cookieName + "=";
      const nameLen = nameEq.length;
      return (
        document.cookie
          .split(";")
          .map(i => i.trim())
          .filter(c => c.substring(0, nameLen) === nameEq)
          .map(c => decodeURIComponent(c.substring(nameLen)))[0] || ""
      );
    };

    const saveReferrerIdCookieFor24h = (value: string) => {
      const now = new Date();
      const expires = new Date(now.getTime() + 1 * 24 * 60 * 60 * 1000);
      document.cookie = `${cookieName}=${value};"expires="${expires.toUTCString()};path=/`;
    };

    const referrerId = tryGetReferrerIdFromHash() || tryGetReferrerIdFromWindow();
    const previousRefId = getPreviousReferrerIdFromCookie();
    const isUnique = referrerId && (!previousRefId || previousRefId !== referrerId);

    // save the referrer id in our namespace (SignIn and SignUp look it up from here, in case the script moves to site)
    (win.CoinPayments || (win.CoinPayments = {})).refId = referrerId || previousRefId;

    if (referrerId && isUnique) {
      // don't extend the previous referrer cookie
      saveReferrerIdCookieFor24h(referrerId);
    }

    const extractHostnameFromUrl = (url: string) => {
      try {
        return new URL(url).hostname; // not supported in IE11 ...
      } catch (e) {
        url = url.split("?")[0]; // drop query string, if present
        const schemeIndex = url.indexOf("://");
        if (schemeIndex >= 4) {
          url = url.substr(schemeIndex + 3); // drop 'http://' or 'https://'
        }
        const lastIndex = url.lastIndexOf("/"); // drop the path, if present
        if (lastIndex >= 10) {
          url = url.substr(0, lastIndex);
        }
        return url;
      }
    };

    if (referrerId) {
      // for the referrer drop the query string and path if present, we only want the hostname
      const referrerHost = (document.referrer && extractHostnameFromUrl(document.referrer)) || undefined;

      // record the impression
      orionHttpClient.post(
        "/api/v1/affiliate/i",
        { a: referrerId, u: isUnique ? 1 : undefined, r: referrerHost },
        { omitAuthorization: true, responseType: "text" }
      );
    }
  }

  private static _impressionRecorded = false;
}
