import { Currency } from "./currencies";
import { FullAmountDto } from "./dtos";
import { Money } from "./money";
import { SmartContract } from "./smartcontracts";

/**
 * Encapsulates the total balances across all [[Account]]'s, grouped by their [[Currency]]
 */
export class FullAmount {
  /** @hidden */
  constructor(
    /**
     * the amount in the original currency
     */
    public readonly amount: Money,
    /**
     * the amount in the users native currency
     */
    public readonly nativeAmount: Money
  ) {}

  /** @hidden */
  public static async create(
    model: FullAmountDto,
    currencyOrGetter: Currency | ((id: string) => Promise<Currency>),
    nativeCurrencyOrGetter: Currency | ((id: string) => Promise<Currency>),
    token?: SmartContract
  ): Promise<FullAmount> {
    let currency: Currency;
    let nativeCurrency: Currency;

    if (typeof currencyOrGetter === "function") {
      const currencyId = token
        ? `${model.amount.currencyId}:${token.contractAddress}`
        : `${model.amount.currencyId}`;
      currency = await currencyOrGetter(currencyId);
    } else {
      currency = currencyOrGetter;
    }

    nativeCurrency =
      typeof nativeCurrencyOrGetter === "function"
        ? await nativeCurrencyOrGetter(`${model.nativeAmount.currencyId}`)
        : nativeCurrencyOrGetter;

    const amount = Money.create(model.amount, currency);
    const nativeAmount = Money.create(model.nativeAmount, nativeCurrency);

    return new FullAmount(amount, nativeAmount);
  }
}
