import { EntityFactory } from "../common";
import { CreatePosResponseDto, InvoiceMoneyDto, PosInvoiceDto } from "../dtos";
import { HttpClient } from "../http";
import { PosInvoice } from "./posInvoice";

/**
 * Represents fields needed to create a POS invoice
 */
export interface CreatePosRequest {
  /** the currency ID of the payment currency for the POS */
  paymentCurrencyId: string;
  /** The total amount of the POS in the invoice currency */
  amount: InvoiceMoneyDto;
}

export class PosInvoices {
  /** @hidden */
  constructor(
    /** @hidden */
    private readonly _http: HttpClient
  ) {
    this._factory = new EntityFactory(
      dto => dto.id,
      async entityDto => {
        return new PosInvoice(entityDto, _http);
      }
    );
  }

  /**
   * Creates a POS invoice
   * @param request the amount and payment currency to create the POS
   */
  public async create(request: CreatePosRequest): Promise<CreatePosResponseDto> {
    const response = await this._http.post<CreatePosResponseDto>(`/api/v1/pos`, request);
    return response.data;
  }

  /**
   * Find a POS invoice that was shared
   * @param sharedInvoiceId the shared invoice id
   */
  public async findSharedInvoice(sharedInvoiceId: string): Promise<PosInvoice | null> {
    const response = await this._http.get<PosInvoiceDto>(`/api/v1/shared-pos/${sharedInvoiceId}`, {
      omitAuthorization: true,
      validateStatus: s => (s >= 200 && s < 300) || s === 404
    });
    if (response.status >= 200 && response.status < 300) {
      return this._factory.getOrCreateEntity(response.data);
    }
    if (response.status === 404) {
      return null;
    }
    throw new Error(`Status ${response.status} (${response.statusText}) does not indicate success`);
  }

  /**
   * @hidden
   */
  private readonly _factory: EntityFactory<PosInvoiceDto, PosInvoice>;
}
