import { Currency } from "../currencies";
import { AffiliateEarningsDto, AffiliateEarningsSummaryDto } from "../dtos";
import { HttpClient } from "../http";
import { Money } from "../money";

/** the affiliate earnings of a single currency */
export class AffiliateEarningsCurrencyInfo {
  constructor(
    /** the currency of the earnings */
    public readonly currency: Currency,
    /** the total earnings in the earned currency (e.g. Bitcoin) */
    public readonly totalEarnings: Money,
    /** the total earnings in the users native currency (e.g. USD) */
    public readonly totalNativeEarnings: Money
  ) {}
}

/**
 * the affiliate earnings
 */
export class AffiliateEarningsInfo {
  constructor(
    /** the earnings grouped by the currency */
    public readonly earningsByCurrency: AffiliateEarningsCurrencyInfo[],
    /** the total earnings in all currencies, in the users native currency */
    public readonly totalNativeEarnings: Money
  ) {}
}

/**
 * Summary of an affiliates earnings
 */
export class AffiliateEarningsSummary {
  constructor(
    /** the earnings in the last 24-hours */
    public readonly last24h: Money,
    /** the earnings in the last 7-days (week) */
    public readonly last7Days: Money,
    /** the earnings in the last 30-days (month) */
    public readonly last30Days: Money,
    /** the earnings in the last year (365 days) */
    public readonly lastYear: Money,
    /** the all time earnings */
    public readonly allTime: Money,
    /** the total conversions through every channel */
    public readonly totalConversions: number
  ) {}
}

/**
 * the options accepted by [[AffiliateEarnings.list]]
 */
export interface ListEarningsOptions {
  /** the id of the channel for which to get earnings for */
  channelId?: string;
  /** the starting date for which to list earnings for (if not specified then the beginning of time) */
  from?: string;
  /** the ending date for which to list earnings for (if not specified then the end of time) */
  to?: string;
}

/**
 * provides access to information about the affiliates earnings
 */
export class AffiliateEarnings {
  /** @hidden */
  constructor(
    /** @hidden */ private readonly _http: HttpClient,
    /** @hidden */ private readonly _getCurrency: (id: string) => Promise<Currency>
  ) {}

  /**
   * List the earnings of the affiliate, optionally filtered by a channel and time period
   * @param options additional filtering options
   */
  public async list(options: ListEarningsOptions = {}): Promise<AffiliateEarningsInfo> {
    const query = this._optionsToQuery(options);
    const url = `/api/v1/affiliate/earnings?${query}`;
    const response = await this._http.get<AffiliateEarningsDto>(url);

    const nativeCurrency = await this._getCurrency(`${response.data.totalNativeEarnings.currencyId}`);
    const earningsByCurrency = [];

    for (const item of response.data.earningsByCurrency) {
      const currencyId = item.contractAddress
        ? `${item.currencyId}:${item.contractAddress}`
        : `${item.currencyId}`;
      const earnedCurrency = await this._getCurrency(currencyId);
      const earnedMoney = Money.create(item.totalEarnings, earnedCurrency);
      const nativeMoney = Money.create(item.totalNativeEarnings, nativeCurrency);
      earningsByCurrency.push(new AffiliateEarningsCurrencyInfo(earnedCurrency, earnedMoney, nativeMoney));
    }

    const totalNativeMoney = Money.create(response.data.totalNativeEarnings, nativeCurrency);
    return new AffiliateEarningsInfo(earningsByCurrency, totalNativeMoney);
  }

  /**
   * Get a summary of the affiliates earnings
   */
  public async summary(): Promise<AffiliateEarningsSummary> {
    const url = `/api/v1/affiliate/earnings/summary`;
    const response = await this._http.get<AffiliateEarningsSummaryDto>(url);
    const dto = response.data;
    const currency = await this._getCurrency(`${dto.allTime.currencyId}`);

    return new AffiliateEarningsSummary(
      Money.create(dto.last24h, currency),
      Money.create(dto.last7Days, currency),
      Money.create(dto.last30Days, currency),
      Money.create(dto.lastYear, currency),
      Money.create(dto.allTime, currency),
      dto.totalConversions
    );
  }

  private _optionsToQuery(options: any) {
    return Object.keys(options)
      .filter(k => options[k])
      .map(k => `${k}=${options[k]}`)
      .join("&");
  }
}
