import { Account } from "../accounts";
import { Currency } from "../currencies";
import { MerchantLogoDto, MerchantProfileDto } from "../dtos";
import { HttpClient } from "../http";
import { MessageHub } from "../signalrhub";
import { AcceptedCurrencies } from "./acceptedCurrencies";
import { Invoices } from "./invoices";
import { MerchantClients } from "./merchantClients";

/**
 * Options accepted by the [[Merchant.createMerchant]] method and [[Merchant.updateProfile]] method
 */
export interface MerchantProfileOptions {
  /** merchant's business name */
  name?: string;
  /** full name of the Ultimate Beneficiary Owner (UBO) of the business */
  uboName?: string;
  /** merchant's website url */
  websiteUrl?: string;
  /** merchant's business country of registration */
  country?: string;
  /** the merchant's business address */
  address?: string;
  /** the merchant's business email */
  email?: string;
  /** optional phone number of the business */
  phone?: string;
  /** merchant's description of their business */
  description?: string;
  /** the business registration number */
  registrationNumber?: string;
}

/** Represents a merchant on the CoinPayments platform. */
export class Merchant {
  public readonly acceptedCurrencies: AcceptedCurrencies;
  public readonly invoices: Invoices;
  public readonly clients: MerchantClients;

  /** @hidden */
  constructor(
    /** @hidden */
    private readonly _http: HttpClient,
    /** @ts-ignore @hidden */
    private readonly _msgHub: MessageHub,
    /** @hidden */
    public readonly _getCurrency: (id: string) => Promise<Currency>,
    /** @hidden */
    public readonly _getAccount: (id: string) => Promise<Account>
  ) {
    /** the currencies the merchant accepts for payment */
    this.acceptedCurrencies = new AcceptedCurrencies(_http);
    this.invoices = new Invoices(_http, _msgHub, _getCurrency, _getAccount);
    this.clients = new MerchantClients(_http);
  }

  /**
   * Creates a merchant identity and merchant profile for a user or organization
   * @param profile the information of the merchant
   */
  public async create(profile: MerchantProfileOptions): Promise<void> {
    await this._http.post(`/api/v1/merchant`, profile);
  }

  /**
   * Gets the profile of an existing merchant
   */
  public async getProfile(): Promise<MerchantProfileDto> {
    const response = await this._http.get<MerchantProfileDto>(`/api/v1/merchant/profile`);
    return response.data;
  }

  /**
   * Updates the profile of an existing merchant
   * @param profile the information of the merchant
   */
  public async updateProfile(profile: Partial<MerchantProfileOptions>): Promise<void> {
    await this._http.put(`/api/v1/merchant/profile`, profile);
  }

  /**
   * Uploads the provided logo
   * @param logo the logo to upload
   * @return the uploaded url
   */
  public async uploadLogo(logo: File): Promise<string> {
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader();

      // load event triggers if reading as buffer completed successfully.
      // handler to send post request for the load event.
      fileReader.onload = async () => {
        const buffer = fileReader.result as ArrayBuffer;
        try {
          const response = await this._http.post<MerchantLogoDto>(`/api/v1/merchant/logo`, buffer);
          resolve(response.data.logoUrl);
        } catch (e) {
          reject(e);
        }
      };

      // abort event triggers when reading operation is aborted
      fileReader.onabort = () => {
        reject("Reading aborted.");
      };

      // error event triggers when reading operation encounters an error
      fileReader.onerror = () => {
        reject("Reading encountered error.");
      };

      fileReader.readAsArrayBuffer(logo);
    });
  }

  /**
   * Deletes the merchant's logo
   */
  public async deleteLogo(): Promise<void> {
    await this._http.delete(`/api/v1/merchant/logo`);
  }
}
