import { Currency } from "../currencies";
import { AccountBalancesDto } from "../dtos";
import { Money } from "../money";
import { AccountBalanceForCurrency } from "./";

/**
 * Encapsulates the total balances across all [[Account]]'s, grouped by their [[Currency]]
 */
export class AccountBalances {
  /** @hidden */
  constructor(
    /** the total balances across all the accounts grouped by their [[Currency]] */
    public readonly balances: AccountBalanceForCurrency[],
    /** the total unconfirmed balance in all the [[Account]]'s, in the users native [[Currency]] */
    public readonly totalNativeBalance: Money,
    /** the total balance available for transactions in all the [[Account]]'s, in the users native [[Currency]] */
    public readonly totalNativeAvailable: Money
  ) {}

  /** @hidden */
  public static async create(
    model: AccountBalancesDto,
    nativeCurrency: Currency,
    getCurrency: (id: string) => Promise<Currency>
  ): Promise<AccountBalances> {
    const totalNativeBalance = Money.create(model.totalNativeBalance, nativeCurrency);
    const totalNativeAvailable = Money.create(model.totalNativeAvailable, nativeCurrency);
    const balances: AccountBalanceForCurrency[] = [];

    for (const balance of model.balancesByCurrency) {
      const currency = await getCurrency(`${balance.currencyId}`);
      const totalBalance = Money.create(balance.totalBalance, currency);
      const totalAvailable = Money.create(balance.totalBalance, currency);
      const totalCurrencyNativeBalance = Money.create(balance.totalNativeBalance, nativeCurrency);
      const totalCurrencyNativeAvailable = Money.create(balance.totalNativeAvailable, nativeCurrency);
      const accountBalance = new AccountBalanceForCurrency(
        totalBalance,
        totalAvailable,
        totalCurrencyNativeBalance,
        totalCurrencyNativeAvailable
      );
      balances.push(accountBalance);
    }

    return new AccountBalances(balances, totalNativeBalance, totalNativeAvailable);
  }
}
