import { DtoRef, EntityFactory } from "../common";
import { TopUpProviderCurrencyDto, TopUpProviderDto, TopUpProviderName, TopUpProvidersDto } from "../dtos";
import { HttpClient } from "../http";
import { getQueryParamByName } from "../utils/queryStringUtils";
import { Mercuryo } from "./mercuryo";

/** Represents the providers that TopUpProviders class can return */
export interface TopUpProvider {
  /** the name of the provider */
  name: string;
  /** the url to the logo of the provider */
  logoUrl: string;
  /** the currencies that can be purchased through this provider */
  currenciesCanPurchase: TopUpProviderCurrencyDto[];
}

/** Represents top up providers available for use on the platform */
export class TopUpProviders {
  /** @hidden */
  constructor(
    /** @hidden */ private readonly _http: HttpClient,
    /** @hidden */ private readonly _isUserSignedIn: () => Promise<boolean>
  ) {
    this._factory = new EntityFactory<TopUpProviderDto, TopUpProvider>(dto => dto.name, this.createEntity);
  }

  /**
   * Get a list of the providers available to the user through which to place a top up order
   */
  public async list(): Promise<TopUpProvider[]> {
    const response = await this._http.get<TopUpProvidersDto>(`/api/v1/topups/providers`);
    const items = response.data.items;

    const providers: TopUpProvider[] = [];
    for (const provider of items) {
      providers.push(await this._factory.getOrCreateEntity(provider));
    }
    return providers;
  }

  /**
   * Finds a specific provider available to the user
   * @param name The name of the provider (this field is case-sensitive)
   */
  public async find(name: TopUpProviderName): Promise<TopUpProvider | null> {
    const url = `/api/v1/topups/providers/${name}`;

    try {
      const isSignedIn = await this._isUserSignedIn();
      if (isSignedIn) {
        // if signed in try to get the existing entity from cache
        const existing = this._factory.getEntityOrDefault(name);
        if (existing) {
          return existing;
        }

        const responseUsingUser = await this._http.get<TopUpProviderDto>(url);
        return this._factory.getOrCreateEntity(responseUsingUser.data);
      }

      // no signed in user so check if token provided
      const token = getQueryParamByName("token");
      if (!token) {
        return null;
      }

      const responseUsingToken = await this._http.get<TopUpProviderDto>(`${url}?token=${token}`, {
        omitAuthorization: true
      });

      return this._factory.getOrCreateEntity(responseUsingToken.data);
    } catch (e) {
      return null;
    }
  }

  /**
   * Creates a specific top up provider object
   * @param entityDto The entity DTO
   */
  private createEntity = async (entityDto: DtoRef<TopUpProviderDto>) => {
    switch (entityDto.dto.name) {
      case "Mercuryo":
      default:
        return new Mercuryo(this._http, entityDto, this._isUserSignedIn);
    }
  };

  private readonly _factory: EntityFactory<TopUpProviderDto, TopUpProvider>;
}
