import { DtoRef } from "../common";
import { MerchantClientWebhookEndpointDto, MerchantClientWebhookNotification } from "../dtos";
import { HttpClient } from "../http";
import { MerchantClientWebhookOptions } from "./merchantClient";

/** Represents a merchant's clients (to communicate with the API) and their webhooks' creation, updates, and deletion. */
export class Webhook implements MerchantClientWebhookEndpointDto {
  /** the id of the notifications endpoint */
  get id(): string {
    return this._model.dto.id;
  }
  /** the url to which to POST webhook notifications to */
  get notifications(): MerchantClientWebhookNotification[] {
    return this._model.dto.notifications;
  }
  /** the types of notifications to send to this endpoint */
  get notificationsUrl(): string {
    return this._model.dto.notificationsUrl;
  }
  /** the ID of the client that this webhook is associated with */
  get clientId(): string {
    return this._clientId;
  }

  /** @hidden */
  constructor(
    /** @hidden */
    private readonly _model: DtoRef<MerchantClientWebhookEndpointDto>,
    /** @hidden */
    private readonly _http: HttpClient,
    /** @hidden */
    private readonly _clientId: string
  ) {}

  /**
   * Update an existing webhook for a client
   * @param options Info used to update the webhook
   */
  public async updateWebhook(options: Partial<MerchantClientWebhookOptions>): Promise<void> {
    await this._http.put(`/api/v1/merchant/clients/${this.clientId}/webhooks/${this.id}`, options);
  }
}
