import { Currency } from "../currencies";
import { AffiliateCommissionFeesInfoDto, AffiliateInfoDto } from "../dtos";
import { HttpClient } from "../http";
import { AffiliateChannels } from "./channels";
import { AffiliateEarnings } from "./earnings";
import { AffiliateImpressions } from "./impressions";

/** Affiliate on the CoinPayments platform */
export class Affiliate {
  /**
   * provides access to channels through which an affiliate promotes CoinPayments and earns
   * commissions on any service fees generated for CoinPayments.
   */
  public readonly channels: AffiliateChannels;

  /**
   * provides access to information about conversion (referred users that sign up)
   */
  public readonly impressions: AffiliateImpressions;

  /**
   * provides access to information about the affiliates earnings
   */
  public readonly earnings: AffiliateEarnings;

  /** @hidden */
  constructor(
    /** @hidden */ private readonly _http: HttpClient,
    /** @hidden */ getCurrency: (id: string) => Promise<Currency>
  ) {
    this.channels = new AffiliateChannels(_http, getCurrency);
    this.impressions = new AffiliateImpressions(_http);
    this.earnings = new AffiliateEarnings(_http, getCurrency);
  }

  /**
   * Gets the affiliate information
   */
  public async getInfo(): Promise<Readonly<AffiliateInfoDto>> {
    const response = await this._http.get<AffiliateInfoDto>("/api/v1/affiliate");
    return response.data;
  }

  /**
   * Gets the commission fee structure information that the affiliate can earn on the CoinPayments platform
   */
  public async getCommissionFeesInfo(): Promise<Readonly<AffiliateCommissionFeesInfoDto>> {
    const response = await this._http.get<AffiliateCommissionFeesInfoDto>("/api/v1/affiliate/fees");
    return response.data;
  }
}
