/** @hidden */
export enum DeferredStatus {
  created,
  running,
  ranToCompletion,
  faulted
}

/**
 * @hidden
 * Represents the producer side of a `Promise<TResult>` unbound to a delegate, providing access to the consumer
 * side through the `promise` property.
 */
export class Deferred<TResult> {
  /** gets the current status of the deferred promise */
  get status(): DeferredStatus {
    return this._internalStatus;
  }

  /** indicating whether the deferred has completed (status == ranToCompletion || faulted) */
  get isCompleted(): boolean {
    return this.status === DeferredStatus.ranToCompletion || this.status === DeferredStatus.faulted;
  }

  /** the promise created by this deferred completion */
  public readonly promise: Promise<TResult>;

  private _internalStatus: DeferredStatus = DeferredStatus.created;
  private _internalResolve?: (result?: TResult) => void;
  private _internalReject?: (reason?: any) => void;

  constructor() {
    this.promise = new Promise<TResult>((resolve, reject) => {
      this._internalStatus = DeferredStatus.running;
      this._internalResolve = result => {
        if (this.status !== DeferredStatus.running) return;
        this._internalStatus = DeferredStatus.ranToCompletion;
        resolve(result);
      };
      this._internalReject = reason => {
        if (this.status !== DeferredStatus.running) return;
        this._internalStatus = DeferredStatus.faulted;
        reject(reason);
      };
    });
  }

  /**
   * resolve the deferred promise with the specified result
   * @param result the result
   */
  public resolve(result: TResult): void {
    if (this._internalResolve) {
      this._internalResolve(result);
    } else {
      throw new Error("not implemented"); // if somehow promise was created but executor not ran
    }
  }

  /**
   * reject the deferred promise with the specified reason
   * @param reason the reason for the rejection
   */
  public reject(reason?: any): void {
    if (this._internalReject) {
      this._internalReject(reason);
    } else {
      throw new Error("not implemented"); // if somehow promise was created but executor not ran
    }
  }
}
