import { Account } from "../accounts";
import { DtoRef, EntityFactory, UrlBuilder } from "../common";
import { Currency } from "../currencies";
import {
  NewTransactionConfirmationReceivedArgs,
  PagedItemsDto,
  TransactedVolumeDto,
  TransactionAccountInputOutputDto,
  TransactionBlockchainInputOutputDto,
  TransactionConversionIdIsSetArgs,
  TransactionDto,
  TransactionInputOutputDto
} from "../dtos";
import { FullAmount } from "../fullAmount";
import { HttpClient, HttpResponse } from "../http";
import { PagedItems } from "../paging";
import { MessageHub } from "../signalrhub";
import { SmartContract } from "../smartcontracts";
import {
  Transaction,
  TransactionAccountEndpoint,
  TransactionBlockchainEndpoint,
  TransactionEndpoint
} from "./";

/**
 * Options accepted by the [[Transactions.list]] method
 */
export interface TransactionsListOptions {
  /**
   * the id of the [[Account]] for which to list transactions, the account can appear either on the origin or receiver side of the transaction
   */
  accountId?: string;
  /** cursor that points to the end of the page of data that has been returned */
  after?: string;
  /** the maximum number of objects that may be returned, the query may return fewer results than the requested maximum. If `after` is specified then `limit` specifies the number of items to return starting from `after`.  If not specified then `limit` specifies the number of items to return from the beginning. */
  limit?: number;
  /** date from which to list transactions starting from */
  from?: string;
  /** date until which to list transactions */
  to?: string;
}

/**
 * Options accepted by the [[Transactions.getCSV]] method
 */
export interface ExportFilters {
  /** the start date that was requested */
  startDate: string;
  /** the end date that was requested */
  endDate: string;
  /** the type transactions that was requested */
  direction: "all" | "spent" | "received";
}

/**
 * provides access to the user's transactions
 */
export class Transactions {
  /** @hidden */
  constructor(
    /** @hidden */ private readonly _http: HttpClient,
    /** @hidden */ private readonly _msgHub: MessageHub,
    /** @hidden */ getCurrency: (id: string) => Promise<Currency>,
    /** @hidden */ getSmartContract: (currencyId: string, contract: string) => Promise<SmartContract | null>,
    /** @hidden */ getAccount: (id: string) => Promise<Account>
  ) {
    async function createEndpoint(
      dtoRef: DtoRef<TransactionInputOutputDto>,
      token: SmartContract | undefined
    ): Promise<TransactionEndpoint> {
      const currencyId = token
        ? `${dtoRef.dto.amount.currencyId}:${token.contractAddress}`
        : `${dtoRef.dto.amount.currencyId}`;
      const currency = await getCurrency(currencyId);
      const nativeCurrency = await getCurrency(`${dtoRef.dto.nativeAmount.currencyId}`);
      switch (dtoRef.dto.type) {
        case "account":
          const accountDto = <TransactionAccountInputOutputDto>dtoRef.dto;
          const account = await getAccount(accountDto.accountId);
          return new TransactionAccountEndpoint(
            dtoRef as DtoRef<TransactionAccountInputOutputDto>,
            currency,
            nativeCurrency,
            account
          );
        case "blockchain":
          return new TransactionBlockchainEndpoint(
            dtoRef as DtoRef<TransactionBlockchainInputOutputDto>,
            currency,
            nativeCurrency
          );
        default:
          throw new Error(`Unknown transaction input/output '${dtoRef.dto.type}'`);
      }
    }

    this._factory = new EntityFactory(
      dto => dto.id,
      async entityDto => {
        const originDto: DtoRef<TransactionInputOutputDto> = {
          get dto(): TransactionInputOutputDto {
            return entityDto.dto.from;
          }
        };
        const receiverDto: DtoRef<TransactionInputOutputDto> = {
          get dto(): TransactionInputOutputDto {
            return entityDto.dto.to;
          }
        };
        const token = entityDto.dto.contractAddress
          ? (await getSmartContract(entityDto.dto.from.amount.currencyId, entityDto.dto.contractAddress)) ||
            undefined
          : undefined;
        const origin = await createEndpoint(originDto, token);
        const receiver = await createEndpoint(receiverDto, token);
        const blockchainFee = entityDto.dto.blockchainFee
          ? await FullAmount.create(entityDto.dto.blockchainFee, getCurrency, getCurrency, token)
          : undefined;
        const coinPaymentsFee = entityDto.dto.coinPaymentsFee
          ? await FullAmount.create(entityDto.dto.coinPaymentsFee, getCurrency, getCurrency, token)
          : undefined;
        return new Transaction(entityDto, this._http, origin, receiver, coinPaymentsFee, blockchainFee);
      }
    );
  }

  /**
   * finds a transaction by its id
   * @param id the id of the transaction to retrieve
   * @returns `Promise<Transaction | null>` which fulfills with the transaction or `null` if it was not found in the system
   */
  public async find(id: string): Promise<Transaction | null> {
    const existing = this._factory.getEntityOrDefault(id);
    if (existing) {
      return existing;
    }

    const response = await this._http.get<TransactionDto>(`/api/v1/transactions/${id}`, {
      validateStatus: _ => true // validated below for 404 null
    });

    if (response.status !== 200) {
      return null;
    }

    return this._factory.getOrCreateEntity(response.data);
  }

  /**
   * gets a page of transactions for the current user, sorted most recent first.
   * @param options optional object specifying the options (e.g. filtering, paging, sorting, etc) for the list query
   * @returns `Promise<PagedTransactions>` that fulfills with a page of the most recent [[Transaction]]s matching the specified `options`
   */
  public async list(options?: TransactionsListOptions): Promise<PagedItems<TransactionDto, Transaction>> {
    const after = options ? options.after : null;
    const limit = options && options.limit ? options.limit : 100;
    const from = options ? options.from : null;
    const to = options ? options.to : null;
    const url = new UrlBuilder(`/api/v1/transactions`)
      .addQueryIfValue("after", after)
      .addQueryIfValue("limit", limit.toString())
      .addQueryIfValue("from", from)
      .addQueryIfValue("to", to)
      .toString();
    const response = await this._http.get<PagedItemsDto<TransactionDto>>(url);
    const data = response.data;

    return PagedItems.createAsync(data, dto => this._factory.getOrCreateEntity(dto), this._http);
  }

  /**
   * gets the user's transacted volume (total value of incoming and outgoing transactions), and the volume limit set in the system.
   * @param currencyId optional to specify the currency the volume and limit will be returned in. If not provided amounts returned will be in USD.
   * @returns `Promise<TransactedVolumeDto>` that fulfills with an object specifying the user's transacted volume and the system's volume limit.
   */
  public async getTransactedVolume(currencyId?: number): Promise<TransactedVolumeDto> {
    let url = `/api/v1/transactions/volume`;

    if (currencyId) {
      url = `${url}?currencyId=${currencyId}`;
    }

    const response = await this._http.get<TransactedVolumeDto>(url);
    return response.data;
  }

  /**
   * Subscribe to event indicating that the transaction has successfully get conversion id
   */
  public async startListeningTransactionConversionIdIsSet(handler: (data: Transaction) => void) {
    await this.startListeningToEvent(
      "transactionConversionIdIsSet",
      async (data: TransactionConversionIdIsSetArgs) => {
        const transactionDto = this._factory.getDtoOrDefault(data.transactionId);
        this._factory.removeEntity(data.transactionId);

        if (transactionDto) {
          transactionDto.conversionId = +data.conversionId;
          const transaction = await this._factory.getOrCreateEntity(transactionDto);
          handler(transaction);
        }
      }
    );
  }

  /**
   * Subscribe to event indicating that the transaction has successfully get confirm number
   */
  public async startListeningNewTransactionConfirmationReceived(handler: (data: Transaction) => void) {
    await this.startListeningToEvent(
      "newTransactionConfirmationReceived",
      async (data: NewTransactionConfirmationReceivedArgs) => {
        const transactionDto = this._factory.getDtoOrDefault(data.transactionId.toString());
        this._factory.removeEntity(data.transactionId.toString());

        if (transactionDto) {
          transactionDto.confirmationNumber = data.confirmationNumber;
          const transaction = await this._factory.getOrCreateEntity(transactionDto);
          handler(transaction);
        }
      }
    );
  }

  private async startListeningToEvent(event: string, handler: (data: any) => void) {
    await this._msgHub.subscribeTo<any>(event, handler);
  }

  /**
   * gets a list of activities for the selected dates
   * @param options is startDate and endDate
   */

  public async exportCSV(options: ExportFilters): Promise<HttpResponse<string>> {
    const url = `/api/v1/exports/transactions/csv`;
    try {
      return await this._http.post(url, options);
    } catch (error) {
      throw error;
    }
  }

  /**
   * @hidden
   */
  private readonly _factory: EntityFactory<TransactionDto, Transaction>;
}
