import { Currency } from "../currencies";
import { InvoicePayoutDetailsDto } from "../dtos";
import { Money } from "../money";
import { MerchantFees } from "./merchantFees";

export class InvoicePayoutDetails {
  /** the merchants payment output address at the time the hot wallet was created */
  get merchantPayoutAddress(): string {
    return this._model.merchantPayoutAddress;
  }

  /** the timestamp of when this payout was created (or scheduled)  */
  get created(): string {
    return this._model.created;
  }

  /** the timestamp of when this payout was sent (e.g. broadcast on the blockchain) */
  get sent(): string {
    return this._model.sent || "";
  }

  /** the approximate timestamp of when this payout is expected to be sent (e.g. broadcast on the blockchain) */
  get expected(): string {
    return this._model.expected || "";
  }

  /** the timestamp of when this payout was confirmed (e.g. on the blockchain) */
  get confirmed(): string {
    return this._model.confirmed || "";
  }

  /** the current state of the payout */
  get state(): "scheduled" | "sending" | "sent" | "confirmed" {
    return this._model.state;
  }

  /** The paidAmount less the <see cref="MerchantFees"/> in the merchant's accepted currency. This is the amount to be finally paid out to the merchant. */
  public readonly payoutAmount: Money;
  /** The @payoutAmount in the invoice currency  */
  public readonly payoutAmountInInvoiceCurrency: Money;
  /** The amount to be deducted from paidAmount for service fees in the merchant's accepted currency  */
  public readonly merchantFees: MerchantFees;
  /** The @merchantFees in the invoice currency  */
  public readonly merchantFeesInInvoiceCurrency: Money;

  /** @hidden */
  constructor(
    /** @hidden */
    private readonly _model: InvoicePayoutDetailsDto,
    /** @hidden */
    public readonly currency: Currency,
    /** @hidden */
    public readonly nativeCurrency: Currency
  ) {
    this.payoutAmount = Money.create(_model.payoutAmount, currency);
    this.payoutAmountInInvoiceCurrency = Money.create(_model.payoutAmountInInvoiceCurrency, nativeCurrency);
    this.merchantFees = new MerchantFees(_model.merchantFees, currency);
    this.merchantFeesInInvoiceCurrency = Money.create(_model.merchantFeesInInvoiceCurrency, nativeCurrency);
  }
}
