import { Money } from "../money";

/**
 * Encapsulates the total account balances for a single [[Currency]]
 */
export class AccountBalanceForCurrency {
  /** the [[Currency]] for the current balances object */
  public get currency() {
    return this.totalBalance.currency;
  }

  /** @hidden */
  constructor(
    /** the total unconfirmed balance */
    public readonly totalBalance: Money,
    /** the total balance available for transactions across all accounts of the current currency */
    public readonly totalAvailable: Money,
    /** the total unconfirmed balance in all accounts, in the users native currency */
    public readonly totalNativeBalance: Money,
    /** the total balance available for transactions in all accounts, in the users native currency */
    public readonly totalNativeAvailable: Money
  ) {}
}
