import { promiseTimeout } from "../async";
import { DtoRef, EntityFactory, validateArgs } from "../common";
import { Currency } from "../currencies";
import { Deferred } from "../deferred";
import {
  AccountAddressCreatedEventArgs,
  AccountAddressDto,
  AccountDto,
  AccountSpendRequestDto,
  AccountSpendRequestResponseDto,
  AccountSpendRequestValidateErrorCode,
  AccountSpendRequestValidateResponseDto,
  CreateAccountAddressResponseDto,
  PagedItemsDto,
  TokenDto,
  TransactionRequestReadyToBeSignedEventDto,
  TransactionRequestRejectedEventDto
} from "../dtos";
import { HttpClient } from "../http";
import { Money } from "../money";
import { PagedItems } from "../paging";
import { MessageHub, MessageHubUtils, SingleEventSubscription } from "../signalrhub";
import { SmartContract } from "../smartcontracts";
import { AccountAddress, AccountSpendRequest } from "./";

type AccountSpendRequestValidateAllResponseDto = AccountSpendRequestValidateResponseDto & {
  title?: string;
  detail?: string;
};

/**
 * Options accepted by the [[Account.changePassword]] method
 */
export interface AccountChangePasswordOptions {
  /** the old password used for encrypting the key */
  oldPassword: string;
  /** the new password to be used for encrypting the key */
  newPassword: string;
}

/**
 * Monetary value (an amount with a currency and contract address).
 */
export interface Token {
  /** blockchain address of the contract respresenting the token */
  contractAddress: string;
  /** the amount of money, in the tokens base (smallest) monetary unit */
  amount: string;
  /** the amount of money, in the tokens base (smallest) monetary unit */
  value: string;
  /** the common currency id of on the CoinPayments platform */
  currencyId: string;
  /** the users native currency id */
  nativeCurrencyId: number;
  /** amount of tokens in users native currency */
  nativeValue: string;
}

/**
 * Options accepted by the [[Account.createAddress]] method
 */
export interface AccountCreateAddressOptions {
  /** optional label to apply to the newly created address */
  label?: string;
}

export interface CreateAccountSpendRequestOptionsCustomData {
  /** currency id to convert to */
  convertTo?: number;
  /** token contract address of the token in the wallet to spend */
  contractAddress?: string;
  /** the destination token contract address. If different than value provided in contractAddress, then spend request will be for a conversion */
  toContractAddress?: string;
  /** the flag which identify who pays fee (receiver or sender) defalut: false */
  receiverPaysFee?: boolean;
}

/**
 * Recipient of a spend request, see [[Account.spend]]
 */
export interface CreateAccountSpendRequestRecipientOptions {
  /** the address where transaction funds will be sent */
  address: string;
  /** the amount of funds to be sent to the [[CreateAccountSpendRequestRecipientOptions.address]] */
  amount: string;
}

/**
 * Options accepted by [[Account.spend]]
 */
export interface CreateAccountSpendRequestOptions {
  /**
   * list of recipients to send funds to, at least one must be included
   */
  recipients: CreateAccountSpendRequestRecipientOptions[];
  /**
   * optional, custom memo to attach to this transaction, this will only be visible on CoinPayments
   */
  memo?: string;
  /**
   * optional, desired number of blocks in which the transaction should confirm,
   * fees will be estimates and applied based on the desired priority
   */
  desiredConfirmationInBlocks?: number;

  /**
   * any custom data for the spend request
   */
  customData?: CreateAccountSpendRequestOptionsCustomData | any;
}

/** Encapsulates the response to a wallet spend validation request */
export interface AccountSpendRequestValidateResponse {
  /** value indicating whether the provided request was valid */
  isValid: boolean;
  /** list of errors, if any */
  errors: { code: AccountSpendRequestValidateErrorCode | "BadRequest"; description: string }[];
}

/** Represents a financial account on the CoinPayments platform. */
export class Account {
  /** the unique id of the account */
  get id(): string {
    return this._model.dto.accountId;
  }

  /** the [[Currency]] this account holds */
  get currency(): Currency {
    return this._currency;
  }

  /** the user or system defined name of the account */
  get label(): string {
    return this._changedLabel || this._model.dto.label;
  }

  /** the current (last) deposit address for the account (for crypto currency accounts) */
  get depositAddress(): string {
    return this._model.dto.depositAddress || "";
  }

  /**
   * the total (unconfirmed) balance in the account, the funds available for transactions are specified by the [[Account.available]] property
   */
  get balance(): string {
    return this.currency.convertSmallestUnitToDisplay(this._model.dto.balance.value);
  }

  /**
   * the amount of funds available for transactions, this is the [[Account.balance]] amount reduced by limits
   * imposed by the user or the system on this account (e.g. not all funds may have received enough confirmations)
   */
  get available(): string {
    return this.currency.convertSmallestUnitToDisplay(this._model.dto.available.value);
  }

  /**
   * whether the account has been activated and can be used
   */
  get isActive(): boolean {
    return this._model.dto.isActive;
  }

  /**
   * whether the account has been locked by admin and can't be used
   */
  get isLocked(): boolean {
    return this._model.dto.isLocked;
  }

  /**
   * whether new addresses can be created for the account
   */
  get canCreateAddresses(): boolean {
    return this._model.dto.canCreateAddresses;
  }

  /**
   * Monetary value (an amount with a currency and contract address).
   */
  get tokens(): TokenDto[] | undefined {
    return this._model.dto.tokens;
  }

  /**
   * the total (unconfirmed) balance in the account, in the users native currency, the funds available for transactions
   * are specified by the [[Account.nativeAvailable]] property
   */
  public readonly nativeBalance: Money;

  /**
   * the amount of funds available for transactions, in the users native currency, this is the [[Account.nativeBalance]] amount
   * reduced by limits imposed by the user or the system on this account (e.g. not all funds may have received enough confirmations)
   */
  public readonly nativeAvailable: Money;

  /** @hidden */
  constructor(
    /** @hidden */ private readonly _model: DtoRef<AccountDto>,
    /** @hidden */ private readonly _http: HttpClient,
    /** @hidden */ private readonly _hub: MessageHub,
    /** @hidden */ private readonly _currency: Currency,
    /** @hidden */ nativeCurrency: Currency,
    /** @hidden */ private readonly _getCurrency: (id: string) => Promise<Currency>,
    /** @hidden */ private readonly _getSmartContract: (
      contractAddress: string
    ) => Promise<SmartContract | null>
  ) {
    this._factory = new EntityFactory(
      dto => dto.addressId,
      entityDto => Promise.resolve(new AccountAddress(entityDto, this._http, this.id))
    );
    this.nativeBalance = Money.create(() => _model.dto.nativeBalance, nativeCurrency);
    this.nativeAvailable = Money.create(() => _model.dto.nativeAvailable, nativeCurrency);
  }

  /**
   * Changes the label of the account, this can only be done by users who have `Admin` access to the account
   * @param newLabel the new label for the account (must be a non-empty string)
   * @returns `Promise<void>` that fulfills when the request to update the account label has been successfully sent to the server, the server may not have completed the update yet though.
   */
  public async changeLabel(newLabel: string): Promise<void> {
    if (!newLabel) {
      throw new Error("Invalid label");
    }

    await this._http.put(`/api/v1/accounts/${this.id}/label`, {
      label: newLabel
    });

    this._changedLabel = newLabel;
  }

  /**
   * finds an address belonging to the account by its CoinPayments id or network address
   * @param idOrNetworkAddress the CoinPayments id for the address or its network address
   * @returns `Promise<AccountAddress | null>` that fulfills with the address if it exists, `null` otherwise
   */
  public async findAddress(idOrNetworkAddress: string): Promise<AccountAddress | null> {
    const existing = this._factory.getEntityOrDefault(idOrNetworkAddress);
    if (existing) {
      return existing;
    }

    const response = await this._http.get<AccountAddressDto>(
      `/api/v1/accounts/${this.id}/addresses/${idOrNetworkAddress}`,
      {
        validateStatus: _ => true // validated below for 404 null
      }
    );

    if (response.status !== 200) {
      return null;
    }

    return this._factory.getOrCreateEntity(response.data);
  }

  /**
   * lists all the accounts deposit addresses, ordering by newest first
   * @returns `Promise<PagedItems<AccountAddress>>` that fulfills with a page of [[AccountAddress]]
   */
  public async listAddresses(): Promise<PagedItems<AccountAddressDto, AccountAddress>> {
    if (!this._addresses) {
      const page = await this._http.get<PagedItemsDto<AccountAddressDto>>(
        `/api/v1/accounts/${this.id}/addresses`
      );
      this._addresses = await PagedItems.createAsync(
        page.data,
        dto => this._factory.getOrCreateEntity(dto),
        this._http
      );
    }
    return this._addresses;
  }

  /**
   * creates a new public deposit address for the account
   * @param options
   * @returns `Promise<AccountAddress>` that fulfills with the newly created [[AccountAddress]]
   */
  public async createAddress(options: AccountCreateAddressOptions): Promise<AccountAddress> {
    const deferredAddressId = new Deferred<string>();
    const serverAddressCreatedSubscription = await MessageHubUtils.subscribeToOneAsync<
      AccountAddressCreatedEventArgs
    >(this._hub, "accountAddressCreatedEvent", async args => {
      return args.accountId === this.id && args.address.addressId === (await deferredAddressId.promise);
    });

    try {
      const createResponse = await this._http.post<CreateAccountAddressResponseDto>(
        `/api/v1/accounts/${this.id}/addresses`,
        {
          label: options.label
        }
      );

      deferredAddressId.resolve(createResponse.data.addressId);
    } catch (e) {
      deferredAddressId.reject(e);
      throw e;
    }

    const created = await serverAddressCreatedSubscription.promise;

    return this._factory.getOrCreateEntity(created.address);
  }

  /**
   * validates a request to spend funds from this account, `options` listing the recipients of the funds
   * @param options
   * @returns `Promise<AccountSpendRequestValidateResponse>`
   */
  public async validateSpend(
    options: CreateAccountSpendRequestOptions
  ): Promise<AccountSpendRequestValidateResponse> {
    validateArgs(options, ["recipients"]);
    const spendRequest = await this._spendRequestOptionsToDto(options);
    const validationResponse = await this._http.post<AccountSpendRequestValidateAllResponseDto>(
      `/api/v1/accounts/${this.id}/spend/validate`,
      spendRequest,
      { validateStatus: _ => true }
    );

    const data = validationResponse.data;

    if (typeof data.isValid !== "undefined") {
      return {
        isValid: data.isValid || false,
        errors: data.errors || []
      };
    }

    // bad request

    return {
      isValid: false,
      errors: [{ code: "BadRequest", description: data.detail || data.title || "Bad Request" }]
    };
  }

  /**
   * creates a request to spend funds from this account, `options` listing the recipients of the funds
   * @param options
   * @returns `Promise<AccountSpendRequest>`
   */
  public async spend(options: CreateAccountSpendRequestOptions): Promise<AccountSpendRequest> {
    const spendRequest = await this._spendRequestOptionsToDto(options);
    return this._sendSpendRequest(`/api/v1/accounts/${this.id}/spend`, spendRequest, options);
  }

  private async _spendRequestOptionsToDto(
    options: CreateAccountSpendRequestOptions
  ): Promise<AccountSpendRequestDto> {
    const contractAddress = options.customData && options.customData.contractAddress;
    const smartContract = contractAddress ? await this._getSmartContract(contractAddress) : undefined;

    return {
      recipients: options.recipients.map(r => {
        const smallestAmount = smartContract
          ? smartContract.convertAmountToSmallestUnit(r.amount)
          : this.currency.convertAmountToSmallestUnit(r.amount);

        return {
          address: r.address,
          amount: smallestAmount.toString(10)
        };
      }),
      memo: options.memo,
      customData: options.customData,
      desiredConfirmationInBlocks: options.desiredConfirmationInBlocks
    };
  }

  private async _sendSpendRequest(
    url: string,
    spendRequest: AccountSpendRequestDto,
    options: CreateAccountSpendRequestOptions
  ) {
    validateArgs(options, ["recipients"]);
    if (options.recipients.length < 1) {
      throw new Error(`At least one recipient must be specified`);
    }

    const deferredSpendId = new Deferred<string>();
    const readySubscription = await MessageHubUtils.subscribeToOneAsync<
      TransactionRequestReadyToBeSignedEventDto
    >(this._hub, "transactionRequestReadyToBeSignedEvent", async data => {
      return data.id === (await deferredSpendId.promise);
    });
    const rejectSubscription = await MessageHubUtils.subscribeToOneAsync<TransactionRequestRejectedEventDto>(
      this._hub,
      "transactionRequestRejectedEvent",
      async data => {
        return data.id === (await deferredSpendId.promise);
      }
    );
    // send a request to spend from the account to the server
    let walletLabel: string;
    let withdrawalDisplayAmount: string;
    try {
      const createResponse = await this._http.post<AccountSpendRequestResponseDto>(url, spendRequest);
      deferredSpendId.resolve(createResponse.data.spendRequestId);
      walletLabel = createResponse.data.walletLabel;
      withdrawalDisplayAmount = createResponse.data.withdrawalDisplayAmount;
    } catch (e) {
      deferredSpendId.reject(e);
      readySubscription.unsubscribe();
      rejectSubscription.unsubscribe();
      throw e;
    }
    await this._waitTheServeAcceptTheRequest(readySubscription, rejectSubscription);

    const readyData = await readySubscription.promise;

    const fromToken = readyData.contractAddress
      ? await this._getSmartContract(readyData.contractAddress)
      : void 0;

    const toToken = readyData.toContractAddress
      ? await this._getSmartContract(readyData.toContractAddress)
      : void 0;

    const toCurrency =
      readyData.toCurrencyId && readyData.toCurrencyId !== parseInt(this.currency.id, 10)
        ? await this._getCurrency(`${readyData.toCurrencyId}`)
        : this.currency;

    return new AccountSpendRequest(
      this,
      readyData,
      walletLabel,
      withdrawalDisplayAmount,
      fromToken || undefined,
      toToken || undefined,
      options.memo,
      toCurrency,
      this._http,
      this._hub
    );
  }

  /**
   * wait for the server to either accept the request and respond with a ready message or reject it and respond accordingly
   * @param readySubscription,
   * @param rejectSubscription
   */
  private async _waitTheServeAcceptTheRequest(
    readySubscription: SingleEventSubscription<TransactionRequestReadyToBeSignedEventDto>,
    rejectSubscription: SingleEventSubscription<TransactionRequestRejectedEventDto>
  ): Promise<void> {
    const timeoutReady = promiseTimeout(
      10 * 1000,
      readySubscription.promise.then(data => ({ success: true, data }))
    );
    const timeoutReject = promiseTimeout(
      11 * 1000,
      rejectSubscription.promise.then(data => ({ success: false, data }))
    );

    let serverResult;

    try {
      serverResult = await Promise.race([timeoutReady, timeoutReject]);
    } finally {
      readySubscription.unsubscribe();
      rejectSubscription.unsubscribe();
    }

    if (!serverResult.success) {
      const rejectedData = await timeoutReject;
      throw new Error(`${rejectedData.data.code}: ${rejectedData.data.reason}`);
    }
  }

  /**
   * @hidden
   */
  private readonly _factory: EntityFactory<AccountAddressDto, AccountAddress>;

  /** @hidden */
  private _addresses?: PagedItems<AccountAddressDto, AccountAddress>;

  /** @hidden */
  private _changedLabel?: string;
}
