import { Currency } from "./currencies";
import { convertSmallestUnitsToDisplayAmount } from "./currencies/utils";
import { InvoiceMoneyDto, MoneyDto } from "./dtos";
import { SmartContract } from "./smartcontracts";

/**
 * Monetary value (an amount with a currency).
 */
export class Money {
  /** the amount of money */
  public get amount(): string {
    return this._amountSelector();
  }

  private readonly _amountSelector: () => string;

  constructor(
    amountOrSelector: string | (() => string),
    /** the currency the [[Money.amount]] is represented in */
    public readonly currency: Currency,
    public readonly token?: SmartContract
  ) {
    this._amountSelector = typeof amountOrSelector === "function" ? amountOrSelector : () => amountOrSelector;
  }

  /** @hidden */
  public static create(
    dtoOrGetter: MoneyDto | InvoiceMoneyDto | (() => MoneyDto | InvoiceMoneyDto),
    currency: Currency,
    token?: SmartContract
  ): Money {
    const convertMoneyAmountToCurrency = (money: MoneyDto | InvoiceMoneyDto) => {
      const decimals = token ? token.decimals : currency.decimalPlaces;
      return convertSmallestUnitsToDisplayAmount(money.value, decimals);
    };

    if (typeof dtoOrGetter === "function") {
      return new Money(() => {
        const dto = dtoOrGetter();
        return convertMoneyAmountToCurrency(dto);
      }, currency);
    }

    return new Money(convertMoneyAmountToCurrency(dtoOrGetter), currency, token);
  }
}
