export const getQueryParamByName = (name: string): string | null => {
  const results = new RegExp("[?&]" + name + "=([^&]*)").exec(window.location.search);
  if (results == null) {
    return null;
  } else {
    // return the captured group which is the value of the param if it exists
    return results[1] || null;
  }
};
