import { Currency } from "../currencies";
import { MerchantFeesDto } from "../dtos";
import { Money } from "../money";

export class MerchantFees {
  /** Fee charged by CoinPayments  */
  public readonly transactionFee: Money;
  /** The amount to be deducted from paidAmount for service fees in the merchant's accepted currency  */
  public readonly networkFee: Money;
  /** The amount to be deducted from paidAmount for service fees in the merchant's accepted currency  */
  public readonly conversionFee: Money | null;

  /** @hidden */
  constructor(
    /** @hidden */
    protected readonly _model: MerchantFeesDto,
    /** @hidden */
    public readonly currency: Currency
  ) {
    this.transactionFee = Money.create(_model.transactionFee, currency);
    this.networkFee = Money.create(_model.networkFee, currency);
    this.conversionFee =
      _model.conversionFee === null ? _model.conversionFee : Money.create(_model.conversionFee, currency);
  }
}
