import * as signalR from "@microsoft/signalr";
import { Deferred } from "./deferred";

/**
 * Subscription to a single event, once the `promise` is resolved the event will be unsubscribed from
 * @hidden
 */
export interface SingleEventSubscription<TEventData> extends Subscription {
  /**
   * Event to await upon to receive the event from the [[MessageHub]], once it resolves the event will be
   * unsubscribed from
   */
  readonly promise: PromiseLike<TEventData>;
}

/**
 * Represents a subscription to an event on a [[MessageHub]]
 * @hidden
 */
export interface Subscription {
  /**
   * unsubscribes this subscription
   */
  unsubscribe(): void;
}

/**
 * Utility methods for [[MessageHub]]
 * @hidden
 */
export class MessageHubUtils {
  /**
   * Subscribes to a single event matching the specified predicate, once the predicate returns true the subscription is
   * torn down and the data returned by the [[SingleEventSubscription.promise]].
   * @param hub the hub where to register the subscription
   * @param eventName the name of the event to subscribe to
   * @param predicate predicate to determine whether the subscription has been fulfilled
   * @returns `Promise<SingleEventSubscription<TEventData>>` that fulfills with a promise to await upon to complete
   */
  public static async subscribeToOne<TEventData>(
    hub: MessageHub,
    eventName: string,
    predicate: (data: TEventData) => boolean
  ): Promise<SingleEventSubscription<TEventData>> {
    return this.subscribeToOneAsync(hub, eventName, data => Promise.resolve(predicate(data)));
  }

  /**
   * Subscribes to a single event matching the specified async predicate, once the predicate returns true the
   * subscription is torn down and the data returned by the [[SingleEventSubscription.promise]].
   * @param hub the hub where to register the subscription
   * @param eventName the name of the event to subscribe to
   * @param predicate predicate to determine whether the subscription has been fulfilled
   * @returns `Promise<SingleEventSubscription<TEventData>>` that fulfills with a promise to await upon to complete
   */
  public static async subscribeToOneAsync<TEventData>(
    hub: MessageHub,
    eventName: string,
    predicate: (data: TEventData) => Promise<boolean>
  ): Promise<SingleEventSubscription<TEventData>> {
    const deferred = new Deferred<TEventData>();
    const subscription = await hub.subscribeTo<TEventData>(eventName, async data => {
      if (deferred.isCompleted) {
        return;
      }
      if (await predicate(data)) {
        deferred.resolve(data);
      }
    });

    const waitThenUnsubscribe = async () => {
      try {
        return await deferred.promise;
      } finally {
        subscription.unsubscribe();
      }
    };

    return { promise: waitThenUnsubscribe(), unsubscribe: () => subscription.unsubscribe() };
  }
}

/**
 * Abstract event message hub
 * @hidden
 */
export interface MessageHub {
  subscribeTo<TEventData>(
    eventName: string,
    callback: (data: TEventData) => void
  ): Promise<{ unsubscribe(): void }>;
}

/**
 * SignalR implementation of the event message hub
 * @hidden
 */
export class SignalRHub implements MessageHub {
  constructor(
    private readonly hubUrl: string,
    private readonly getAuthTokenCallback: () => Promise<string>
  ) {}

  /**
   * Registers a handler that will be invoked when the hub receives a message with the specified
   * event method name
   * @param eventName the name of the method to register a handler for
   * @param callback the method to call when the eventName method is received
   * @returns `Promise<Subscription>` which fulfills with a [[Subscription]] when the connection has been initiated and handler attached
   */
  public async subscribeTo<TEventData>(
    eventName: string,
    callback: (data: TEventData) => void
  ): Promise<Subscription> {
    const handler = (data: TEventData) => {
      callback(data);
    };
    const connection = await this.getConnection();
    connection.on(eventName, handler);
    return { unsubscribe: () => connection.off(eventName, handler) };
  }

  private async getConnection(): Promise<signalR.HubConnection> {
    if (this.connection) {
      return this.connection;
    }
    const connection = new signalR.HubConnectionBuilder()
      .withUrl(this.hubUrl, {
        accessTokenFactory: () => this.getAuthTokenCallback()
      })
      .withAutomaticReconnect()
      .build();
    await connection.start();
    connection.onreconnecting(() => {
      // tslint:disable-next-line:no-console
      console.assert(connection.state === signalR.HubConnectionState.Reconnecting);
    });
    connection.onreconnected(() => {
      // tslint:disable-next-line:no-console
      console.assert(connection.state === signalR.HubConnectionState.Connected);
    });
    return (this.connection = connection);
  }

  private connection?: signalR.HubConnection;
}
