/**
 * Returns a promise which resolves if `promise` resolves before the number of `ms` milliseconds is up,
 * otherwise is rejected after `ms` milliseconds.
 * @hidden
 * @param ms the milliseconds after which to timeout (reject the resuling promise)
 * @param promise the promise to timeout
 * @returns `Promise<TResult>` that is resolved if `promise` resolves before `ms` milliseconds or is rejected
 */
export function promiseTimeout<TResult>(ms: number, promise: PromiseLike<TResult>): Promise<TResult> {
  if (ms <= 0) throw new Error("timeout must be greater than 0");
  if (!promise) throw new Error("argument null exception");

  const timeout = new Promise<TResult>((_, reject) => {
    // tslint:disable-next-line: ban
    const id = setTimeout(() => {
      clearTimeout(id);
      reject("Operation timed out");
    }, ms);
  });

  // race between `promise` and the `timeout` promise
  return Promise.race([promise, timeout]);
}
