import { DtoRef } from "../common";
import { UserNotificationDto, UserNotificationType } from "../dtos";
import { HttpClient } from "../http";

/** Represents a user dashboard notification */
export class UserNotification implements UserNotificationDto {
  /** the id of the notification */
  get id(): string {
    return this._model.dto.id;
  }
  /** the heading of the notification */
  get heading(): string {
    return this._model.dto.heading;
  }
  /** the body of the notification */
  get body(): string {
    return this._model.dto.body;
  }
  /** the date the notification started showing (was published) */
  get publishedDate(): string {
    return this._model.dto.publishedDate;
  }
  /** indicates whether notification has been read by the user */
  get isRead(): boolean {
    return this._model.dto.isRead;
  }
  /** the type of the notification */
  get type(): UserNotificationType {
    return this._model.dto.type;
  }
  /** the name to give the action (e.g. such as a button to go to the actionUrl) */
  get actionName(): string | undefined {
    return this._model.dto.actionName;
  }
  /** link to go view more information about the notification or to take some action */
  get actionUrl(): string | undefined {
    return this._model.dto.actionUrl;
  }

  /** @hidden */
  constructor(
    /** @hidden */
    private readonly _model: DtoRef<UserNotificationDto>,
    /** @hidden */
    private readonly _http: HttpClient
  ) {}

  /**
   * Mark a notification as read by the currently logged in user
   */
  public async markAsRead(): Promise<void> {
    await this._http.post(`/api/v1/notifications/${this.id}/read`);
  }
}
