import bn from "bn.js";
import { promiseTimeout } from "../async";
import { validateArgs } from "../common";
import { Currency } from "../currencies";
import {
  AccountSpendRequestBroadcastDto,
  AccountSpendRequestTwoFAResponseDto,
  TransactionRequestReadyToBeSignedEventDto,
  TransactionRequestRejectedEventDto
} from "../dtos";
import { HttpClient } from "../http";
import { MessageHub, MessageHubUtils } from "../signalrhub";
import { SmartContract } from "../smartcontracts";
import { Account } from "./";

/**
 * Options accepted by [[UtxoAccountSpendRequest.signAndSend]]
 */
export interface ConfirmAndSendAccountSpendRequestOptions {
  /**
   * the account password, used to decrypt the client key for signing transactions
   */
  password: string;
  /**
   * the second factor authentication code, e.g. (email or google authenticator)
   */
  otp: string;
}

/** Recipient of a transaction */
export interface AccountSpendRequestRecipient {
  /** the currency the recipient will receive */
  currency: Currency;
  /** the address where the funds will be sent */
  address: string;
  /** the amount of funds that will be received at the [[AccountSpendRequestRecipient.address]] */
  amount: string;
}

/**
 * Represents a request to spend funds from an [[Account]]
 */
export class AccountSpendRequest {
  /**
   * the recipients of the spend requests, where the payment will be sent
   */
  public readonly recipients: AccountSpendRequestRecipient[];

  /**
   * the account from which the spend request was created
   */
  public get account() {
    return this._account;
  }

  /**
   * the currency of the spend request
   */
  public get currency() {
    return this._account.currency;
  }

  /**
   * the token, if spending from token wallet, undefined otherwise
   */
  public get fromToken(): SmartContract | undefined {
    return this._fromToken;
  }

  /**
   * the token, if spending to token wallet, undefined otherwise
   */
  public get toToken(): SmartContract | undefined {
    return this._toToken;
  }

  /**
   * the amount of funds to send provided by user (fees are not included)
   */
  public get fromAmount(): string {
    return this._fromToken
      ? this._fromToken.convertSmallestUnitToDisplay(this._request.fromAmount.toString())
      : this.currency.convertSmallestUnitToDisplay(this._request.fromAmount.toString());
  }

  /**
   * the network fees charged for the transaction in the [[Currency]] of the account
   */
  public get networkFee() {
    return this._fromToken
      ? this._fromToken.convertSmallestUnitToDisplay(this._request.networkFee.toString())
      : this.currency.convertSmallestUnitToDisplay(this._request.networkFee.toString());
  }

  /**
   * the service fees charged for the transaction in the [[Currency]] of the account
   */
  public get serviceFee() {
    return this._fromToken
      ? this._fromToken.convertSmallestUnitToDisplay(this._request.serviceFee.toString())
      : this.currency.convertSmallestUnitToDisplay(this._request.serviceFee.toString());
  }

  /**
   * the total funds that will be spent in the transaction
   */
  public get total(): string {
    const result = this._request.total;
    return this.currency.convertSmallestUnitToDisplay(result);
  }

  /**
   * the total funds in erc20 that will be spent in the transaction
   */
  public get tokenTotal(): string | undefined {
    const result = new bn(this._request.tokenTotal, 10);
    return this._fromToken ? this._fromToken.convertSmallestUnitToDisplay(result.toString(10)) : undefined;
  }

  /**
   * custom memo for this request/transaction, this will only be visible on CoinPayments
   */
  public get memo() {
    return this._memo || "";
  }

  private _spendRequestToken!: string;

  /**
   * @hidden
   */
  constructor(
    /** @hidden */ private readonly _account: Account,
    /** @hidden */ private readonly _request: TransactionRequestReadyToBeSignedEventDto,
    /** @hidden */ private readonly _walletLabel: string,
    /** @hidden */ private readonly _withdrawalDisplayAmount: string,
    /** @hidden */ private readonly _fromToken: SmartContract | undefined,
    /** @hidden */ private readonly _toToken: SmartContract | undefined,
    /** @hidden */ private readonly _memo: string | undefined,
    /** @hidden */ toCurrency: Currency,
    /** @hidden */ private readonly _http: HttpClient,
    /** @hidden */ private readonly _msgHub: MessageHub
  ) {
    this.recipients = _request.outputs.map(i => {
      const displayAmount = _toToken
        ? _toToken.convertSmallestUnitToDisplay(i.toAmount || i.amount!)
        : toCurrency.convertSmallestUnitToDisplay(i.toAmount || i.amount!);

      return {
        address: i.address,
        amount: displayAmount,
        currency: toCurrency
      };
    });
  }

  /**
   * Send twoFA code (if email)
   * Should be run regardless of current twoFA method (email or google) because it returns token required for signing transaction
   */
  public async sendTwoFAToUser(): Promise<boolean> {
    const body = {
      addresses: [this._request.outputs[0].address],
      walletLabel: this._walletLabel,
      withdrawalDisplayAmount: this._withdrawalDisplayAmount
    };
    const response = await this._http.post<AccountSpendRequestTwoFAResponseDto>(
      `/api/v1/accounts/twofa/${this._request.id}`,
      body
    );
    this._spendRequestToken = response.data.spendRequestToken;
    return true;
  }

  /**
   * Sign and send the transaction
   * @param options specifies the password to decrypt the account key with and second factor password to validate the request
   * @returns `Promise<void>` which fulfills after the server has successfully broadcast the transaction
   */
  public async signAndSend(options: ConfirmAndSendAccountSpendRequestOptions): Promise<void> {
    validateArgs(options, ["password", "otp"]);
    if (!this._spendRequestToken) {
      throw new Error("spendRequestToken is not defiend");
    }

    let txHex: string;
    txHex = this._request.unsignedTx; // just send back for server to sign

    const broadcastData: AccountSpendRequestBroadcastDto = {
      spendRequestToken: this._spendRequestToken,
      signedTxHex: txHex, // not always signed, rename or make this optional maybe?
      otp: options.otp
    };

    const broadcastedSubscription = await MessageHubUtils.subscribeToOneAsync<{ id: string }>(
      this._msgHub,
      "transactionRequestBroadcastedEvent",
      async data => {
        return data.id === this._request.id;
      }
    );

    const rejectedSubscription = await MessageHubUtils.subscribeToOneAsync<
      TransactionRequestRejectedEventDto
    >(this._msgHub, "transactionRequestRejectedEvent", async data => {
      return data.id === this._request.id;
    });

    await this._http.post(
      `/api/v1/accounts/${this.account.id}/spend/${this._request.id}/broadcast`,
      broadcastData
    );

    const timeoutBroadcast = promiseTimeout(
      10 * 1000,
      broadcastedSubscription.promise.then(data => ({ success: true, data }))
    );
    const timeoutReject = promiseTimeout(
      11 * 1000,
      rejectedSubscription.promise.then(data => ({ success: false, data }))
    );

    let serverResult;

    try {
      serverResult = await Promise.race([timeoutBroadcast, timeoutReject]);
    } finally {
      broadcastedSubscription.unsubscribe();
      rejectedSubscription.unsubscribe();
    }

    if (!serverResult.success) {
      const rejectedData = await timeoutReject;
      throw new Error(`${rejectedData.data.code}: ${rejectedData.data.reason}`);
    }

    await broadcastedSubscription.promise;
  }
}
