import { DtoRef } from "../common";
import { SettingDto } from "../dtos";

export class Setting {
  /** the CoinPayments key for the setting */
  get key(): string {
    return this._model.dto.key;
  }
  /** value of the setting  */
  get value(): any {
    return this._model.dto.value;
  }

  /** @hidden */
  constructor(/** @hidden */ private readonly _model: DtoRef<SettingDto>) {}
}
