import { EntityFactory } from "../common";
import { SettingDto } from "../dtos";
import { HttpClient } from "../http";
import { Setting } from "./setting";

export class Settings {
  /** @hidden */
  constructor(
    /** @hidden */
    private readonly _http: HttpClient
  ) {
    this._factory = new EntityFactory(
      dto => dto.key,
      async entityDto => {
        return new Setting(entityDto);
      }
    );
  }

  /**
   * Lists all user's settings
   */
  public async list(): Promise<Setting[]> {
    const response = await this._http.get<SettingDto[]>(`/api/v1/settings/all`);
    return this._mapSettingsDtoArray(response.data);
  }

  /**
   * Find setting belonging to user by the KEY
   * @param key the key of the setting
   */
  public async find(key: string): Promise<Setting> {
    const response = await this._http.get<SettingDto>(`/api/v1/settings/${key}`);
    const setting: SettingDto = this._parseOrStringify(response.data, "parse") as SettingDto;
    return this._factory.getOrCreateEntity(setting);
  }

  /**
   * Create new setting belonging to user by the KEY with VALUE
   * @param key the key of the setting
   * @param value the value of the setting
   */
  public async add(key: string, value: any): Promise<void> {
    const jsonValue = JSON.stringify(value);
    await this._http.post<SettingDto>(`/api/v1/settings/`, { key, value: jsonValue });
  }

  /**
   * Update setting VALUE belonging to user by the KEY
   * @param key the key of the setting
   * @param value the value of the setting
   */
  public async update(key: string, value: any): Promise<void> {
    const jsonValue: string = JSON.stringify(value);
    await this._http.put<SettingDto>(`/api/v1/settings/`, { key, value: jsonValue });
  }

  /**
   * Create default user settings. Does not overwrite existing settings.
   * After successful creation returns all current user settings.
   * @param settings the array of keys and default values.
   */
  public async createDefaultSettings(settings: SettingDto[]): Promise<Setting[]> {
    if (!settings.length) {
      return [];
    }
    const body = this._parseOrStringify(settings, "stringify") as SettingDto[];
    const response = await this._http.post<SettingDto[]>(`/api/v1/settings/default`, body);
    return this._mapSettingsDtoArray(response.data);
  }

  /**
   * Map SettingDto array into Setting array
   * @param items the setting dto array
   */
  private async _mapSettingsDtoArray(items: SettingDto[]): Promise<Setting[]> {
    const settingsResult: Setting[] = [];
    const settings = this._parseOrStringify(items, "parse") as SettingDto[];
    for (const s of settings) {
      settingsResult.push(await this._factory.getOrCreateEntity(s));
    }

    return settingsResult;
  }

  private _parseOrStringify(
    settings: SettingDto | SettingDto[],
    action: "parse" | "stringify"
  ): SettingDto | SettingDto[] {
    const parse: boolean = action === "parse";

    function modify(setting: SettingDto): SettingDto {
      let value: any;
      try {
        value = parse ? JSON.parse(setting.value) : JSON.stringify(setting.value);
      } catch {
        value = setting.value;
      }
      const key = setting.key;
      return { key, value };
    }

    if (!Array.isArray(settings)) {
      return modify(settings);
    }

    const result: SettingDto[] = [];
    settings.forEach(s => {
      result.push(modify(s));
    });
    return result;
  }

  /**
   * @hidden
   */
  private readonly _factory: EntityFactory<SettingDto, Setting>;
}
