import { Account } from "../accounts";
import { DtoRef, EntityFactory } from "../common";
import { Currency } from "../currencies";
import {
  BuyerDto,
  InvoiceAmountDto,
  InvoiceCurrencyDto,
  InvoiceDto,
  InvoiceEmailDeliveryOptionsDto,
  InvoiceHistoryEventDto,
  InvoiceMerchantDto,
  InvoiceMerchantOptionsDto,
  InvoiceMetadataDto,
  InvoiceShippingDetailDto,
  InvoiceStatus,
  LineItemDto,
  PagedItemsDto
} from "../dtos";
import { HttpClient } from "../http";
import {
  Buyer,
  InvoiceAmount,
  InvoiceEmailDeliveryOptions,
  InvoiceShippingDetail,
  LineItem
} from "./interfaces";
import { InvoiceBase } from "./invoiceBase";
import { InvoicePaymentDetails } from "./invoicePaymentDetails";

/**
 * Request to update an invoice matching the data within
 */
export interface UpdateMerchantInvoiceRequest {
  /** indicates if invoice was delivered by email or to be delivered by email */
  isEmailDelivery: boolean;
  /** invoice e-mail delivery options, customers will receive an email with invoice details */
  emailDelivery?: InvoiceEmailDeliveryOptions;
  /** custom invoice date if not the created date of the invoice, invoices with a future date will be scheduled */
  invoiceDate?: string;
  /** optional due date to be shown on the invoice */
  dueDate?: string;
  /** flag indicating whether this is a draft invoice */
  draft?: boolean;
  /** the optional API caller provided external invoice number.  Appears in screens shown to the Buyer and emails sent. */
  invoiceId?: string;
  /** the buyer information, if not provided it will be requested during payment so that refunds can be sent if there is a problem with the payment. */
  buyer?: Buyer;
  /** the purchase description, can be provided instead of a list of `items` */
  description?: string;
  /** the optional array of items that a buyer intends to purchase from the merchant */
  items?: LineItem[];
  /** the total amount of the invoice, with an optional breakdown that provides details, such as the total item
   * amount, total tax amount, shipping, handling, insurance and discounts, if any
   */
  amount: InvoiceAmount;
  /** the optional shipping method and address */
  shipping?: InvoiceShippingDetail;
  /** any custom data the caller wishes to attach to the invoice which will be sent back in notifications */
  customData?: object;
  /**
   * flag indicating whether a buyer name and email are required, they will be requested at checkout
   * if not provider by the caller.  The @buyerDataCollectionMessage will be displayed
   * to the buyer when prompted.
   */
  requireBuyerNameAndEmail?: boolean;
  /** the message to display when collecting buyer user data */
  buyerDataCollectionMessage?: string;
  /** any additional information to share with the buyer about the transaction */
  notesToRecipient?: string;
  /** any terms and conditions, e.g. a cancellation policy */
  termsAndConditions?: string;
  /** options to show/hide merchant information on an invoice, or include additional merchant information specific to an invoice */
  merchantOptions?: Partial<InvoiceMerchantOptionsDto>;
  /** the invoice PO number */
  poNumber?: string;
}

/** Represents a merchant's invoice on the CoinPayments platform. */
export class Invoice extends InvoiceBase implements InvoiceDto {
  /** the CoinPayments id for the invoice */
  get id(): string {
    return this._model.dto.id;
  }
  /** the optional API caller provided external invoice number.  Appears in screens shown to the Buyer and emails sent. */
  get invoiceId(): string {
    return this._model.dto.invoiceId;
  }
  /** the timestamp when the invoice was created */
  get created(): string {
    return this._model.dto.created;
  }
  /** the date of the invoice, either the system created date or custom date specified by the merchant */
  get invoiceDate(): string {
    return this._model.dto.invoiceDate;
  }
  /** the optional due date of the invoice */
  get dueDate(): string | undefined {
    return this._model.dto.dueDate;
  }
  /** the timestamp when the invoice was confirmed (paid) */
  get confirmed(): string | undefined {
    return this._model.dto.confirmed;
  }
  /** the timestamp when the invoice was completed (paid out to the merchant and CoinPayments fees) */
  get completed(): string | undefined {
    return this._model.dto.completed;
  }
  /** the timestamp when the invoice was manually cancelled. Applicable for payment invoices only */
  get cancelled(): string | undefined {
    return this._model.dto.cancelled;
  }
  /** the currency the invoice is in */
  get currency(): InvoiceCurrencyDto {
    return this._model.dto.currency;
  }
  /** the merchant the invoice is for */
  get merchant(): InvoiceMerchantDto {
    return this._model.dto.merchant;
  }
  /** options to show/hide merchant information on an invoice, or include additional merchant information specific to an invoice */
  get merchantOptions(): InvoiceMerchantOptionsDto {
    return this._model.dto.merchantOptions;
  }
  /** the buyer information, if not provided it will be requested during payment so that refunds can be sent if there is a problem with the payment. */
  get buyer(): BuyerDto {
    return this._model.dto.buyer;
  }
  /** the purchase description */
  get description(): string {
    return this._model.dto.description;
  }
  /** the optional array of items and/or services that a buyer intends to purchase from the merchant */
  get items(): LineItemDto[] {
    return this._model.dto.items;
  }
  /** the total amount of the invoice, with an optional breakdown that provides details, such as the total item amount, total tax amount, shipping, handling, insurance and discounts, if any */
  get amount(): InvoiceAmountDto {
    return this._model.dto.amount;
  }
  /** the shipping method and address */
  get shipping(): InvoiceShippingDetailDto {
    return this._model.dto.shipping;
  }
  /** any custom data the caller wishes to attach to the invoice which will be sent back in notifications */
  get customData(): object {
    return this._model.dto.customData;
  }
  /** the status of the invoice */
  get status(): InvoiceStatus {
    return this._model.dto.status;
  }
  /**
   * flag indicating whether a buyer name and email are required, they will be requested at checkout
   * if not provider by the caller.  The @buyerDataCollectionMessage will be displayed
   * to the buyer when prompted.
   */
  get requireBuyerNameAndEmail(): boolean {
    return this._model.dto.requireBuyerNameAndEmail || false;
  }

  /** the message to display when collecting buyer user data */
  get buyerDataCollectionMessage(): string {
    return this._model.dto.buyerDataCollectionMessage || "";
  }

  /** notes for the merchant only, these are not visible to the buyers */
  get notes(): string {
    return this._model.dto.notes || "";
  }

  /** any additional information to share with the buyer about the transaction */
  get notesToRecipient(): string {
    return this._model.dto.notesToRecipient || "";
  }

  /** any terms and conditions, e.g. a cancellation policy */
  get termsAndConditions(): string {
    return this._model.dto.termsAndConditions || "";
  }

  /** indicates if invoice was delivered or to be delivered by email */
  get isEmailDelivery(): boolean {
    return this._model.dto.isEmailDelivery;
  }

  /** the invoice email delivery options if the invoice is to be emailed */
  get emailDelivery(): InvoiceEmailDeliveryOptionsDto | undefined {
    return this._model.dto.emailDelivery;
  }

  /** the invoice metadata, the integration and host where the invoice was created */
  get metadata(): InvoiceMetadataDto | undefined {
    return this._model.dto.metadata;
  }

  /** the invoice PO number */
  get poNumber(): string | undefined {
    return this._model.dto.poNumber;
  }

  /** @hidden */
  constructor(
    /** @hidden */ private readonly _model: DtoRef<InvoiceDto>,
    /** @hidden */ private readonly _http: HttpClient,
    /** @hidden */ private readonly _factory: EntityFactory<InvoiceDto, Invoice>,
    /** @hidden */ private readonly _getCurrency: (id: string) => Promise<Currency>,
    /** @hidden */ private readonly _getAccount: (id: string) => Promise<Account>,
    /** @hidden */ public readonly paymentDetails: InvoicePaymentDetails | null
  ) {
    super();
  }

  /**
   * Get payout details for an invoice, including if invoice has been fully paid out, the exact amount they will receive
   * and in what currency, which address payout will be deposited, and who (Buyer) performed the payment.
   */
  public async listPayouts(): Promise<InvoicePaymentDetails | null> {
    const response = await super.listPayouts(this._http, this.id, this._getCurrency, this._getAccount);
    return response;
  }

  /**
   * Gets the history events of an invoice
   */
  public async listHistory(): Promise<PagedItemsDto<InvoiceHistoryEventDto>> {
    return super.listHistory(this._http, this.id);
  }

  /**
   * Updates the invoice
   */
  public async update(data: UpdateMerchantInvoiceRequest): Promise<void> {
    await this._http.put<string>(`/api/v1/merchant/invoices/${this.id}`, data, {
      responseType: "text"
    });

    // do a request to the server again, server will calculate additional fields (e.g. display value for monetary amounts)
    const updated = await this._http.get<InvoiceDto>(`/api/v1/merchant/invoices/${this.id}`);
    this._factory.getOrCreateEntity(updated.data);
  }

  /**
   * Cancels the invoice
   */
  public async cancel(): Promise<void> {
    return super.cancel(this._http, this.id);
  }

  /**
   * Deletes an invoice (only works for invoices in draft or scheduled state)
   */
  public async delete(): Promise<void> {
    await super.delete(this._http, this.id);
  }
}
