import { DtoRef } from "../common";
import { AccountAddressDto } from "../dtos";
import { HttpClient } from "../http";

/**
 * Represents a deposit address of a financial account on the CoinPayments platform.
 */
export class AccountAddress {
  /**
   * the unique id of the address, this is not the same as the network address (see [[AccountAddress.address]])
   */
  public get id() {
    return this._model.dto.addressId;
  }

  /** user supplied or system generated label for this address */
  public get label() {
    return this._changedLabel || this._model.dto.label;
  }

  /** the network address */
  public get address() {
    return this._model.dto.address;
  }

  /**
   * Updates the label of the address in an account, this can only be done by users who have `Admin` access to the account
   * @param newLabel the new label for the address (must be a non-empty string)
   * @returns `Promise<void>` that fulfills when the request to change the address label has been successfully sent to the server, the server may not have completed the update yet though.
   */
  public async changeLabel(newLabel: string): Promise<void> {
    if (!newLabel) {
      throw new Error("Invalid label");
    }

    await this._http.put(`/api/v1/accounts/${this._ownerAccountId}/addresses/${this.id}/label`, {
      label: newLabel
    });

    this._changedLabel = newLabel;
  }

  private _changedLabel?: string;

  /** @hidden */
  constructor(
    /** @hidden */ private readonly _model: DtoRef<AccountAddressDto>,
    /** @hidden */ private readonly _http: HttpClient,
    /** @hidden */ private readonly _ownerAccountId: string
  ) {}
}
