/**
 * Converts an amount to the currencies smallest unit
 * @hidden
 * @param amount the amount to convert
 */
export function convertDisplayAmountToSmallestUnits(amount: string, decimalPlaces: number): bn {
  const base = 10;
  const separator = ".";
  /**
   * 1-st replacec removed all spaces, 2-nd change all comas into dots,
   * 3-rd remove all dots excluding the last one
   */
  amount = amount
    .replace(/\s/g, "")
    .replace(/,/g, ".")
    .replace(/[.](?=.*[.])/g, "");
  const parts = amount.split(separator);
  const whole = parts[0];
  const fraction = parts[1];
  const wholeMultiplier = new bn(base).pow(new bn(decimalPlaces));
  if (!fraction) {
    return new bn(whole, base).mul(wholeMultiplier);
  }
  const wholeBn = new bn(whole, base).mul(wholeMultiplier);
  const fractionPow = decimalPlaces - fraction.length;
  if (fractionPow < 0) {
    throw new Error("Too many fraction digits");
  }
  const fractionMultiplier = new bn(base).pow(new bn(fractionPow));
  const fractionBn = new bn(fraction, base).mul(fractionMultiplier);
  return wholeBn.add(fractionBn);
}

/**
 * Converts an amount in the currencies smallest unit to a display amount
 * @hidden
 * @param amount the amount in the currencies smallest unit to convert to a display amount
 */
export function convertSmallestUnitsToDisplayAmount(amount: string, decimalPlaces: number): string {
  if (amount.includes(".")) {
    const dotIndex = amount.indexOf(".");
    amount = amount.slice(0, dotIndex);
  }

  const digits = amount.length;
  let decimals = "0";
  let wholeNumbers = "0";

  if (decimalPlaces > digits) {
    decimals = amount;
    for (let i = digits; i < decimalPlaces; i++) {
      decimals = "0" + decimals;
    }
  } else if (decimalPlaces === digits) {
    decimals = amount;
  } else {
    wholeNumbers = amount.substr(0, digits - decimalPlaces);
    decimals = amount.substr(digits - decimalPlaces, decimalPlaces);
  }

  return wholeNumbers + "." + decimals;
}
