import { EntityFactory, validateArgs } from "../common";
import { Currency } from "../currencies";
import {
  AccountBalancesDto,
  AccountDto,
  CreateAccountResponseDto,
  CreateSingleSigAccountRequestDto,
  PagedItemsDto,
  TransactionCompletedOrReceivedArgs,
  TransactionConfirmedArgs
} from "../dtos";
import { HttpClient } from "../http";
import { PagedItems } from "../paging";
import { MessageHub, MessageHubUtils } from "../signalrhub";
import { SmartContract } from "../smartcontracts";
import { optionsToQuery } from "../utils/httpUtils";
import { Account, AccountBalances } from "./";

/**
 * Options accepted by the [[Accounts.balances]] method
 */
export interface AccountBalancesOptions {
  /** the id of the owning organization for which to filter the accounts by, by default accounts belonging to the user returned */
  ownerId?: string;
}

/**
 * Options accepted by the [[Accounts.list]] method
 */
export interface AccountsListOptions {
  /** cursor that points to the end of the page of data that has been returned, see [[PagedAccountsDto.paging.cursors.after]] */
  after?: string;
  /** the maximum number of objects that may be returned, the query may return fewer results than the requested maximum. If `after` is specified then `limit` specifies the number of items to return starting from `after`.  If not specified then `limit` specifies the number of items to return from the beginning. */
  limit?: number;
  /** the id of the currency to filter the accounts by */
  currencyId?: string;
  /** the id of the owning organization for which to filter the accounts by, by default accounts belonging to the user are retrieved */
  ownerId?: string;
  /** optional to query accounts either with a balance or for those that are empty */
  withBalance?: boolean;
}

/**
 * Options accepted by the [[Accounts.changePassword]] method
 */
export interface AccountsChangePasswordOptions {
  /** the old password used for encrypting the key */
  oldPassword: string;
  /** the new password to be used for encrypting the key */
  newPassword: string;
}

/**
 * Options accepted by the [[Accounts.createNewSingleSigAccount]] method
 */
export interface SingleSigAccountCreateOptions {
  /** the id of the [[Currency]] that the account will hold */
  currencyId: number;
  /**
   * the password to encrypt the account key with (this could be different from the master key password).
   * this password will be needed when creating transactions and spending funds from the account.
   */
  accountPassword: string;
  /**
   * the user specified label for the new account
   */
  label: string;
  /**
   * optional, owner id of the created wallet.  If not specified then defaults to the currently
   * authenticated user, otherwise it must be the id of an organization the user has access to.
   */
  ownerId?: string;
  /**
   * @hidden
   * optional, timeout to apply to internal operations
   */
  timeout?: number;
}

/** Provides access to the users financial accounts */
export class Accounts {
  /** @hidden */
  constructor(
    /** @hidden */
    private readonly _http: HttpClient,
    /** @hidden */
    private readonly _msgHub: MessageHub,
    /** @hidden */
    private readonly _getCurrency: (id: string) => Promise<Currency>,
    /** @hidden */ _getSmartContract: (currencyId: string, contract: string) => Promise<SmartContract | null>
  ) {
    this._factory = new EntityFactory(
      dto => dto.accountId,
      async entityDto => {
        const currency = await _getCurrency(`${entityDto.dto.currencyId}`);
        const nativeCurrency = await _getCurrency(`${entityDto.dto.nativeBalance.currencyId}`);
        return new Account(entityDto, _http, _msgHub, currency, nativeCurrency, _getCurrency, contract => {
          return _getSmartContract(`${entityDto.dto.currencyId}`, contract);
        });
      }
    );
  }

  /**
   * Create a new Single-Signature [[Account]], which is one where the user does not own the account private key.
   * Any transactions sent from the [[Account]] must be signed with the CoinPayments key.
   * @param options dictionary containing the parameters for configuring the new account
   * @returns `Promise<string>` that fulfills with the new [[Account]]'s ID
   */
  public async createNewSingleSigAccount(options: SingleSigAccountCreateOptions): Promise<Account> {
    validateArgs(options, ["currencyId", "accountPassword", "label"]);
    if (!options.label) {
      throw new Error("Label can not be empty");
    }

    if (!options.accountPassword) {
      throw new Error("Account password can not be empty");
    }

    const createRequestData: CreateSingleSigAccountRequestDto = {
      currencyId: options.currencyId,
      label: options.label,
      ownerId: options.ownerId
    };

    const activatedSubscription = await MessageHubUtils.subscribeToOneAsync<any>(
      this._msgHub,
      "accountActivatedEvent",
      async data => {
        return data.accountId;
      }
    );
    const createResponse = await this._http.post<CreateAccountResponseDto>(
      "/api/v1/accounts/singlesig",
      createRequestData
    );
    await activatedSubscription.promise;
    const accountId = createResponse.data.accountId;
    const foundAccount = await this.find(accountId);
    if (!foundAccount) {
      throw new Error("Failed to create account");
    }

    return foundAccount;
  }

  /**
   * Subscribe to an event indicating that the transaction was successfully received on the server side
   * UPD: now works only for 'sent' transactions (FRN-1926)
   */
  public async startListeningTransactionCompleted(
    handler: (data: TransactionCompletedOrReceivedArgs) => void
  ) {
    await this.startListeningToEvent("transactionCompletedEvent", data => {
      this._factory.removeEntity(data.accountId);
      handler(data);
    });
  }

  /**
   * Subscribe to an event indicating that the transaction was successfully received on the server side
   * UPD: now works only for 'receive' transactions (FRN-1926)
   */
  public async startListeningTransferReceived(handler: (data: TransactionCompletedOrReceivedArgs) => void) {
    await this.startListeningToEvent("transferReceived", data => {
      this._factory.removeEntity(data.accountId);
      handler(data);
    });
  }

  /**
   * Subscribe to an event indicating that the external transaction was successfully confirmed
   */
  public async startListeningTransactionConfirmed(handler: (data: TransactionConfirmedArgs) => void) {
    await this.startListeningToEvent("transactionConfirmed", data => {
      handler(data);
    });
  }

  private async startListeningToEvent(event: string, handler: (data: any) => void) {
    await this._msgHub.subscribeTo<any>(event, handler);
  }

  /**
   * Finds an account by its id
   * @param id the id of the account to find
   * @returns `Promise<Account>` that fulfills with the [[Account]] or null if the account was not found or inaccessible
   */
  public async find(id: string): Promise<Account | null> {
    const existing = this._factory.getEntityOrDefault(id);
    if (existing) {
      return existing;
    }

    // BU8Wcq9LywbforVCWEn5Mg - refund account id
    if (id === "BU8Wcq9LywbforVCWEn5Mg") {
      return new Account(
        {
          dto: {
            accountId: "BU8Wcq9LywbforVCWEn5Mg",
            currencyId: 1,
            depositAddress: "Unknown address",
            label: "Unknown CPS wallet",
            available: {
              amount: "0",
              value: "0",
              currencyId: "1"
            },
            balance: {
              amount: "0",
              value: "0",
              currencyId: "1"
            },
            nativeAvailable: {
              amount: "0",
              value: "0",
              currencyId: "5057"
            },
            nativeBalance: {
              amount: "0",
              value: "0",
              currencyId: "5057"
            },
            isActive: true,
            isLocked: false,
            canCreateAddresses: false
          }
        },
        <any>null,
        <any>null,
        <any>null,
        <any>null,
        <any>null,
        <any>null
      );
    }

    try {
      const response = await this._http.get<AccountDto>(`/api/v1/accounts/${id}`, {
        validateStatus: _ => true // validated below for 404 null
      });

      if (response.status !== 200) {
        return this.unknownAccount();
      }

      return this._factory.getOrCreateEntity(response.data);
    } catch (e) {
      // hotfix for transactions with deleted accounts
      return this.unknownAccount();
    }
  }

  /**
   * Gets a page of [[Account]]'s belonging to the current user
   * @param options optional object specifying the options (e.g. paging, sorting, etc) for the list query
   * @returns `Promise<PagedItems<AccountDto, Account>>` that fulfills with a page of [[Account]]'s
   */
  public async list(options?: AccountsListOptions): Promise<PagedItems<AccountDto, Account>> {
    options = options || {};
    const query = optionsToQuery(options);
    const response = await this._http.get<PagedItemsDto<AccountDto>>(`/api/v1/accounts${query}`);
    const data = response.data;
    return PagedItems.createAsync(data, dto => this._factory.getOrCreateEntity(dto), this._http);
  }

  /**
   * Gets the total balances across all the [[Account]]s
   * @param options optional options to filter the account balances by
   * @returns `Promise<AccountBalances>` that fulfills with an object with the total balances across all accounts
   */
  public async balances(options?: AccountBalancesOptions): Promise<AccountBalances> {
    const response = await this._http.get<AccountBalancesDto>(
      `/api/v1/accounts/balances?ownerId=${(options && options.ownerId) || ""}`
    );

    const nativeCurrency = await this._getCurrency(`${response.data.totalNativeBalance.currencyId}`);

    return AccountBalances.create(response.data, nativeCurrency, this._getCurrency);
  }

  /**
   * Delete an existing account
   * @param id the id of the account to delete
   */
  public async delete(id: string): Promise<void> {
    await this._http.delete(`/api/v1/accounts/${id}`);
  }

  /**
   * Return an empty account if it was not found or inactive
   */
  private unknownAccount(): Account {
    return new Account(
      {
        dto: {
          accountId: "Unknown id",
          currencyId: 1,
          depositAddress: "Unknown address",
          label: "Unknown CPS wallet",
          available: {
            value: "0",
            currencyId: "1"
          },
          balance: {
            value: "0",
            currencyId: "1"
          },
          nativeAvailable: {
            value: "0",
            currencyId: "5057"
          },
          nativeBalance: {
            value: "0",
            currencyId: "5057"
          },
          isActive: true,
          isLocked: false,
          canCreateAddresses: false
        }
      },
      <any>null,
      <any>null,
      <any>null,
      <any>null,
      <any>null,
      <any>null
    );
  }

  /**
   * @hidden
   */
  private readonly _factory: EntityFactory<AccountDto, Account>;
}
