import { DtoRef } from "../common";
import {
  TransactionDto,
  TransactionFlags,
  TransactionHistoryEventsDto,
  WalletTransactionType
} from "../dtos";
import { FullAmount } from "../fullAmount";
import { HttpClient } from "../http";
import { TransactionEndpoint } from "./";

/**
 * Represents a single history event for a transaction
 */
export interface TransactionHistoryEvent {
  /**
   * the timestamp of when the event was observed
   */
  timestamp: string;

  /**
   * the type of event that was observed
   */
  eventType: "Created" | "SeenOnBlockchain" | "ConfirmedOnBlockchain" | "Commented";
}

/**
 * Transactions represent an event on one or more accounts (e.g. debit, withdrawal, transfer or conversion)
 */
export class Transaction {
  /**
   * the unique id of the transaction on the CoinPayments platform.
   * @note this is not the same as a blockchain transaction id as the transaction may have been performed off chain, for example an internal transfer between accounts.
   */
  get id() {
    return this._model.dto.id;
  }

  /**
   * the state of the transaction ("created", "pending", or "complete")
   */
  get state() {
    return this._model.dto.state;
  }

  /**
   * timestamp when the transaction was created
   */
  get created() {
    return this._model.dto.created;
  }

  /**
   * timestamp when the transaction was completed/confirmed
   */
  get completed(): string | undefined {
    return this._model.dto.completed;
  }

  /**
   * the user's custom memo attached to the transaction
   */
  get memo(): string {
    return this._changedMemo || this._model.dto.memo || "";
  }

  /**
   * the type of transaction (conversion, internal, external, etc.)
   */
  get transactionType(): WalletTransactionType {
    return this._model.dto.transactionType;
  }

  /**
   * the id of conversion used to tie 'sent' and 'received' transactions
   */
  get conversionId(): number | undefined {
    return this._model.dto.conversionId;
  }

  /**
   * contract address for token transfers of account-based currencies like ERC20 for ETH
   */
  get contractAddress(): string | undefined {
    return this._model.dto.contractAddress;
  }

  /**
   * "receiver" contract address for token transfers of account-based currencies like ERC20 for ETH
   */
  get toContractAddress(): string | undefined {
    return this._model.dto.toContractAddress;
  }

  /**
   * Block number the transaction appeared at
   */
  get blockNumberTxAppearedAt(): number | undefined {
    return this._model.dto.blockNumberTxAppearedAt;
  }

  /**
   * Transaction flags
   */
  get flags(): TransactionFlags {
    return this._model.dto.flags;
  }

  /**
   * Number of confirmations
   */
  get confirmationNumber(): number | undefined {
    return this._model.dto.confirmationNumber;
  }
  /**
   * Changes the custom memo to attached to the transaction, this will only be visible on the CoinPayments platform.
   * @param newMemo the new memo to set for the transaction
   * @returns `Promise<void>` which fulfills when the memo has been updated
   */
  public async changeMemo(newMemo: string): Promise<void> {
    if (!newMemo) {
      throw new Error("Invalid memo");
    }

    await this._http.put(`/api/v1/transactions/${this.id}/memo`, {
      memo: newMemo
    });

    this._changedMemo = newMemo;
  }

  /**
   * get a list of this transaction's history
   */
  public async listHistory(): Promise<TransactionHistoryEvent[]> {
    const response = await this._http.get<TransactionHistoryEventsDto>(
      `/api/v1/transactions/${this.id}/events`
    );

    const historyEvents: TransactionHistoryEvent[] = response.data.items.map(item => {
      return {
        timestamp: item.timestamp,
        eventType: item.eventType
      };
    });

    return historyEvents;
  }

  /** @hidden */
  constructor(
    /** @hidden */ private readonly _model: DtoRef<TransactionDto>,
    /** @hidden */ private readonly _http: HttpClient,
    /** the input for the transaction, origin of where the funds came from */
    public readonly from: TransactionEndpoint,
    /** the output for the transaction, receiver of the funds, where funds were sent */
    public readonly to: TransactionEndpoint,
    /** @hidden */ public readonly coinPaymentsFee?: FullAmount,
    /** @hidden */ public readonly blockchainFee?: FullAmount
  ) {}

  /** @hidden */
  private _changedMemo?: string;
}
