export interface HttpResponseHeaders {
  get(name: string): string | null;
  has(name: string): boolean;
  forEach(callbackfn: (value: string, key: string, parent: HttpResponseHeaders) => void): void;
}

/** @hidden response returned by the server */
export interface HttpResponse<T> {
  readonly headers: HttpResponseHeaders;
  readonly data: T;
  readonly status: number;
  readonly statusText: string;
  readonly url: string;
}

/** @hidden configuration options for requests to be sent to the server */
export interface RequestConfig {
  /** whether to omit the authorzation header, if included by default */
  omitAuthorization?: boolean;

  /** url the request will be sent to */
  url?: string;

  /** the request method to be used */
  method?: "GET" | "POST" | "DELETE" | "PUT";

  /** the type of response expected from the server (default = json) */
  responseType?: "json" | "text" | "arraybuffer" | "text/csv";

  /** custom headers to be sent */
  headers?: { [key: string]: string };

  /** the data to be sent as the request body (PUT and POST requests) */
  body?: any;

  /** specifies whether the body should be form url encoded */
  bodyShouldBeFormUrlEncoded?: boolean;

  /** defines whether to resolve or reject the promise for a given HTTP response */
  validateStatus?: (status: number) => boolean;
}

/** @hidden Provides functionality for sending HTTP requests and receiving HTTP responses from a resource identified by a URI. */
export interface HttpClient {
  send<T>(config: RequestConfig): Promise<HttpResponse<T>>;
  get<T = any>(url: string, config?: RequestConfig): Promise<HttpResponse<T>>;
  put<T = any>(url: string, data?: any, config?: RequestConfig): Promise<HttpResponse<T>>;
  post<T = any>(url: string, data?: any, config?: RequestConfig): Promise<HttpResponse<T>>;
  delete<T = any>(url: string, config?: RequestConfig): Promise<HttpResponse<T>>;
}

/**
 * HTTP client which internally uses the `fetch` api and adds a custom Bearer `Authorization` token
 * @hidden
 */
export class FetchHttpClient implements HttpClient {
  private readonly defaultRequestConfig: RequestConfig = {
    responseType: "json",
    validateStatus: status => status >= 200 && status < 300
  };

  constructor(public readonly baseUrl: string) {}

  public addDefaultHeader(name: string, valueOrFactory: string | (() => string) | (() => Promise<string>)) {
    if (!name || !valueOrFactory) {
      throw new Error("argument null");
    }

    const nameUpper = name.toUpperCase();
    this._defaultHeaders = this._defaultHeaders.filter(i => i.name.toUpperCase() !== nameUpper); // if exists, remove it

    const entry = {
      name,
      valueFactory:
        typeof valueOrFactory === "string"
          ? () => Promise.resolve(valueOrFactory)
          : () => {
              const result = valueOrFactory();
              if (typeof result === "string") {
                return Promise.resolve(result);
              }
              return result;
            }
    };

    this._defaultHeaders.push(entry);
  }

  public async send<T>(config: RequestConfig): Promise<HttpResponse<T>> {
    const absoluteUrl =
      config.url && (config.url.startsWith("http://") || config.url.startsWith("https://"))
        ? config.url
        : this.baseUrl + (config.url || "");

    if (!absoluteUrl) {
      throw new Error("url is required");
    }

    const headers: { [key: string]: string } = {};
    for (const defaultHeader of this._defaultHeaders) {
      if (config.omitAuthorization && defaultHeader.name === "Authorization") {
        continue;
      }
      // use the config headers if provided instead of the existing one
      headers[defaultHeader.name] =
        config.headers && config.headers.hasOwnProperty(defaultHeader.name)
          ? config.headers[defaultHeader.name]
          : await defaultHeader.valueFactory();
    }

    const mergedConfig = { ...this.defaultRequestConfig, ...config };
    const request = this.createRequestInit(headers, mergedConfig);
    const response = await window.fetch(absoluteUrl, request);

    if (mergedConfig.validateStatus && !mergedConfig.validateStatus(response.status)) {
      throw new Error(`Status ${response.status} (${response.statusText}) does not indicate success`);
    }

    const contentType = response.headers.get("content-type");
    const hasContent = response.status !== 204 && !!contentType;

    let data: T | undefined;

    if (hasContent) {
      switch (mergedConfig.responseType) {
        case "json":
          if (contentType && contentType.includes("application/json")) {
            data = (await response.json()) as T;
            break;
          } else if (contentType && contentType.includes("text/csv")) {
            data = (await response.blob()) as any;
            break;
          }
          throw new Error(`Expected 'json' response but got content-type : '${contentType}'`);
        case "text":
          data = (await response.text()) as any;
          break;
        default:
          throw new Error("Not implemented");
      }
    }

    const responseHeaders: HttpResponseHeaders = {
      get: (name: string) => {
        return response.headers.get(name);
      },
      has: (name: string) => {
        return response.headers.has(name);
      },
      forEach: (callbackfn: (value: string, key: string, parent: HttpResponseHeaders) => void) => {
        response.headers.forEach((v, k, p) => {
          callbackfn(v, k, responseHeaders);
        });
      }
    };

    return {
      headers: responseHeaders,
      data: data as any, // better way to handle 'No Content' ?
      status: response.status,
      statusText: response.statusText,
      url: response.url
    };
  }

  public get<T>(url: string, config?: RequestConfig): Promise<HttpResponse<T>> {
    return this.send({
      method: "GET",
      url,
      ...(config || {})
    });
  }

  public put<T>(url: string, data?: any, config?: RequestConfig): Promise<HttpResponse<T>> {
    return this.send({
      body: data,
      method: "PUT",
      url,
      ...(config || {})
    });
  }

  public post<T>(url: string, data?: any, config?: RequestConfig): Promise<HttpResponse<T>> {
    return this.send({
      body: data,
      method: "POST",
      url,
      ...(config || {})
    });
  }

  public delete<T>(url: string, config?: RequestConfig): Promise<HttpResponse<T>> {
    return this.send({
      method: "DELETE",
      url,
      ...(config || {})
    });
  }

  private createRequestInit(headers: { [key: string]: string }, config: RequestConfig): RequestInit {
    const mergedHeaders = new Headers({
      ...(headers || {}),
      ...(config.headers || {})
    });
    const request: RequestInit = {
      cache: "no-cache",
      credentials: "same-origin",
      headers: mergedHeaders,
      method: config.method,
      mode: "cors",
      referrer: "no-referrer"
    };
    if (config.body) {
      if (config.body instanceof ArrayBuffer || config.body.buffer instanceof ArrayBuffer) {
        request.body = config.body;
        mergedHeaders.set("Content-Type", "application/octet-stream");
      } else if (config.bodyShouldBeFormUrlEncoded) {
        const queryString = Object.keys(config.body)
          .map(key => `${key}=${config.body[key]}`)
          .join("&");

        request.body = queryString;
        mergedHeaders.set("Content-Type", "application/x-www-form-urlencoded");
      } else {
        request.body = JSON.stringify(config.body);
        mergedHeaders.set("Content-Type", "application/json");
      }
    }
    return request;
  }

  private _defaultHeaders: {
    name: string;
    valueFactory: () => PromiseLike<string>;
  }[] = [];
}
