import { AcceptedCurrencyConfigOptions } from ".";
import { DtoRef } from "../common";
import {
  AcceptedCurrencyDto,
  CurrencyDto,
  MerchantPaymentCurrencyPayoutFrequency,
  PayoutConfigDto
} from "../dtos";
import { HttpClient } from "../http";

/** Represents the currencies that a merchant accepts for payments */
export class AcceptedCurrency implements AcceptedCurrencyDto {
  get currency(): CurrencyDto {
    return this._model.dto.currency;
  }
  get payoutFrequency(): MerchantPaymentCurrencyPayoutFrequency {
    return this._model.dto.payoutFrequency;
  }
  get order(): number {
    return this._model.dto.order;
  }
  get payoutConfigurations(): PayoutConfigDto[] {
    return this._model.dto.payoutConfigurations;
  }
  /** the discount to give buyers when paying in this currency, maybe as a promotional tool */
  get discountPercent(): number {
    return this._model.dto.discountPercent;
  }
  /** the markup to charge buyers when paying in this currency, maybe to cover local crypto/fiat conversion costs */
  get markupPercent(): number {
    return this._model.dto.markupPercent;
  }
  /** @hidden */
  constructor(
    /** @hidden */
    private readonly _model: DtoRef<AcceptedCurrencyDto>,
    /** @hidden */
    private readonly _http: HttpClient
  ) {}

  /**
   * Updates a currency that a merchant wishes to receive as payments from Buyers
   * @param currencyConfig the frequency and payout configurations for the payment currency
   */
  public async update(currencyConfig: Partial<AcceptedCurrencyConfigOptions>): Promise<void> {
    await this._http.put(`/api/v1/merchant/currencies/${this.currency.id}`, currencyConfig);
  }

  /**
   * Update the order (ranking) of an accepted coin. Will update the rest of the payment currencies so that they can be returned in the correct order.
   * To unassign the rank for a currency, pass in 0 for the order.
   *
   * Example:
   * A merchant has coins in this rank:
   * 1. Bitcoin
   * 2. Litecoin
   * 3. Ethereum
   *
   * Scenario 1: Merchant would like to update Ethereum to rank 1. The new ranks will be:
   * 1. Ethereum
   * 2. Bitcoin
   * 3. Litecoin
   *
   * Scenario 2: Merchant would like to unassign the rank for Bitcoin. The new ranks will be:
   * 1. Litecoin
   * 2. Ethereum
   * 0. Bitcoin
   * @param order the preferred order of acceptance for the currency. The lower the number, the higher the preference
   */
  public async updateOrder(order: number): Promise<void> {
    const request = {
      order
    };

    await this._http.put(`/api/v1/merchant/currencies/${this.currency.id}/order`, request);
  }
}
