import { EntityFactory } from "../common";
import { Currency } from "../currencies";
import { AffiliateChannelDto, NewAffiliateChannelDto, PagedItemsDto } from "../dtos";
import { HttpClient } from "../http";
import { PagedItems } from "../paging";
import { AffiliateChannel } from "./channel";

/**
 * Options accepted by the [[AffiliateChannels.list]] method
 */
export interface AffiliateChannelsListOptions {
  /** cursor that points to the end of the page of data that has been returned, see [[PagedItemsDto.paging.cursors.after]] */
  after?: string;
  /** the maximum number of objects that may be returned, the query may return fewer results than the requested maximum. If `after` is specified then `limit` specifies the number of items to return starting from `after`.  If not specified then `limit` specifies the number of items to return from the beginning. */
  limit?: number;
  /** search query for channels with a similar name */
  q?: string;
}

/** Affiliate Channels on the CoinPayments platform */
export class AffiliateChannels {
  /**
   * @hidden
   */
  private readonly _factory: EntityFactory<AffiliateChannelDto, AffiliateChannel>;

  /** @hidden */
  constructor(
    /** @hidden */ private readonly _http: HttpClient,
    /** @hidden */ _getCurrency: (id: string) => Promise<Currency>
  ) {
    this._factory = new EntityFactory(
      dto => dto.id,
      async entityDto => {
        const currency = await _getCurrency(`${entityDto.dto.totalEarnings.currencyId}`);
        return new AffiliateChannel(entityDto, _http, currency);
      }
    );
  }

  /**
   * Creates a new channel
   * @param data the name and configuration of the new channel
   * @returns `Promise<AffiliateChannel>` that fulfills with the newly created channel if successful
   */
  public async create(data: NewAffiliateChannelDto): Promise<AffiliateChannel> {
    const response = await this._http.post<AffiliateChannelDto>("/api/v1/affiliate/channels", data);
    return this._factory.getOrCreateEntity(response.data);
  }

  /**
   * Gets a page of [[AffiliateChannel]]'s belonging to the current user affiliate
   * @param options optional object specifying the options (e.g. paging, sorting, etc) for the list query
   * @returns `Promise<PagedItems<AffiliateChannelDto, AffiliateChannel>>` that fulfills with a page of [[AffiliateChannel]]'s
   */
  public async list(
    options?: AffiliateChannelsListOptions
  ): Promise<PagedItems<AffiliateChannelDto, AffiliateChannel>> {
    options = options || {};

    const limit = options.limit ? options.limit : 100;

    const response = await this._http.get<PagedItemsDto<AffiliateChannelDto>>(
      `/api/v1/affiliate/channels?q=${options.q || ""}&after=${options.after || ""}&limit=${limit || ""}`
    );
    const data = response.data;
    return PagedItems.createAsync(data, dto => this._factory.getOrCreateEntity(dto), this._http);
  }

  /**
   * Finds a channel by its id
   * @param id the id of the channel to find
   * @returns `Promise<AffiliateChannel>` that fulfills with the [[AffiliateChannel]] or null if the channel was not found or inaccessible
   */
  public async find(id: string): Promise<AffiliateChannel | null> {
    const existing = this._factory.getEntityOrDefault(id);
    if (existing) {
      return existing;
    }

    const response = await this._http.get<AffiliateChannelDto>(`/api/v1/affiliate/channels/${id}`, {
      validateStatus: _ => true // validated below for 404 null
    });

    if (response.status !== 200) {
      return null;
    }

    return this._factory.getOrCreateEntity(response.data);
  }
}
