import { DtoRef } from "../common";
import { TopUpProviderCurrencyDto, TopUpProviderDto, TopUpProviderName } from "../dtos";
import { HttpClient } from "../http";

/**
 * Represents the base class of all top up providers and contains fields all providers should have
 */
export class TopUpProviderBase {
  /** the name of the provider */
  get name(): TopUpProviderName {
    return this._model.dto.name;
  }
  /** the url to the logo of the provider */
  get logoUrl(): string {
    return this._model.dto.logoUrl;
  }
  /** the currencies that can be purchased through this provider */
  get currenciesCanPurchase(): TopUpProviderCurrencyDto[] {
    return this._model.dto.currenciesCanPurchase;
  }

  /** @hidden */
  constructor(
    /** @hidden */ protected readonly _http: HttpClient,
    /** @hidden */ protected readonly _model: DtoRef<TopUpProviderDto>,
    /** @hidden */ protected readonly _isUserSignedIn: () => Promise<boolean>
  ) {}
}
