import { EntityFactory } from "../common";
import { SmartContractDto } from "../dtos";
import { HttpClient } from "../http";
import { SmartContract } from "./smartcontract";

/** Provides access to the smart contracts hosted on the CoinPayments platform */
export class SmartContracts {
  /** @hidden */
  constructor(
    /** @hidden */
    private readonly _http: HttpClient
  ) {
    this._factory = new EntityFactory(
      dto => dto.contractAddress,
      async entityDto => {
        return new SmartContract(entityDto);
      }
    );
  }

  /**
   * lists smart contracts by currency id.
   * @param currencyId the id of currency for which we want to get smart contracts (e.g. 4 for ETH)
   * @returns `Promise<SmartContractDto, SmartContract>` that fulfills with the [[SmartContract]]
   */
  // TODO: Create method to list all smart contracts by currencyId
  // public async list(currencyId: number): Promise<SmartContract[]> {
  //   const response = await this._http.get<SmartContractDto[]>(`api/v1/smartcontracts/${currencyId}`);
  // }

  /**
   * finds a smart contract by currency id and its address
   * @param currencyId the id of the currency for which we want to get smart contract (e.g. 4 for ETH)
   * @param contractAddress the address of the smart contract to retrieve
   * @returns `Promise<SmartContractDto>` that fulfills with the [[SmartContract]] matching the specified id or null if not found
   */
  public async find(currencyId: string, contractAddress: string): Promise<SmartContract | null> {
    const existing = this._factory.getEntityOrDefault(contractAddress);
    if (existing) {
      return existing;
    }

    const response = await this._http.get<SmartContractDto>(
      `/api/v1/smartcontracts/${currencyId}/${contractAddress}`
    );

    if (response.status === 404) {
      return null;
    }

    if (response.status < 200 || response.status >= 300) {
      throw new Error(`Status ${response.status} (${response.statusText}) does not indicated success`);
    }

    return this._factory.getOrCreateEntity(response.data);
  }

  /**
   * @hidden
   */
  private readonly _factory: EntityFactory<SmartContractDto, SmartContract>;
}
