import { Account } from "../accounts";
import { Currency } from "../currencies";
import { InvoiceHistoryEventDto, InvoicePaymentDetailsDto, PagedItemsDto } from "../dtos";
import { HttpClient } from "../http";
import { InvoicePaymentDetails } from "./invoicePaymentDetails";

export abstract class InvoiceBase {
  /**
   * Get payment details for an invoice with payout details, including if invoice has been fully paid out, the exact amount they will receive
   * and in what currency, which address payout will be deposited, and who (Buyer) performed the payment.
   */
  protected async listPayouts(
    client: HttpClient,
    invoiceId: string,
    _getCurrency: (id: string) => Promise<Currency>,
    _getAccount: (id: string) => Promise<Account>
  ): Promise<InvoicePaymentDetails | null> {
    const response = await client.get<InvoicePaymentDetailsDto>(
      `/api/v1/merchant/invoices/${invoiceId}/payouts`
    );

    if (response.data.items.length === 0) {
      return null;
    }

    return InvoicePaymentDetails.create(response.data, _getCurrency, _getAccount);
  }

  /**
   * Gets the history events of an invoice
   */
  protected async listHistory(
    client: HttpClient,
    invoiceId: string
  ): Promise<PagedItemsDto<InvoiceHistoryEventDto>> {
    const response = await client.get<PagedItemsDto<InvoiceHistoryEventDto>>(
      `/api/v1/merchant/invoices/${invoiceId}/history`
    );
    return response.data;
  }

  /**
   * Cancels the invoice
   */
  protected async cancel(client: HttpClient, invoiceId: string): Promise<void> {
    await client.post(`/api/v1/merchant/invoices/${invoiceId}/cancel`);
  }

  /**
   * Deletes an invoice (only works for invoices in draft or scheduled state)
   */
  protected async delete(client: HttpClient, invoiceId: string): Promise<void> {
    await client.delete(`/api/v1/merchant/invoices/${invoiceId}/delete`);
  }
}
