import { Account } from "../accounts";
import { DtoRef } from "../common";
import { Currency } from "../currencies";
import { TransactionAccountInputOutputDto } from "../dtos";
import { Money } from "../money";
import { TransactionEndpoint } from "./";

/**
 * Defines an [[Account]] as an input or output of the [[Transaction]].  The transaction was sent from the [[TransactionAccountEndpoint.account]] or received into it.
 */
export class TransactionAccountEndpoint implements TransactionEndpoint {
  /** the type of the endpoint, returns `account` */
  public readonly type = "account";

  /** the account from which funds were sent or into which funds were received */
  public readonly account: Account;

  /**
   * the amount of money in the original currency on this side of the transaction
   */
  public readonly amount: Money;

  /**
   * the amount of money in the native currency of the user, on this side of the transaction.
   * the conversion rate is locked to that at the time the transaction was created.
   */
  public readonly nativeAmount: Money;

  /** @hidden */
  constructor(
    /** @hidden */ model: DtoRef<TransactionAccountInputOutputDto>,
    /** @hidden */ currency: Currency,
    /** @hidden */ nativeCurrency: Currency,
    /** @hidden */ account: Account
  ) {
    this.amount = Money.create(model.dto.amount, currency);
    this.nativeAmount = Money.create(model.dto.nativeAmount, nativeCurrency);
    this.account = account;
  }
}
