/**
 * @hidden
 */
export interface DtoRef<TDto> {
  readonly dto: TDto;
}

/**
 * @hidden
 */
export class EntityFactory<TDto, TEntity> {
  constructor(
    private readonly getKey: (dto: TDto) => string,
    private readonly createEntity: (dto: DtoRef<TDto>) => Promise<TEntity>
  ) {}

  /**
   * Gets entity from cache if it exists or creates a new entity
   * @param dto The dto to create a new entity from
   */
  public async getOrCreateEntity(dto: TDto): Promise<TEntity> {
    const key = this.getKey(dto);
    const existing = this.getEntityOrDefault(key);
    if (existing) {
      this._entities[key].entityDto.dto = dto;
      return this._entities[key].entity;
    }

    const entityDto: DtoRef<TDto> = {
      dto
    };
    const created = await this.createEntity(entityDto);
    this._entities[key] = {
      entity: created,
      entityDto
    };
    return created;
  }

  public getEntityOrDefault(key: string): TEntity | undefined {
    if (this._entities.hasOwnProperty(key)) {
      return this._entities[key].entity;
    }
    return undefined;
  }

  public getDtoOrDefault(key: string): TDto | undefined {
    if (this._entities.hasOwnProperty(key)) {
      return this._entities[key].entityDto.dto;
    }
    return undefined;
  }

  public removeEntity(key: string) {
    if (this._entities.hasOwnProperty(key)) {
      delete this._entities[key];
    }
  }

  private readonly _entities: { [key: string]: { entity: TEntity; entityDto: { dto: TDto } } } = {};
}

/**
 * @hidden
 */
export class UrlBuilder {
  constructor(private readonly _baseUrl: string) {}

  /**
   * adds the specified query parameter if the `value` is not `undefined` or `null`
   * @param key the query parameter name
   * @param value the query parameter value
   * @returns `UrlBuilder` the current builder instance
   */
  public addQueryIfValue(key: string, value: string | undefined | null): UrlBuilder {
    if (value) {
      if (!this._queryParams.hasOwnProperty(key)) {
        this._queryParams[key] = [];
        this._queryParamKeys.push(key);
      }
      this._queryParams[key].push(value);
    }
    return this;
  }

  public toString(): string {
    let result = this._baseUrl;
    if (this._queryParamKeys.length > 0) {
      const query = this._queryParamKeys.map(key => `${key}=${this._queryParams[key].join(",")}`).join("&");
      result += `?${query}`;
    }
    return result;
  }

  private _queryParamKeys: string[] = [];
  private _queryParams: { [key: string]: string[] } = {};
}

/**
 * Validates that the specified @see params object contains the @see requiredParams
 * @hidden
 * @param params the parameters object to validate
 * @param requiredParams the names of the required parameters to check for existance
 */
export function validateArgs<T, K extends keyof T>(params: T, requiredParams: K[]): boolean {
  if (!params) {
    throw new Error("missing parameters dictionary");
  }

  requiredParams.forEach(expectedParamName => {
    const param = params[expectedParamName];
    if (typeof param === "undefined" || param === null) {
      throw new Error(`missing parameter '${expectedParamName}'`);
    }
  });

  return true;
}
