import { DtoRef, EntityFactory, UrlBuilder } from "../common";
import {
  CreateMerchantClientWebhookResponseDto,
  MerchantClientDto,
  MerchantClientPublicState,
  MerchantClientWebhookEndpointDto,
  MerchantClientWebhookNotification,
  PagedItemsDto,
  RegenerateSecretResponseDto
} from "../dtos";
import { HttpClient } from "../http";
import { PagedItems } from "../paging";
import { Webhook } from "./webhook";

/**
 * Options accepted by the [[update]] method
 */
export interface UpdateMerchantClientOptions {
  /** The name of the client provided by the merchant */
  name?: string;
  /** The state of the client to update to */
  state?: MerchantClientPublicState;
  /** The store url of the client */
  storeUrl?: string;
}

/**
 * Options accepted by the [[listWebhooks]] method
 */
export interface ListMerchantClientWebhooksOptions {
  /** The notification types for a webhook */
  type?: MerchantClientWebhookNotification;
  /**
   * the maximum number of objects that may be returned, the query may return fewer results than
   * the requested maximum. If `after` is specified then `limit` specifies the number of items
   * to return starting from `after`.  If not specified then `limit` specifies the number of
   * items to return from the beginning.
   */
  limit?: number;
}

/**
 * Options accepted by the [[createWebhook]] method and [[Webhook.updateWebhook]] method
 */
export interface MerchantClientWebhookOptions {
  /** the url to which to POST webhook notifications to  */
  notificationsUrl: string;
  /** the types of notifications to send to this endpoint */
  notifications: MerchantClientWebhookNotification[];
}

/** Represents a merchant's clients (to communicate with the API) and their webhooks' creation, updates, and deletion. */
export class MerchantClient implements MerchantClientDto {
  /** the unique id for the client app that the merchant will use to communicate with CoinPayments REST API */
  get clientId(): string {
    return this._model.dto.clientId;
  }
  /** the name of the integration for which this client was created */
  get integration(): string | undefined {
    return this._model.dto.integration;
  }
  /** the name of the client app on the merchant dashboard */
  get name(): string {
    return this._model.dto.name;
  }
  /** the state of the client */
  get state(): MerchantClientPublicState {
    return this._model.dto.state;
  }
  /** the store url of the client */
  get storeUrl(): string {
    return this._model.dto.storeUrl;
  }

  /** @hidden */
  constructor(
    /** @hidden */
    private readonly _model: DtoRef<MerchantClientDto>,
    /** @hidden */
    private readonly _http: HttpClient
  ) {
    this._factory = new EntityFactory(
      dto => dto.id,
      async entityDto => {
        return new Webhook(entityDto, _http, this.clientId);
      }
    );
  }

  /**
   * Regenerates the API secret for the current client, the previous secret can no longer be used
   * to authenticate with the API
   */
  public async regenerateSecret(): Promise<string> {
    const response = await this._http.post(`/api/v1/merchant/clients/${this.clientId}/secret`);
    return (response.data as RegenerateSecretResponseDto).clientSecret;
  }

  /**
   * Update an existing client belonging to the merchant
   * @param options Info used to update the client (name and state)
   */
  public async update(options: UpdateMerchantClientOptions): Promise<void> {
    await this._http.put(`/api/v1/merchant/clients/${this.clientId}`, options);
  }

  /**
   * List all of the webhooks for a particular client
   * @param options Optional fields to filter the list by
   */
  public async listWebhooks(
    options?: ListMerchantClientWebhooksOptions
  ): Promise<PagedItems<MerchantClientWebhookEndpointDto, Webhook>> {
    const baseUrl = new UrlBuilder(`/api/v1/merchant/clients/${this.clientId}/webhooks`);

    if (options) {
      baseUrl
        .addQueryIfValue("type", options.type)
        .addQueryIfValue("limit", options.limit ? options.limit.toString() : "100");
    }

    const url = baseUrl.toString();
    const response = await this._http.get<PagedItemsDto<MerchantClientWebhookEndpointDto>>(url);
    const data = response.data;
    return PagedItems.createAsync(data, dto => this._factory.getOrCreateEntity(dto), this._http);
  }

  /**
   * Create a new webhook for a client
   * @param options Info used to create the webhook
   */
  public async createWebhook(
    options: MerchantClientWebhookOptions
  ): Promise<CreateMerchantClientWebhookResponseDto> {
    const response = await this._http.post<CreateMerchantClientWebhookResponseDto>(
      `/api/v1/merchant/clients/${this.clientId}/webhooks`,
      options
    );
    return response.data;
  }

  /**
   * Delete a webhook for a client
   * @param webhookId The id of the notifications endpoint
   */
  public async deleteWebhook(webhookId: string): Promise<void> {
    await this._http.delete(`/api/v1/merchant/clients/${this.clientId}/webhooks/${webhookId}`);
  }

  /**
   * @hidden
   */
  private readonly _factory: EntityFactory<MerchantClientWebhookEndpointDto, Webhook>;
}

export class NewMerchantClient extends MerchantClient {
  /** the secret for the app, used to secure communicates with the CoinPayments REST API */
  get clientSecret(): string {
    return this._newModel.dto.clientSecret!;
  }

  /** @hidden */
  constructor(
    /** @hidden */
    private readonly _newModel: DtoRef<MerchantClientDto>,
    /** @hidden */
    _http: HttpClient
  ) {
    super(_newModel, _http);
  }
}
