import { DtoRef } from "../common";
import { Currency } from "../currencies";
import { TopUpOrderDto, TopUpOrderStatus } from "../dtos";
import { Money } from "../money";

/** Represents a single top up of the user */
export class TopUpOrder {
  /** The ID of the top up order */
  get id(): string {
    return this._model.dto.id;
  }
  /** The blockchain transaction ID (hash) */
  get blockchainTxId(): string | undefined {
    return this._model.dto.blockchainTxId;
  }
  /** Time when the top up order was created */
  get created(): string {
    return this._model.dto.created;
  }
  /** The status of the order */
  get status(): TopUpOrderStatus {
    return this._model.dto.status;
  }

  /** The amount received or will be received in the purchased crypto */
  public readonly coinReceived: Money;

  /** The amount used to purchase crypto */
  public readonly purchaseAmount: Money;

  /** @hidden */
  constructor(
    /** @hidden */ private readonly _model: DtoRef<TopUpOrderDto>,
    /** @hidden */ purchaseCurrency: Currency,
    /** @hidden */ receivedCurrency: Currency
  ) {
    this.purchaseAmount = Money.create(() => _model.dto.purchaseAmount, purchaseCurrency);
    this.coinReceived = Money.create(() => _model.dto.coinReceived, receivedCurrency);
  }
}
