import { Currency } from "../currencies";
import { InvoicePaymentTransactionDto } from "../dtos";
import { Money } from "../money";

export class InvoicePaymentTransaction {
  /** the id of transaction the payment was sent to external address ("To External" settlement mode) */
  get txId(): string | undefined {
    return this._model.hash;
  }

  /** the transaction amount */
  public readonly amount: Money;

  /** @hidden */
  constructor(
    /** @hidden */
    private readonly _model: InvoicePaymentTransactionDto,
    /** @hidden */
    public readonly _currency: Currency
  ) {
    this.amount = Money.create(_model.amount, _currency);
  }
}
