import { DtoRef } from "../common";
import { MercuryoWidgetDto, TopUpProviderDto } from "../dtos";
import { HttpClient, HttpResponse } from "../http";
import { getQueryParamByName } from "../utils/queryStringUtils";
import { TopUpProviderBase } from "./topUpProviderBase";
import { TopUpProvider } from "./topUpProviders";

/**
 * The format of the address map accepted by Mercuryo
 */
interface AddressMap {
  [index: string]: string;
}

/** Represents the Mercuryo top up provider */
export class Mercuryo extends TopUpProviderBase implements TopUpProvider {
  /**
   * Indicates if widget was successfully loaded by the show() method
   */
  public widgetDidNotLoad: boolean = false;

  /** @hidden */
  constructor(
    /** @hidden */ _http: HttpClient,
    /** @hidden */ _model: DtoRef<TopUpProviderDto>,
    /** @hidden */ _isUserSignedIn: () => Promise<boolean>
  ) {
    super(_http, _model, _isUserSignedIn);
  }

  /**
   * Shows the widget in the specified div
   * @param divId The id of the div in which to show the widget
   * @param defaultCryptoSymbol The default crypto that should be selected for purchase dropdown in the mercuryo widget
   */
  public async show(divId: string, defaultCryptoSymbol?: string): Promise<void> {
    try {
      if (!document.getElementById("mercuryo-script")) {
        // create new script if it doesn't exist already
        const script = document.createElement("script");
        script.id = "mercuryo-script";
        script.src = "https://widget.mercuryo.io/embed.2.0.js";
        script.type = "text/javascript";
        script.onerror = () => (this.widgetDidNotLoad = true);
        script.onload = () => this.onLoadMercuryoScript(divId, defaultCryptoSymbol);
        document.body.appendChild(script);
      } else {
        await this.onLoadMercuryoScript(divId, defaultCryptoSymbol);
      }
    } catch (e) {
      this.widgetDidNotLoad = true;
    }
  }

  private onLoadMercuryoScript(divId: string, defaultCryptoSymbol?: string): Promise<void> {
    const url = "/api/v1/topups/providers/mercuryo/config";
    const populateWidgetParams = (response: HttpResponse<MercuryoWidgetDto>) => {
      if (defaultCryptoSymbol) {
        const index = this.currenciesCanPurchase.findIndex(
          i => i.symbol === defaultCryptoSymbol!.toUpperCase()
        );
        if (index < 0) {
          // set symbol to undefined since the provided symbol is not valid for purchase with mercuryo
          defaultCryptoSymbol = undefined;
        }
      }

      const widgetData = response.data;
      const addrMap: AddressMap = {};
      const cryptoSymbols: string[] = [];
      for (const address of widgetData.addresses) {
        addrMap[address.currencySymbol] = address.depositAddress;
        cryptoSymbols.push(address.currencySymbol);
      }
      const widgetParams = {
        widgetId: widgetData.widgetId,
        host: document.getElementById(divId),
        type: widgetData.type,
        theme: "ch",
        currency: defaultCryptoSymbol, // cryptocurrency ticker
        currencies: cryptoSymbols, // array of crypto currencies that can be purchased
        addressMap: addrMap, // the user's addresses in the currencies available for purchase
        hideAddress: true,
        merchantTransactionId: widgetData.merchantTransactionId,
        shareToken: widgetData.shareToken
      };

      (window as any).mercuryoWidget.run(widgetParams);
    };

    return this._isUserSignedIn()
      .then(isUserSignedIn => {
        if (isUserSignedIn) {
          this._http
            .get<MercuryoWidgetDto>(url)
            .then(response => {
              populateWidgetParams(response);
            })
            .catch(reason => {
              this.widgetDidNotLoad = true;
            });
        } else {
          // if token provided then attempt to fetch the config with the provided token
          const token = getQueryParamByName("token");
          const timestamp = getQueryParamByName("timestamp");
          const verification = getQueryParamByName("verification");

          if (!token || !timestamp || !verification) {
            this.widgetDidNotLoad = true;
          }

          this._http
            .get<MercuryoWidgetDto>(
              `${url}?token=${token}&timestamp=${timestamp}&verification=${verification}`,
              {
                omitAuthorization: true
              }
            )
            .then(response => {
              populateWidgetParams(response);
            })
            .catch(reason => {
              this.widgetDidNotLoad = true;
            });
        }
      })
      .catch(reason => {
        this.widgetDidNotLoad = true;
      });
  }
}
