import { DtoRef } from "../common";
import { Currency } from "../currencies";
import { TransactionBlockchainInputOutputDto } from "../dtos";
import { Money } from "../money";
import { TransactionEndpoint } from "./";

/**
 * Defines the blockchain as the origin or receiver of a [[Transaction]].
 */
export class TransactionBlockchainEndpoint implements TransactionEndpoint {
  /** the type of the endpoint, returns `blockchain` */
  public readonly type = "blockchain";

  /** the blockchain transaction id */
  get txid() {
    return this._model.dto.txid;
  }

  /** the address to which funds were sent to or received at */
  get address() {
    return this._model.dto.address;
  }

  /**
   * the amount of money in the original currency on this side of the transaction
   */
  public readonly amount: Money;

  /**
   * the amount of money in the native currency of the user, on this side of the transaction.
   * the conversion rate is locked to that at the time the transaction was created.
   */
  public readonly nativeAmount: Money;

  /** @hidden */
  constructor(
    /** @hidden */ private readonly _model: DtoRef<TransactionBlockchainInputOutputDto>,
    /** @hidden */ currency: Currency,
    /** @hidden */ nativeCurrency: Currency
  ) {
    this.amount = Money.create(this._model.dto.amount, currency);
    this.nativeAmount = Money.create(this._model.dto.nativeAmount, nativeCurrency);
  }
}
