import { Account } from "../accounts";
import { Currency } from "../currencies";
import { InvoicePaymentDetailsDto, SettlementModeErrorCodes } from "../dtos";
import { FullAmount } from "../fullAmount";
import { InvoicePaymentTransaction } from "./invoicePaymentTransaction";
import { InvoicePayoutDetails } from "./invoicePayoutDetails";

export class InvoicePaymentDetails {
  /** the id of transaction the payment was sent to external address ("To External" settlement mode) */
  get completedTxId(): string | undefined {
    return this._model.completedTxId;
  }

  /** the contract address of token the invoice was paid in */
  get expectedDisplayValue(): string {
    return this._model.expectedDisplayValue;
  }

  /** the contract address of token the invoice was paid in */
  get externalAddress(): string | undefined {
    return this._model.externalAddress;
  }

  /** the currency what the merchant receive funds in */
  get sourceCurrency(): Currency {
    return this._sourceCurrency;
  }

  /** the currency what the invoice was paid in */
  get destinationCurrency(): Currency {
    return this._destinationCurrency;
  }

  /** the amount merchant receive in conclusive currency (e.g. if conversion from BTC to LTC than amount will be in LTC) */
  get destinationAmount(): FullAmount | undefined {
    return this._destinationAmount;
  }

  /** would be funds converted */
  get isConversion(): boolean {
    return this._model.isConversion;
  }

  /** the merchant's account id where funds were sent if settelment mode is "To Balance" */
  get destinationAccount(): Account | undefined {
    return this._account;
  }

  /** payout details */
  get payout(): InvoicePayoutDetails {
    return this._payout;
  }

  /** the timestamp of when invoice was paid by buyer */
  get paid(): string {
    return this._model.paid || "";
  }

  /** error code when the funds were settled in "To Balance" mode instead of merchant choosed mode */
  get settlementModeErrorCode(): SettlementModeErrorCodes | null {
    return this._model.settlementModeErrorCode;
  }

  /** the contract address of token the invoice was paid in */
  public readonly paidTransactions: InvoicePaymentTransaction[];

  /** @hidden */
  constructor(
    /** @hidden */
    private readonly _model: InvoicePaymentDetailsDto,
    /** @hidden */
    private readonly _payout: InvoicePayoutDetails,
    /** @hidden */
    private readonly _sourceCurrency: Currency,
    /** @hidden */
    private readonly _destinationCurrency: Currency,
    /** @hidden */
    private readonly _destinationAmount?: FullAmount,
    /** @hidden */
    private readonly _account?: Account
  ) {
    this.paidTransactions = _model.paidTransactions.map(item => {
      return new InvoicePaymentTransaction(item, _sourceCurrency);
    });
  }

  public static async create(
    payoutDetailsDto: InvoicePaymentDetailsDto,
    _getCurrency: (id: string) => Promise<Currency>,
    _getAccount: (id: string) => Promise<Account>
  ): Promise<InvoicePaymentDetails | null> {
    const paymentDto = payoutDetailsDto;
    const payoutDto = payoutDetailsDto.items[0];

    if (!paymentDto.sourceCurrencyId) {
      return null;
    }

    const payoutCurrency = await _getCurrency(payoutDto.currency.id);
    const payoutNativeCurrency = await _getCurrency(payoutDto.payoutAmountInInvoiceCurrency.currencyId);
    const payout: InvoicePayoutDetails = new InvoicePayoutDetails(
      payoutDto,
      payoutCurrency,
      payoutNativeCurrency
    );

    const destinationAccount: Account | undefined = paymentDto.destinationAccountId
      ? await _getAccount(paymentDto.destinationAccountId)
      : undefined;

    const sourceCurrency = await _getCurrency(paymentDto.sourceCurrencyId);
    const destinationCurrency = await _getCurrency(paymentDto.destinationCurrencyId);
    const destinationAmount = paymentDto.destinationAmount
      ? await FullAmount.create(
          paymentDto.destinationAmount,
          destinationCurrency,
          payoutNativeCurrency,
          undefined
        )
      : undefined;
    return new InvoicePaymentDetails(
      payoutDetailsDto,
      payout,
      sourceCurrency,
      destinationCurrency,
      destinationAmount,
      destinationAccount
    );
  }
}
