import { EntityFactory } from "../common";
import { PagedItemsDto, UserNotificationDto } from "../dtos";
import { HttpClient } from "../http";
import { PagedItems } from "../paging";
import { UserNotification } from "./userNotification";

/** Represents active user dashboard notifications */
export class UserNotifications {
  /** @hidden */
  constructor(
    /** @hidden */
    private readonly _http: HttpClient
  ) {
    this._factory = new EntityFactory<UserNotificationDto, UserNotification>(
      dto => dto.id,
      async entityDto => {
        return new UserNotification(entityDto, _http);
      }
    );
  }

  /**
   * Get a list of active notifications
   */
  public async list(): Promise<PagedItems<UserNotificationDto, UserNotification>> {
    const response = await this._http.get<PagedItemsDto<UserNotificationDto>>(`/api/v1/notifications`);
    return PagedItems.createAsync(response.data, dto => this._factory.getOrCreateEntity(dto), this._http);
  }

  /**
   * @hidden
   */
  private readonly _factory: EntityFactory<UserNotificationDto, UserNotification>;
}
