import { DtoRef } from "../common";
import { Currency } from "../currencies";
import { AffiliateChannelDto, AffiliateEarningsType, UpdateAffiliateChannelDetailsDto } from "../dtos";
import { HttpClient } from "../http";
import { Money } from "../money";

/**
 * Individually tracked channel with its own referral link through which an affiliate
 * promotes CoinPayments and earns commissions on service fees generated for CoinPayments.
 */
export class AffiliateChannel {
  /** the unique id of the channel */
  get id(): string {
    return this._model.dto.id;
  }
  /** the name of the channel (unique to an affiliate) */
  get name(): string {
    return this._updatedName || this._model.dto.name;
  }
  /** the timestamp of when the channel was created */
  get created(): string {
    return this._model.dto.created;
  }
  /** the description of the channel */
  get description(): string {
    return this._updatedDescription || this._model.dto.description;
  }
  /** determines how the affiliate will be earning commissions through this channel */
  get earningsType(): AffiliateEarningsType {
    return this._model.dto.earningsType;
  }
  /** the total number of people that viewed the CoinPayments homepage using the referral link */
  get impressionsTotal(): number {
    return this._model.dto.impressionsTotal;
  }
  /** the total number unique number of people that viewed the CoinPayments homepage using the referral link */
  get impressionsUnique(): number {
    return this._model.dto.impressionsUnique;
  }
  /** the total number of people that signed up for CoinPayments using the referral link */
  get conversions(): number {
    return this._model.dto.conversions;
  }
  /** the total earnings for this channel in the users native currency */
  public readonly totalEarnings: Money;
  /** the referral link for this channel */
  get referralLink(): string {
    return this._model.dto.referralLink;
  }

  private _updatedName?: string;
  private _updatedDescription?: string;

  /** @hidden */
  constructor(
    /** @hidden */ private readonly _model: DtoRef<AffiliateChannelDto>,
    /** @hidden */ private readonly _http: HttpClient,
    /** @hidden */ currency: Currency
  ) {
    this.totalEarnings = Money.create(() => _model.dto.totalEarnings, currency);
  }

  /**
   * Changes the descriptive data of the channel
   * @param data the channel data to update
   * @returns `Promise<void>` that fulfills when the request to update the channel data has been successfully sent to the server
   */
  public async update(data: UpdateAffiliateChannelDetailsDto): Promise<void> {
    await this._http.put(`/api/v1/affiliate/channels/${this.id}`, data);
    this._updatedName = data.name ? data.name : this._updatedName;
    this._updatedDescription = data.description ? data.description : this._updatedDescription;
  }
}
