import { PagedItemsDto, PagingCursorsDto, PagingDto } from "./dtos";
import { HttpClient } from "./http";

/**
 * Encapsulates a page of items, providing functionality to retrieve more items
 * @typeparam TDto dto from which items are mapped
 * @typeparam TItem item which is exposed by [[PagedItems.items]], mapped from the dto
 */
export class PagedItems<TDto, TItem> {
  /** the page of items */
  public get items(): TItem[] {
    return this._mapped;
  }

  /** `boolean` value indicating whether there are more items */
  public get hasNextPage(): boolean {
    return !!this._paging.next;
  }

  /**
   * the cursors for the current page of data
   * @note these can be useful to perform additional manual paging on the original data source
   */
  public get cursors(): Readonly<PagingCursorsDto> {
    return this._paging.cursors;
  }

  /**
   * gets the next page of items, if none are available (e.g. when [[PagedItems.hasNextPage]] returns `false`) then returns an empty page of items.
   * @returns `Promise<PagedItems<TDto, TItem>>` that fulfills with the next page of items (if available) or an empty page when there are no more pages
   * @note this method caches the results of the call internally, if invoked more than once only one call will be made to the server
   */
  public async nextPage(): Promise<PagedItems<TDto, TItem>> {
    if (!this.hasNextPage) {
      this._cachedNextPage = new PagedItems<TDto, TItem>(
        [],
        { cursors: { before: "", after: "" } },
        _ => Promise.resolve<any>(undefined),
        this._http
      );
    }
    if (this._cachedNextPage) {
      return this._cachedNextPage;
    }
    if (this._paging.next) {
      const nextPage = await this._http.get<PagedItemsDto<TDto>>(this._paging.next);
      this._cachedNextPage = await PagedItems.createAsync(nextPage.data, this._projector, this._http);
      return this._cachedNextPage;
    }
    throw new Error("No next page url");
  }

  /** @hidden */
  public static async createAsync<TDto, TItem>(
    model: PagedItemsDto<TDto>,
    projector: (dto: TDto) => Promise<TItem>,
    http: HttpClient
  ): Promise<PagedItems<TDto, TItem>> {
    const items: TItem[] = [];
    for (const item of model.items) {
      items.push(await projector(item));
    }
    return new PagedItems(items, model.paging, projector, http);
  }

  /** @hidden */
  private constructor(
    /** @hidden */ private readonly _mapped: TItem[],
    /** @hidden */ private readonly _paging: PagingDto,
    /** @hidden */ private readonly _projector: (dto: TDto) => Promise<TItem>,
    /** @hidden */ private readonly _http: HttpClient
  ) {}

  /** @hidden */
  private _cachedNextPage?: PagedItems<TDto, TItem>;
}
