// tslint:disable:no-conditional-assignment

/**
 * Since Edge doesn't support TextEncoder, encoding code is implemented here (package importing issues)
 * @source https://github.com/solderjs/TextEncoderLite/blob/master/text-encoder-lite.js
 * @hidden
 */
export function utf8ToBytes(str: string, initUnits?: number): Uint8Array {
  let units = initUnits || Infinity;
  let leadSurrogate: number | null = null;
  const bytes = [];

  const length = str.length;

  for (let i = 0; i < length; i++) {
    let codePoint = str.charCodeAt(i);

    // is surrogate component
    if (codePoint > 0xd7ff && codePoint < 0xe000) {
      // last char was a lead
      if (leadSurrogate) {
        // 2 leads in a row
        if (codePoint < 0xdc00) {
          if ((units -= 3) > -1) {
            bytes.push(0xef, 0xbf, 0xbd);
          }
          leadSurrogate = codePoint;
          continue;
        } else {
          // valid surrogate pair
          codePoint = ((leadSurrogate - 0xd800) << 10) | (codePoint - 0xdc00) | 0x10000;
          leadSurrogate = null;
        }
      } else {
        // no lead yet

        if (codePoint > 0xdbff) {
          // unexpected trail
          if ((units -= 3) > -1) {
            bytes.push(0xef, 0xbf, 0xbd);
          }
          continue;
        } else if (i + 1 === length) {
          // unpaired lead
          if ((units -= 3) > -1) {
            bytes.push(0xef, 0xbf, 0xbd);
          }
          continue;
        } else {
          // valid lead
          leadSurrogate = codePoint;
          continue;
        }
      }
    } else if (leadSurrogate) {
      // valid bmp char, but last char was a lead
      if ((units -= 3) > -1) {
        bytes.push(0xef, 0xbf, 0xbd);
      }
      leadSurrogate = null;
    }

    // encode utf8
    if (codePoint < 0x80) {
      if ((units -= 1) < 0) break;
      bytes.push(codePoint);
    } else if (codePoint < 0x800) {
      if ((units -= 2) < 0) break;
      bytes.push((codePoint >> 0x6) | 0xc0, (codePoint & 0x3f) | 0x80);
    } else if (codePoint < 0x10000) {
      if ((units -= 3) < 0) break;
      bytes.push((codePoint >> 0xc) | 0xe0, ((codePoint >> 0x6) & 0x3f) | 0x80, (codePoint & 0x3f) | 0x80);
    } else if (codePoint < 0x200000) {
      if ((units -= 4) < 0) break;
      bytes.push(
        (codePoint >> 0x12) | 0xf0,
        ((codePoint >> 0xc) & 0x3f) | 0x80,
        ((codePoint >> 0x6) & 0x3f) | 0x80,
        (codePoint & 0x3f) | 0x80
      );
    } else {
      throw new Error("Invalid code point");
    }
  }

  return new Uint8Array(bytes);
}

/**
 * @hidden
 */
export function bytesToUtf8(bytes: Uint8Array): string {
  function utf8Slice(buf: Uint8Array): string {
    const end = buf.length;
    let res = "";
    let tmp = "";

    for (let i = 0; i < end; i++) {
      if (buf[i] <= 0x7f) {
        res += decodeUtf8Char(tmp) + String.fromCharCode(buf[i]);
        tmp = "";
      } else {
        tmp += "%" + buf[i].toString(16);
      }
    }

    return res + decodeUtf8Char(tmp);
  }

  function decodeUtf8Char(str: string): string {
    try {
      return decodeURIComponent(str);
    } catch (err) {
      return String.fromCharCode(0xfffd); // UTF 8 invalid char
    }
  }

  return utf8Slice(bytes);
}
