import BN from "bn.js";
import { DtoRef } from "../common";
import { CurrencyDto } from "../dtos";
import { convertDisplayAmountToSmallestUnits, convertSmallestUnitsToDisplayAmount } from "./utils";

/** Contains the logo URLs for a currency */
export interface CurrencyLogo {
  /**
   * Link to a CoinPayments hosted image for a currency, 64x64 is the default size returned.  Replace "64x64" in the
   * image url with these alternative sizes: 32, 64, 128, 200.
   */
  readonly imageUrl: string;
  /**
   * If available then the link to a CoinPayments hosted vector image (SVG) for the currency.
   */
  readonly vectorUrl?: string;
}

/** Contains various URLs for a currency */
export interface CurrencyUrls {
  /** the websites for the currency */
  readonly websites: string[];
  /** the explorers for the currency (if crypto or a token) */
  readonly explorers: string[];
}

/**
 * Represents money, in any form (e.g. crypto/fiat currencies and tokens), supported on the
 * CoinPayments platform in some capacity (e.g. holding in a wallet and/or payment processing).
 */
export class Currency {
  /** the unique id of the currency on the CoinPayments platform */
  get id() {
    return this._model.dto.id;
  }

  /** the type of the currency (crypto, token, fiat) */
  get type() {
    return this._model.dto.type;
  }

  /** the ticker symbol for the currency.  For fiat currencies this is the three character (ISO-4217)
   * currency code, and for crypto currencies their multi-character symbol.
   * @note these are not unique and can change with crypto currency rebrands
   */
  get symbol() {
    return this._model.dto.symbol;
  }

  /** the name of the currency, e.g. 'Bitcoin' or 'United States Dollar' */
  get name() {
    return this._model.dto.name;
  }

  /**
   * the logo urls for the currency, see [[CurrencyLogo]]
   */
  get logo(): CurrencyLogo {
    return this._model.dto.logo;
  }

  /**
   * the logo url for the currency, uploaded by admin
   */
  get adminLogo(): string {
    return `https://api.coinpayments.net/api/v1/currencies/${this.id}/logo`;
  }

  /** the number of digits after the decimal separator, e.g. 8 for BTC, 2 for USD, 0 for JPY */
  get decimalPlaces() {
    return this._model.dto.decimalPlaces;
  }

  /** the relative ordering/ranking of this currency to the other supported currencies on the CoinPayments platform */
  get rank() {
    return this._model.dto.rank;
  }

  /** the status of the currency on the CoinPayments platform */
  get status() {
    return this._model.dto.status;
  }

  /** array of capabilities of the currency on the CoinPayments platform (e.g. "multiSigAccounts", "payments") */
  get capabilities() {
    return this._model.dto.capabilities;
  }

  /**
   * object containing various urls for the currency, see [[CurrencyUrls]]
   */
  get urls(): CurrencyUrls {
    return this._model.dto.urls;
  }

  /**
   * Converts an amount to the currencies smallest unit
   * @hidden
   * @param amount the amount to convert
   */
  public convertAmountToSmallestUnit(amount: string): string {
    return convertDisplayAmountToSmallestUnits(amount, this.decimalPlaces).toString();
  }

  /**
   * Converts an amount in the currencies smallest unit to a display amount
   * @hidden
   * @param amount the amount in the currencies smallest unit to convert to a display amount
   */
  public convertSmallestUnitToDisplay(amount: string): string {
    return convertSmallestUnitsToDisplayAmount(amount, this.decimalPlaces);
  }

  /**
   * Adds values in display amount
   * @hidden
   * @param amounts an array of values in display amount to add
   */
  public add(values: string[]): string {
    let sum: BN = new BN("0");
    for (const value of values) {
      const smallestUnit = this.convertAmountToSmallestUnit(value);
      sum = sum.add(new BN(smallestUnit));
    }
    const result = this.convertSmallestUnitToDisplay(sum.toString());
    return result;
  }

  /**
   * Subtract values in display amount
   * Takes first value from an array and subtract each next value from it
   * @hidden
   * @param amounts an array of values in display amount to subtract
   */
  public sub(values: string[]): string {
    if (values.length === 0) {
      throw new Error("array cannot be empty");
    }

    const firstVal = this.convertAmountToSmallestUnit(values.shift() as string);
    let res: BN = new BN(firstVal);
    for (const value of values) {
      const smallestUnit = this.convertAmountToSmallestUnit(value);
      res = res.sub(new BN(smallestUnit));
    }

    let result = res.toString();
    if (result[0] === "-") {
      result = this.convertSmallestUnitToDisplay(result.slice(1));
      return "-" + result;
    }
    return this.convertSmallestUnitToDisplay(result);
  }

  /** @hidden */
  constructor(/** @hidden */ private readonly _model: DtoRef<CurrencyDto>) {}
}
