import { Money } from "../money";

export * from "./transaction";
export * from "./transactionAccountEndpoint";
export * from "./transactionBlockchainEndpoint";
export * from "./transactions";

/**
 * Represents the the input or output (origin or receiver) of a transaction.
 * The [[TransactionEndpoint.type]] field tells you what kind of endpoint it is (`blockchain` or `account`).
 *
 * See [[TransactionAccountEndpoint]] and [[TransactionBlockchainEndpoint]] for specifics.
 */
export interface TransactionEndpoint {
  /**
   * the type of the endpoint
   * - `blockchain` for when the transaction originated from the blockchain or was sent to the blockchain, implemented by [[TransactionBlockchainEndpoint]]
   * - `account` for when the transaction originated from an account or was received in an account, implemented by [[TransactionAccountEndpoint]]
   */
  readonly type: "blockchain" | "account";

  /**
   * the amount of money in the original currency on this side of the transaction
   */
  readonly amount: Money;

  /**
   * the amount of money in the native currency of the user, on this side of the transaction.
   * the conversion rate is locked to that at the time the transaction was created.
   */
  readonly nativeAmount: Money;
}
