import { EntityFactory, UrlBuilder } from "../common";
import { CurrencyDto, PagedItemsDto, PagedRatesDto, RequiredConfirmationDto } from "../dtos";
import { HttpClient } from "../http";
import { PagedItems } from "../paging";
import { Currency } from "./";

export type CurrencyType = "crypto" | "token" | "fiat";

export type CurrencyCapability = "MultiSigAccounts" | "SingleSigAccounts" | "payments";

/**
 * Options accepted by the [[Currencies.list]] method
 */
export interface CurrenciesListOptions {
  /**
   * optional search query to find currencies with names and/or codes similar to the specified search string
   */
  q?: string;
  /**
   * optional list of the types to filter the currencies by (`crypto`, `token`, and `fiat`)
   */
  types?: CurrencyType[];
  /**
   * optional list of the capabilities to filter the currencies by, currencies without these capabilities will not be returned
   */
  capabilities?: CurrencyCapability[];
  /**
   * cursor that points to the end of the page of data that has been returned, see [[PagedCurrencies.paging.cursors.after]]
   */
  after?: string;
  /**
   * the maximum number of objects that may be returned, the query may return fewer results than
   * the requested maximum. If `after` is specified then `limit` specifies the number of items
   * to return starting from `after`.  If not specified then `limit` specifies the number of
   * items to return from the beginning.
   */
  limit?: number;
}

/**
 * A pair of currencies ids, from one to another of which conversion is possible
 */
export interface ConversionPair {
  /**
   * id of the currency from which conversion is possible to the `to` currency
   */
  from: string;
  /**
   * id of the currency to which conversion is possible from the `from` currency
   */
  to: string;
}

/** Provides access to the currencies hosted on the CoinPayments platform */
export class Currencies {
  /** @hidden */
  constructor(
    /** @hidden */
    private readonly _http: HttpClient
  ) {
    this._factory = new EntityFactory(
      dto => dto.id.toString(), // make it generic or something?
      entityDto => Promise.resolve(new Currency(entityDto))
    );
  }

  /**
   * finds a currency by its id
   * @param id the id of the currency to retrieve
   * @returns `Promise<CurrencyDto>` that fulfills with the [[Currency]] matching the specified id or null if not found
   */
  public async find(id: string): Promise<Currency | null> {
    const existing = this._factory.getEntityOrDefault(id.toString());
    if (existing) {
      return existing;
    }

    const response = await this._http.get<CurrencyDto>(`/api/v1/currencies/${id}`, {
      omitAuthorization: true,
      validateStatus: _ => true // validated below, so that 404 -> null
    });

    if (response.status === 404) {
      return null;
    }

    if (response.status < 200 || response.status >= 300) {
      throw new Error(`Status ${response.status} (${response.statusText}) does not indicated success`);
    }

    return this._factory.getOrCreateEntity(response.data);
  }

  /**
   * lists platform supported currencies, their details and capabilities.
   * @param options optional object specifying the options (e.g. paging, sorting, etc) for the list query
   * @returns `Promise<PagedItems<CurrencyDto, Currency>>` that fulfills with [[PagedItems]] of [[Currency]]
   */
  public async list(options?: CurrenciesListOptions): Promise<PagedItems<CurrencyDto, Currency>> {
    options = options || {};
    const types = options.types ? options.types.join(",") : "";
    const caps = options.capabilities ? options.capabilities.join(",") : "";
    const url = new UrlBuilder("/api/v1/currencies")
      .addQueryIfValue("q", options.q)
      .addQueryIfValue("types", types)
      .addQueryIfValue("capabilities", caps)
      .addQueryIfValue("after", options.after)
      .addQueryIfValue("limit", options.limit ? options.limit.toString() : "")
      .toString();
    const response = await this._http.get<PagedItemsDto<CurrencyDto>>(url, { omitAuthorization: true });
    const data = response.data;
    return PagedItems.createAsync<CurrencyDto, Currency>(
      data,
      dto => this._factory.getOrCreateEntity(dto),
      this._http
    );
  }
  /**
   * Shows count to required confirmation
   * @returns `Promise<RequiredConfirmationDto>` which provides id and confirmation count current curreny
   */
  public async listRequiredConfiramtions(): Promise<RequiredConfirmationDto> {
    const response = await this._http.get<RequiredConfirmationDto>(
      "/api/v1/currencies/required-confirmations",
      { omitAuthorization: true }
    );
    return response.data;
  }

  /**
   * Shows which currencies can be converted one into another
   * @returns `Promise<ConversionPair[]>` which provides the list of possible conversion pairs
   */
  public async listConversionPairs(): Promise<ConversionPair[]> {
    const url = "/api/v1/currencies/conversions";
    const response = await this._http.get<ConversionPair[]>(url, { omitAuthorization: true });
    return response.data;
  }

  /**
   * Gets the conversion rate between the two specified currency ids
   * @param fromCurrencyId the id of the [[Currency]] to convert from, this is the base currency
   * @param toCurrencyId the id of the [[Currency]] to convert to, this is the quoted currency
   * @returns `Promise<number>` which fulfills with the conversion rate from the `fromCurrencyId` to `toCurrencyId`
   */
  public async rate(fromCurrencyId: number, toCurrencyId: number): Promise<string> {
    const rateResponse = await this._http.get<PagedRatesDto>(
      `/api/v1/rates?from=${fromCurrencyId}&to=${toCurrencyId}`,
      { omitAuthorization: true }
    );
    return rateResponse.data.items[0].rate;
  }

  /**
   * Gets the conversion rate between the two currencies and optionally tokens on their platforms
   * @param fromCurrencyId the id of the [[Currency]] to convert from, this is the base currency
   * @param fromTokenContractAddress the contract address of the token on the from currencies platform
   * @param toCurrencyId the id of the [[Currency]] to convert to, this is the quoted currency
   * @param toTokenContractAddress the contract address of the token on the quote currencies platform
   * @returns `Promise<number>` which fulfills with the conversion rate from the base to the quote currencies and/or tokens
   */
  public async tokenRate(
    fromCurrencyId: number,
    fromTokenContractAddress: string | undefined,
    toCurrencyId: number,
    toTokenContractAddress: string | undefined
  ): Promise<string> {
    let query = `from=${fromCurrencyId}&to=${toCurrencyId}`;

    if (fromTokenContractAddress) {
      query += `&fromToken=${fromTokenContractAddress}`;
    }

    if (toTokenContractAddress) {
      query += `&toToken=${toTokenContractAddress}`;
    }

    const rateResponse = await this._http.get<PagedRatesDto>(`/api/v1/rates?${query}`, {
      omitAuthorization: true
    });

    return rateResponse.data.items[0].rate;
  }

  /**
   * Validates an address for the specific coin
   * @param currencyId the id of the [[Currency]] for which address should be valid
   * @param address the address to validate
   * @returns `Promise<boolean>` which confirms the validity of the address for the selected coin
   */
  public async validateAddress(currencyId: string, address: string): Promise<boolean> {
    const isValidResponse = await this._http.get<boolean>(
      `/api/v1/currencies/${currencyId}/${address}/validate`
    );
    return isValidResponse.data;
  }

  /**
   * @hidden
   */
  private readonly _factory: EntityFactory<CurrencyDto, Currency>;
}
