import { Account } from "../accounts";
import { EntityFactory, UrlBuilder } from "../common";
import { Currency } from "../currencies";
import {
  CreateMerchantInvoiceResponseDto,
  InvoiceDto,
  InvoiceMerchantOptionsDto,
  InvoiceMetadataDto,
  InvoiceStateChangedArgs,
  InvoiceStatus,
  MerchantInvoiceNextIdDto,
  PagedItemsDto,
  UserPreferencesDto
} from "../dtos";
import { HttpClient } from "../http";
import { PagedItems } from "../paging";
import { MessageHub } from "../signalrhub";
import {
  Buyer,
  InvoiceAmount,
  InvoiceEmailDeliveryOptions,
  InvoiceShippingDetail,
  LineItem
} from "./interfaces";
import { Invoice } from "./invoice";
import { InvoicePaymentDetails } from "./invoicePaymentDetails";

/**
 * Request to create an invoice, which is a list of goods or services with a statement of the sum due provided
 * by the merchant, that a buyer intends to purchase
 */
export interface CreateMerchantInvoiceRequest {
  /** indicates if invoice was delivered by email or to be delivered by email */
  isEmailDelivery: boolean;
  /** invoice e-mail delivery options, customers will receive an email with invoice details */
  emailDelivery?: InvoiceEmailDeliveryOptions;
  /** custom invoice date if not the created date of the invoice, invoices with a future date will be scheduled */
  invoiceDate?: string;
  /** optional due date to be shown on the invoice */
  dueDate?: string;
  /** flag indicating whether this is a draft invoice */
  draft?: boolean;
  /** the id of the client creating this invoice */
  clientId?: string;
  /** the optional API caller provided external invoice number.  Appears in screens shown to the Buyer and emails sent. */
  invoiceId?: string;
  /** the buyer information, if not provided it will be requested during payment so that refunds can be sent if there is a problem with the payment. */
  buyer?: Buyer;
  /** the purchase description, can be provided instead of a list of `items` */
  description?: string;
  /** the optional array of items that a buyer intends to purchase from the merchant */
  items?: LineItem[];
  /** the total amount of the invoice, with an optional breakdown that provides details, such as the total item
   * amount, total tax amount, shipping, handling, insurance and discounts, if any.
   * Note: Must be included if invoice is not a draft
   */
  amount?: InvoiceAmount;
  /** the optional shipping method and address */
  shipping?: InvoiceShippingDetail;
  /** any custom data the caller wishes to attach to the invoice which will be sent back in notifications */
  customData?: object;
  /**
   * flag indicating whether a buyer name and email are required, they will be requested at checkout
   * if not provider by the caller.  The @buyerDataCollectionMessage will be displayed
   * to the buyer when prompted.
   */
  requireBuyerNameAndEmail?: boolean;
  /** the message to display when collecting buyer user data */
  buyerDataCollectionMessage?: string;
  /** notes for the merchant only, these are not visible to the buyers */
  notes?: string;
  /** any additional information to share with the buyer about the transaction */
  notesToRecipient?: string;
  /** any terms and conditions, e.g. a cancellation policy */
  termsAndConditions?: string;
  /** options to show/hide merchant information on an invoice, or include additional merchant information specific to an invoice */
  merchantOptions?: Partial<InvoiceMerchantOptionsDto>;
  metadata?: InvoiceMetadataDto;
  /** the invoice PO number */
  poNumber?: string;
}

/**
 * Request to create the HTML for a "Buy Now" Button
 */
export interface CreateMerchantInvoiceBuyNowButtonHtmlRequest extends CreateMerchantInvoiceRequest {
  /** the url to redirect to once an invoice is successfully paid */
  successUrl: string;
  /** the url to redirect to if payment of an invoice fails (e.g. expired) or is cancelled by the user */
  cancelUrl: string;
  /** the url to send a payment complete message to once the invoice has been paid */
  IPNUrl?: string;
  /** flag indicating whether to send an email notification once payment completes successfully */
  emailNotifications?: boolean;
  /** the desired style of the "Buy Now" button */
  buttonStyle?: string;
  /** the desired width of the "Buy Now" button (defaults to 225) */
  buttonWidth?: number;
}

/**
 * Options accepted by the [[list]] method
 */
export interface ListMerchantInvoicesOptions {
  /** optional search string */
  q?: string;
  /** optional name of the integration that created the invoice */
  integration?: string;
  /** optional clientId query can be passed to fetch invoices that were created with the specific client. */
  clientId?: string;
  /** optional status query can be passed to filter the invoices by pending, completed, or expired status. */
  status?: InvoiceStatus;
  /** optional query to fetch from and including the time specified up to the current time */
  from?: string;
  /** optional query to fetch all invoices up to and including the specified time */
  to?: string;
  /** optional query to filter the invoices by the wallet they were paid out to (for 'paid' and 'completed' invoices) */
  payoutWalletId?: string;
  /**
   * the maximum number of objects that may be returned, the query may return fewer results than
   * the requested maximum.
   */
  limit?: number;
}

/** Represents a merchant's invoices on the CoinPayments platform. */
export class Invoices {
  /** @hidden */
  constructor(
    /** @hidden */
    private readonly _http: HttpClient,
    /** @hidden */
    private readonly _msgHub: MessageHub,
    /** @hidden */
    public readonly _getCurrency: (id: string) => Promise<Currency>,
    /** @hidden */
    public readonly _getAccount: (id: string) => Promise<Account>
  ) {
    this._factory = new EntityFactory(
      dto => dto.id,
      async entityDto => {
        const payoutDetails = entityDto.dto.payoutDetails
          ? await InvoicePaymentDetails.create(entityDto.dto.payoutDetails, _getCurrency, _getAccount)
          : null;
        return new Invoice(entityDto, _http, this._factory, _getCurrency, _getAccount, payoutDetails);
      }
    );
  }

  /**
   * Get a list of the current merchant's invoices. An optional query can be passed to filter the invoices by pending, completed, or expired status.
   * @param options optional object specifying the filters for the list query (status and clientId)
   */
  public async list(options?: ListMerchantInvoicesOptions): Promise<PagedItems<InvoiceDto, Invoice>> {
    let url = "/api/v1/merchant/invoices";
    if (options) {
      url = new UrlBuilder(`/api/v1/merchant/invoices`)
        .addQueryIfValue("q", options.q)
        .addQueryIfValue("clientId", options.clientId)
        .addQueryIfValue("status", options.status)
        .addQueryIfValue("from", options.from)
        .addQueryIfValue("to", options.to)
        .addQueryIfValue("integration", options.integration)
        .addQueryIfValue("payoutWalletId", options.payoutWalletId)
        .addQueryIfValue("limit", options.limit ? options.limit.toString() : "100")
        .toString();
    }

    const response = await this._http.get<PagedItemsDto<InvoiceDto>>(url);
    const data = response.data;
    return PagedItems.createAsync(data, dto => this._factory.getOrCreateEntity(dto), this._http);
  }

  /**
   * Find invoice belonging to merchant by the invoice ID
   * @param invoiceId the id of the invoice to find
   */
  public async find(invoiceId: string): Promise<Invoice> {
    const response = await this._http.get<InvoiceDto>(`/api/v1/merchant/invoices/${invoiceId}`);
    return this._factory.getOrCreateEntity(response.data);
  }

  /**
   * Find an invoice that was shared by a merchant
   * @param sharedInvoiceId the shared invoice id
   */
  public async findSharedInvoice(sharedInvoiceId: string): Promise<Invoice | null> {
    const response = await this._http.get<InvoiceDto>(`/api/v1/shared-invoices/${sharedInvoiceId}`, {
      omitAuthorization: true,
      validateStatus: s => (s >= 200 && s < 300) || s === 404
    });
    if (response.status >= 200 && response.status < 300) {
      return this._factory.getOrCreateEntity(response.data);
    }
    if (response.status === 404) {
      return null;
    }
    throw new Error(`Status ${response.status} (${response.statusText}) does not indicate success`);
  }

  /**
   * Generate the next valid invoice number
   * @param userInvoiceNumber the user provided invoice number
   * @returns {string} the next valid invoice number, if the @userInvoiceNumber does not exist in the system and is valid then it will be returned, otherwise a new invoice number will be generated composed of the user invoice number with an auto-incrementing number appended to it
   */
  public async generateNextInvoiceNumber(userInvoiceNumber?: string): Promise<string> {
    const request: MerchantInvoiceNextIdDto = {
      invoiceId: userInvoiceNumber || ""
    };
    const response = await this._http.post<MerchantInvoiceNextIdDto>(
      `/api/v1/merchant/invoices/nextid`,
      request
    );
    return response.data.invoiceId;
  }

  /**
   * Creates "Buy Now" button HTML code for the specified invoice and payment options
   * @param request the invoice and payment options for the "Buy Now" button
   */
  public async createBuyNowButtonHtml(
    request: CreateMerchantInvoiceBuyNowButtonHtmlRequest
  ): Promise<string> {
    const response = await this._http.post<string>(`/api/v1/merchant/invoices/buy-now-button`, request, {
      responseType: "text"
    });

    return response.data;
  }

  /**
   * Creates a new invoice
   * @param body info used to create an invoice
   */
  public async create(
    body: CreateMerchantInvoiceRequest
  ): Promise<Readonly<CreateMerchantInvoiceResponseDto>> {
    if (body.amount && !body.amount.currencyId) {
      const userPreferences = await this._http.get<UserPreferencesDto>(`/api/v1/preferences`);
      body.amount.currencyId = userPreferences.data.nativeCurrencyId;
    }

    if (body.items && body.items.length > 0) {
      const userPreferences = await this._http.get<UserPreferencesDto>(`/api/v1/preferences`);
      for (const item of body.items) {
        if (item.amount && !item.amount.currencyId) {
          item.amount.currencyId = userPreferences.data.nativeCurrencyId;
        }
        if (!item.quantity) {
          item.quantity = { type: 2, value: 1 };
        }
      }
    }

    const response = await this._http.post<CreateMerchantInvoiceResponseDto>(
      `/api/v1/merchant/invoices`,
      body,
      {
        validateStatus: _ => true // validated below
      }
    );

    const status = response.status;
    if (status < 200 || status >= 300) {
      const statusText = response.statusText;
      throw new Error("Failed to create invoice, API returned: " + status + " (" + statusText + ")");
    }

    return response.data;
  }

  /**
   * Subscribe to an event indicating that the invoice status was changed on the server side
   */
  public async startListeningInvoiceStateChangedEvent(handler: (data: InvoiceStateChangedArgs) => void) {
    await this.startListeningToEvent("invoiceStateChangedEvent", data => {
      this._factory.removeEntity(data.id);
      handler(data);
    });
  }

  private async startListeningToEvent(event: string, handler: (data: any) => void) {
    await this._msgHub.subscribeTo<any>(event, handler);
  }

  /**
   * @hidden
   */
  private readonly _factory: EntityFactory<InvoiceDto, Invoice>;
}
