import { EntityFactory } from "../common";
import { Currency } from "../currencies";
import { PagedItemsDto, TopUpOrderDto } from "../dtos";
import { HttpClient } from "../http";
import { PagedItems } from "../paging";
import { TopUpOrder } from "./topUpOrder";

/** Represents a user's cryptocurrency purchases (top ups) */
export class TopUpOrders {
  /** @hidden */
  constructor(
    /** @hidden */ private readonly _http: HttpClient,
    /** @hidden */ _getCurrency: (id: string) => Promise<Currency>
  ) {
    this._factory = new EntityFactory<TopUpOrderDto, TopUpOrder>(
      dto => dto.id,
      async entityDto => {
        const purchaseCurrency = await _getCurrency(`${entityDto.dto.purchaseAmount.currencyId}`);
        const receivedCurrency = await _getCurrency(`${entityDto.dto.coinReceived.currencyId}`);
        return new TopUpOrder(entityDto, purchaseCurrency, receivedCurrency);
      }
    );
  }

  /**
   * Get a list of the user's top up orders
   */
  public async list(): Promise<PagedItems<TopUpOrderDto, TopUpOrder>> {
    const response = await this._http.get<PagedItemsDto<TopUpOrderDto>>(`/api/v1/topups/orders`);
    const data = response.data;
    return PagedItems.createAsync(data, dto => this._factory.getOrCreateEntity(dto), this._http);
  }

  private readonly _factory: EntityFactory<TopUpOrderDto, TopUpOrder>;
}
