import {
  AffiliateImpressionsCountryDto,
  AffiliateImpressionsDailyDto,
  AffiliateImpressionsSummaryDto
} from "../dtos";
import { HttpClient } from "../http";

/**
 * Options accepted by [[AffiliateImpressions]].listImpressionsByDay and [[AffiliateImpressions]].listImpressionsByCountry methods
 */
export interface ListImpressionsOptions {
  /** the id of the channel for which to get conversions for */
  channelId?: string;
  /** the starting date for which to retrieve conversions for (if not specified then the beginning of time) */
  from?: string;
  /** the ending date for which to retrieve conversions for (if not specified then the end of time) */
  to?: string;
}

/**
 * provides access to information about impressions and conversion, users that click on affiliate
 * referral links and referred users that subsequently sign up.
 */
export class AffiliateImpressions {
  /** @hidden */
  constructor(/** @hidden */ private readonly _http: HttpClient) {}

  /**
   * get the total impressions, optionally filtering by channel and time period
   */
  public async getTotalImpressions(
    options: ListImpressionsOptions = {}
  ): Promise<Readonly<AffiliateImpressionsSummaryDto>> {
    const query = this._optionsToQuery(options);
    const url = `/api/v1/affiliate/impressions/?${query}`;
    const response = await this._http.get<AffiliateImpressionsSummaryDto>(url);
    return response.data;
  }

  /**
   * list the impressions and conversions by day for the affiliate, optionally filtered by a channel and time period
   */
  public async listImpressionsByDay(
    options: ListImpressionsOptions = {}
  ): Promise<Readonly<AffiliateImpressionsDailyDto>> {
    const query = this._optionsToQuery(options);
    const url = `/api/v1/affiliate/impressions/daily?${query}`;
    const response = await this._http.get<AffiliateImpressionsDailyDto>(url);
    return response.data;
  }

  /**
   * List the impressions and conversions by country for the affiliate, optionally filtered by a channel and time period
   */
  public async listImpressionsByCountry(
    options: ListImpressionsOptions = {}
  ): Promise<Readonly<AffiliateImpressionsCountryDto>> {
    const query = this._optionsToQuery(options);
    const url = `/api/v1/affiliate/impressions/country?${query}`;
    const response = await this._http.get<AffiliateImpressionsCountryDto>(url);
    return response.data;
  }

  private _optionsToQuery(options: any) {
    return Object.keys(options)
      .filter(k => options[k])
      .map(k => `${k}=${options[k]}`)
      .join("&");
  }
}
