import * as Oidc from "oidc-client";
import { TokenReponseDto } from "../dtos";
import { HttpClient, RequestConfig } from "../http";
import { UserManager } from "../user";
import { getTimeAsUnixTimeSeconds } from "../utils/dateUtils";

/**
 * Service to obtain new access token if the current one is expiring in less than 10 minutes
 * @hidden
 */
export class AccessTokenService {
  constructor(
    private readonly identityClient: HttpClient,
    private readonly userManager: UserManager | Oidc.UserManager,
    private readonly clientId: string
  ) {}

  /**
   * Returns current access token or new access token if current is expiring in less than 10 mins
   */
  public getAccessTokenOrThrow = async (): Promise<string> => {
    const user = await this.userManager.getUser();
    if (!user || !user.access_token) {
      throw new Error("Authentication required");
    }

    const nowPlus10Mins = getTimeAsUnixTimeSeconds(Date.now(), 10 * 60);

    // if existing token is about to expire in less than 10 minutes, extend the session
    if (!user.expired && nowPlus10Mins > user.expires_at) {
      const newToken = await this.getNewAccessToken(user.access_token);

      // update existing user in the store
      user.access_token = newToken.access_token;
      user.expires_at = getTimeAsUnixTimeSeconds(Date.now(), newToken.expires_in); // update the expires_at time
      await this.userManager.storeUser(user as Oidc.User);

      return newToken.access_token;
    }

    // allow to use existing token even if user.expired is true due to Microsoft JWT default 5min clock skew,
    // so token can be used up to a max of 5 mins if we set the access token lifetime to be shorter than that.
    // https://github.com/IdentityServer/IdentityServer4/issues/962
    return user.access_token;
  };

  /**
   * Returns the token with the Bearer string prepended
   */
  public makeBearerHeader = async (): Promise<string> => {
    const token = await this.getAccessTokenOrThrow();
    return "Bearer " + token;
  };

  private async getNewAccessToken(currentToken: string): Promise<TokenReponseDto> {
    const data = {
      client_id: this.clientId,
      grant_type: "extendsession",
      token: currentToken
    };

    const config: RequestConfig = {
      body: data,
      method: "POST",
      headers: {
        Authorization: "Bearer " + currentToken
      },
      bodyShouldBeFormUrlEncoded: true,
      url: "/connect/token"
    };

    const response = await this.identityClient.send<TokenReponseDto>(config);
    return response.data;
  }
}
