import { DtoRef } from "../common";
import {
  InvoiceAmountDto,
  InvoiceCurrencyDto,
  InvoiceMerchantDto,
  InvoicePaymentCurrencyPaymentDetailsDto,
  InvoicePayoutDetailsDto,
  InvoiceStatus,
  InvoiceStatusDto,
  PosInvoiceDto
} from "../dtos";
import { HttpClient } from "../http";

export class PosInvoice implements PosInvoiceDto {
  get amount(): InvoiceAmountDto {
    return this._model.dto.amount;
  }
  get completed(): string | undefined {
    return this._model.dto.completed;
  }
  get confirmed(): string | undefined {
    return this._model.dto.confirmed;
  }
  get created(): string {
    return this._model.dto.created;
  }
  get currency(): InvoiceCurrencyDto {
    return this._model.dto.currency;
  }
  get expectedPayment(): InvoicePaymentCurrencyPaymentDetailsDto | undefined {
    return this._model.dto.expectedPayment;
  }
  get expires(): string {
    return this._model.dto.expires;
  }
  get id(): string {
    return this._model.dto.id;
  }
  get merchant(): InvoiceMerchantDto {
    return this._model.dto.merchant;
  }
  get paymentId(): string | undefined {
    return this._model.dto.paymentId;
  }
  get payouts(): InvoicePayoutDetailsDto | undefined {
    return this._model.dto.payouts;
  }
  get status(): InvoiceStatus {
    return this._model.dto.status;
  }

  /** @hidden */
  constructor(
    /** @hidden */ private readonly _model: DtoRef<PosInvoiceDto>,
    /** @hidden */ private readonly _http: HttpClient
  ) {}

  /**
   * Gets the status of a shared pos, whether it's outstanding, partially paid or fully paid
   */
  public async getSharedStatus(): Promise<InvoiceStatusDto> {
    const response = await this._http.get<InvoiceStatusDto>(
      `/api/v1/shared-pos/${this.id}/payments/${this.paymentId}/status`
    );
    return response.data;
  }
}
