import { validateArgs } from "./common";
import { bytesToUtf8, utf8ToBytes } from "./text-encoding";

/** @hidden */
interface WrappedEncryptedData {
  readonly v: 1;
  readonly iv: string;
  readonly salt: string;
  readonly iter: number;
  readonly ks: number;
  readonly ts: number;
  readonly mode: string;
  readonly hash: string;
  readonly ct: string;
}

/**
 * Compute the HMAC for the given key and message
 * @hidden
 * @param key the key to use for the HMAC
 * @param message the actual message to HMAC
 * @returns the result of the HMAC operation as a hex string
 */
export async function hmac(key: string, message: string): Promise<string> {
  const cryptoKey = await crypto.subtle.importKey(
    "raw",
    utf8ToBytes(key),
    { name: "HMAC", hash: "SHA-256" },
    false,
    ["sign"]
  );

  const signature = await crypto.subtle.sign("HMAC", cryptoKey, utf8ToBytes(message));

  // convert to hex

  return Array.from(new Uint8Array(signature))
    .map(b => b.toString(16).padStart(2, "0"))
    .join("");
}

/**
 * Returns an `ArrayBuffer` of the requested `count` bytes in size, filled with random byte values
 * @hidden
 * @param count the number of random bytes to generate
 * @returns `ArrayBuffer` containing the requested number of random bytes
 */
export function randomBytes(count: number): ArrayBuffer {
  if (count < 0) {
    throw new Error("expected count > 0");
  }
  const bytes = new Uint8Array(count);
  crypto.getRandomValues(bytes);
  return bytes.buffer as ArrayBuffer;
}

/**
 * Returns a `Promise` that fulfills with the clear text corresponding to the encrypted text and password parameters.
 * @hidden
 * @param encryptedText the encrypted string (encrypted with [[encryptString]])
 * @param password the password used to encrypt the string
 * @returns `Promise` that fulfills with a `string` containing the plaintext data
 */
export async function decryptString(encryptedText: string, password: string): Promise<string> {
  const bytes = await decryptBytes(encryptedText, password);
  return bytesToUtf8(bytes);
}

/**
 * Returns a `Promise` that fulfills with the encrypted data corresponding to the plaintext data and password provided.
 * @hidden
 * @param str the plain text string to encrypt
 * @param password the password to encrypt the string with
 * @returns `Promise` that fulfills with a `string` containing the encrypted data, which can be decrypted with [[decryptString]]
 */
export async function encryptString(str: string, password: string): Promise<string> {
  const bytes = utf8ToBytes(str);
  return encryptBytes(bytes, password);
}

/**
 * Returns a `Promise` that fulfills with the decrypted bytes corresponding to the encrypted data and password parameters.
 * @hidden
 * @param encryptedBytes the encrypted byte string (encrypted with [[encryptBytes]])
 * @param password the password used to encrypt the data
 * @returns `Promise` that fulfills with a `Uint8Array` containing the unencrypted data
 */
export async function decryptBytes(encryptedBytes: string, password: string): Promise<Uint8Array> {
  const unwrapped = JSON.parse(encryptedBytes) as WrappedEncryptedData;
  validateArgs(unwrapped, ["v", "iv", "salt", "hash", "iter", "ks", "ts", "mode", "ct"]);

  function base64ToBuffer(base64: string): Uint8Array {
    const binstr = atob(base64);
    const buffer = new Uint8Array(binstr.length);
    Array.prototype.forEach.call(binstr, (ch: string, i: number) => {
      buffer[i] = ch.charCodeAt(0);
    });
    return buffer;
  }

  const randomIV = base64ToBuffer(unwrapped.iv);
  const randomSalt = base64ToBuffer(unwrapped.salt);
  const encrypted = base64ToBuffer(unwrapped.ct);

  const key = await pbkdf2(
    password,
    randomSalt,
    unwrapped.iter,
    unwrapped.ks,
    unwrapped.mode,
    unwrapped.hash
  );

  const decrypted = await crypto.subtle.decrypt(
    { name: unwrapped.mode, iv: randomIV.buffer, tagLength: unwrapped.ts },
    key,
    encrypted
  );

  return new Uint8Array(decrypted);
}

/**
 * Returns a `Promise` that fulfills with the encrypted data corresponding to the byte array and password provided.
 * @hidden
 * @param data the byte array to encrypt
 * @param password the password to encrypt the byte array with
 * @returns `Promise` that fulfills with a `string` representing the encrypted byte array, which can be decrypted with [[decryptBytes]]
 */
export async function encryptBytes(data: Uint8Array, password: string): Promise<string> {
  const randomSalt = new Uint8Array(8);
  const randomIV = new Uint8Array(16);

  crypto.getRandomValues(randomSalt);
  crypto.getRandomValues(randomIV);

  const iterations = 10000;
  const keySize = 256;
  const mode = "AES-GCM";
  const hash = "SHA-256";
  const tagLength = 128;

  const key = await pbkdf2(password, randomSalt, iterations, keySize, mode, hash);

  const encrypted = await crypto.subtle.encrypt({ name: mode, iv: randomIV.buffer, tagLength }, key, data);

  const bufferToBase64 = (buffer: Uint8Array) => {
    const binstr = Array.prototype.map
      .call(buffer, (ch: number) => {
        return String.fromCharCode(ch);
      })
      .join("");
    return btoa(binstr);
  };

  const wrapped: WrappedEncryptedData = {
    v: 1,
    mode,
    hash,
    iter: iterations,
    ks: keySize,
    ts: tagLength,
    iv: bufferToBase64(randomIV),
    salt: bufferToBase64(randomSalt),
    ct: bufferToBase64(new Uint8Array(encrypted))
  };

  return JSON.stringify(wrapped);
}

/** @hidden */
async function pbkdf2(
  password: string,
  salt: Uint8Array,
  iterations: number,
  keySize: number,
  mode: string,
  hash: string
): Promise<CryptoKey> {
  const keyMaterial = utf8ToBytes(password);
  const baseKey = await crypto.subtle.importKey("raw", keyMaterial, { name: "PBKDF2", length: 256 }, false, [
    "deriveKey"
  ]);

  return crypto.subtle.deriveKey(
    { name: "PBKDF2", salt, iterations, hash },
    baseKey,
    { name: mode, length: keySize },
    true,
    ["encrypt", "decrypt"]
  );
}
